import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { Term } from '../../common/api-interfaces'
import { ListComponent } from '../../common/list.component'
import { TermViewComponent } from './term-view/term-view.component'
import { TermsFormComponent } from './terms-form/terms-form.component'

@Component({
    selector: 'admin-terms',
    templateUrl: 'admin-terms.component.html',
})
export class AdminTermsComponent extends ListComponent<Term> {
    protected apiUrl = '/admin/terms'
    protected formComponent = TermsFormComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public viewTerm(term: Term): void {
        const modal = this.ngbModal.open(TermViewComponent, {
            windowClass: 'modal-primary',
        })
        ;(modal.componentInstance as TermViewComponent).term = term
    }
}
