import { CardProvider, CardStatus, CardType, ICard } from '../../api-interfaces'
import { User } from '../core/user.model'
import { Currency } from './currency.model'

export const providerMap: { [K in CardProvider]: string } = {
    nium: 'Nium',
    noop: 'Noop',
    sample: 'Sample',
}

export class Card implements ICard {
    public id: string
    public maskedPan: string
    public provider: CardProvider
    public publicToken: string
    public status: CardStatus
    public type: CardType
    public expiresAt: string
    public activationCode: string
    public activatedAt: string
    public currency: Currency
    public cardholder: User | null
    public createdAt: string
    public updatedAt: string

    constructor(data: ICard) {
        Object.assign(this, data)
        this.currency = new Currency(data.currency)
        if (data.cardholder) {
            this.cardholder = new User(data.cardholder)
        }
    }
}
