import { HttpClient, HttpParams } from '@angular/common/http'
import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { pick } from 'lodash'
import { debounceTime, distinctUntilChanged, finalize, switchMap } from 'rxjs/operators'
import { CardProvider, ICard, IUser } from '../../common/api-interfaces'
import { providerMap } from '../../common/models/accounting/card.model'
import { ToastrService } from '../../common/services/toastr.service'

@Component({
    selector: 'card-form',
    templateUrl: 'card-form.component.html',
})
export class CardFormComponent implements OnInit {
    @Input()
    public item: ICard
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()
    @Output()
    public readonly onDiscard = new EventEmitter<void>()

    public copy: Partial<ICard & { last4Digits: string }> = {}
    @ViewChild('cardForm')
    public cardForm: NgForm

    public cardholder?: IUser
    public cardholderAutocomplete = new EventEmitter<string>()
    public usersStream = this.cardholderAutocomplete.pipe(
        distinctUntilChanged(),
        debounceTime(200),
        switchMap(search =>
            this.http.get<IUser[]>('/users', {
                params: new HttpParams().set('limit', '25').set('search', search),
            })
        )
    )

    public isSaving = false

    public providerMap = providerMap
    public cardProvider: CardProvider[] = ['nium']

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.copy = Object.assign({}, this.item)
    }

    public submit(): void {
        if (this.cardForm.invalid) {
            return
        }
        this.isSaving = true
        this.http
            .post<ICard>('/cards', {
                ...this.copy,
                cardholder: pick(this.cardholder, 'id'),
                type: 'physical',
            })
            .pipe(
                finalize(() => {
                    this.isSaving = false
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('card-form.card-created'))
                this.onSave.emit()
            })
    }
}
