import { HttpClient } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { cloneDeep, mapValues, values } from 'lodash'
import { Observable, of, Subject, Subscription } from 'rxjs'
import { filter, finalize, flatMap, map, switchMap, tap } from 'rxjs/operators'
import { AlertTrigger, ICurrency, Paginated, TransactionMethod } from '../../common/api-interfaces'
import { toStartCase } from '../../common/common.mixin'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { ToastrService } from '../../common/services/toastr.service'

export enum AlertTriggerType {
    ACCOUNT_INTER_ACCOUNT_TRANSACTIONS = 'account-inter-account-transactions',
    ACCOUNT_NEW_LARGE_CREDITS = 'account-new-large-credits',
    ACCOUNT_DORMANT = 'account-dormant',
    ACCOUNT_COUNTRY_HOPPING = 'account-country-hopping',
    ACCOUNT_DELAYED_OPENING = 'account-delayed-opening',
    ACCOUNT_SAME_IP_ADDRESS = 'account-same-ip-address',
    ACCOUNT_MONITORED = 'account-monitored',
    ACCOUNT_SANCTION_MATCHES = 'account-sanction-matches',
    TRANSACTION_VOLUME = 'transaction-volume',
    TRANSACTION_REPORTING_AVOIDANCE = 'transaction-reporting-avoidance',
    TRANSACTION_LARGE = 'transaction-large',
    TRANSACTION_HIGH_VELOCITY = 'transaction-high-velocity',
    TRANSACTION_HIGH_RISK_JURISDICTIONS = 'transaction-high-risk-jurisdiction',
    TRANSACTION_LOOK_UP_COUNTRY = 'transaction-look-up-country',
    TRANSACTION_FINTRAC_REPORT = 'transaction-fintrac-report',
}

interface EditableAlertTrigger {
    id?: string
    type?: AlertTriggerType
    scenario?: TransactionMethod | null
    retailHighRisk?: string | boolean
    retailMediumRisk?: string | boolean
    retailLowRisk?: string | boolean
    smeHighRisk?: string | boolean
    smeMediumRisk?: string | boolean
    smeLowRisk?: string | boolean
    corporateHighRisk?: string | boolean
    corporateMediumRisk?: string | boolean
    corporateLowRisk?: string | boolean
    agencyHighRisk?: string | boolean
    agencyMediumRisk?: string | boolean
    agencyLowRisk?: string | boolean
    merchantHighRisk?: string | boolean
    merchantMediumRisk?: string | boolean
    merchantLowRisk?: string | boolean

    createdAt?: Date
    updatedAt?: Date
    currency?: ICurrency | null
}

@Component({
    selector: 'alert-trigger-form',
    templateUrl: 'alert-trigger-form.component.html',
})
export class AlertTriggerFormComponent implements OnInit, OnDestroy {
    @Input()
    public item: AlertTrigger
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()

    public copy: EditableAlertTrigger = {}
    @ViewChild('alertTriggerForm')
    public alertTriggerForm: NgForm

    public currencies: ICurrency[]
    public alertTriggerType: AlertTriggerType[] = values(AlertTriggerType).sort() as AlertTriggerType[]
    public scenarios: TransactionMethod[] = ['internal', 'card', 'crypto', 'local', 'swift']
    public toStartCase = toStartCase

    public isSaving = false
    public isDeleting = false

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() => this.fetchCurrencies()),
                    flatMap(() =>
                        this.item && this.item.id
                            ? this.http.get<AlertTrigger>(`/alert-triggers/${this.item.id}`)
                            : of(undefined)
                    )
                )
                .subscribe(response => {
                    if (response) {
                        Object.assign(this.item, response)
                    }
                    this.copy = this.formatRuleAttributes(cloneDeep(this.item))
                })
        )
        this.fetchEvent.next()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public fetchCurrencies(): Observable<void> {
        return this.http.get<Paginated<ICurrency>>('/currencies', { params: { limit: 100 + '' } }).pipe(
            tap(response => {
                this.currencies = response.data
            }),
            map(() => undefined)
        )
    }

    public delete(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant('are-you-sure')}<br>${this.translate.instant(
                        'you-cant-undo-action'
                    )}`,
                    confirmText: this.translate.instant('common.delete'),
                    confirmClass: 'danger',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => {
                        this.isDeleting = true
                        return this.http.delete<void>(`/alert-triggers/${this.item.id}`)
                    }),
                    finalize(() => {
                        this.isDeleting = false
                    })
                )
                .subscribe(() => {
                    this.onDelete.emit()
                    this.toastr.success(this.translate.instant('alert-trigger-form.trigger-deleted'))
                })
        )
    }

    public submit(): void {
        if (this.alertTriggerForm.invalid) {
            return
        }
        this.isSaving = true
        const body = mapValues(this.copy, (value, key) =>
            key.includes('Risk') && this.isBooleanType(this.copy.type as AlertTriggerType) ? (value ? 1 : 0) : value
        )
        this.subscriptions.add(
            (this.item.id
                ? this.http.put<AlertTrigger>(`/alert-triggers/${this.item.id}`, body)
                : this.http.post<AlertTrigger>('/alert-triggers', body)
            )
                .pipe(
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(trigger => {
                    this.item = trigger
                    this.fetchEvent.next()
                    this.toastr.success(
                        `${this.translate.instant('common.trigger')} ${
                            this.item.id
                                ? this.translate.instant('common.updated').toLowerCase()
                                : this.translate.instant('common.created').toLowerCase()
                        }`
                    )
                    this.onSave.emit()
                })
        )
    }

    public isBooleanType(type: AlertTriggerType): boolean {
        return [
            AlertTriggerType.ACCOUNT_SAME_IP_ADDRESS,
            AlertTriggerType.ACCOUNT_MONITORED,
            // AlertTriggerType.ACCOUNT_SHORT_LIFESPAN,
            AlertTriggerType.ACCOUNT_DORMANT,
            AlertTriggerType.ACCOUNT_COUNTRY_HOPPING,
            AlertTriggerType.ACCOUNT_SANCTION_MATCHES,
            AlertTriggerType.TRANSACTION_HIGH_VELOCITY,
            AlertTriggerType.TRANSACTION_LOOK_UP_COUNTRY,
            AlertTriggerType.TRANSACTION_FINTRAC_REPORT,
        ].includes(type)
    }

    public isIntegerType(type: AlertTriggerType): boolean {
        return [AlertTriggerType.ACCOUNT_INTER_ACCOUNT_TRANSACTIONS, AlertTriggerType.TRANSACTION_VOLUME].includes(type)
    }

    public isNumericType(type: AlertTriggerType): boolean {
        return [
            AlertTriggerType.ACCOUNT_NEW_LARGE_CREDITS,
            AlertTriggerType.TRANSACTION_LARGE,
            AlertTriggerType.TRANSACTION_REPORTING_AVOIDANCE,
            AlertTriggerType.TRANSACTION_HIGH_RISK_JURISDICTIONS,
        ].includes(type)
    }

    public formatType(type: AlertTriggerType): string {
        return type.startsWith('account-')
            ? 'Account - ' + toStartCase(type.replace('account-', ''))
            : 'Transaction - ' + toStartCase(type.replace('transaction-', ''))
    }

    public hasMethod(): boolean {
        return [
            AlertTriggerType.ACCOUNT_NEW_LARGE_CREDITS,
            AlertTriggerType.TRANSACTION_HIGH_VELOCITY,
            AlertTriggerType.TRANSACTION_LARGE,
            AlertTriggerType.TRANSACTION_REPORTING_AVOIDANCE,
            AlertTriggerType.TRANSACTION_VOLUME,
        ].includes(this.copy.type as AlertTriggerType)
    }

    public hasCurrency(trigger: AlertTrigger = this.copy as AlertTrigger): boolean {
        return [
            AlertTriggerType.ACCOUNT_NEW_LARGE_CREDITS,
            AlertTriggerType.TRANSACTION_LARGE,
            AlertTriggerType.TRANSACTION_REPORTING_AVOIDANCE,
        ].includes(trigger.type as AlertTriggerType)
    }

    public isCurrencyRequired(trigger: AlertTrigger = this.copy as AlertTrigger): boolean {
        return ![AlertTriggerType.TRANSACTION_HIGH_VELOCITY, AlertTriggerType.TRANSACTION_VOLUME].includes(
            trigger.type as AlertTriggerType
        )
    }

    private formatRuleAttributes(t: AlertTrigger): EditableAlertTrigger {
        const decimalPlaces =
            this.isNumericType(t.type as AlertTriggerType) && this.hasCurrency(t)
                ? (t.currency && t.currency.decimalPlaces) || 0
                : 0
        t.retailHighRisk = t.retailHighRisk && new BigNumber(t.retailHighRisk).toFixed(decimalPlaces, 1)
        t.retailMediumRisk = t.retailMediumRisk && new BigNumber(t.retailMediumRisk).toFixed(decimalPlaces, 1)
        t.retailLowRisk = t.retailLowRisk && new BigNumber(t.retailLowRisk).toFixed(decimalPlaces, 1)
        t.smeHighRisk = t.smeHighRisk && new BigNumber(t.smeHighRisk).toFixed(decimalPlaces, 1)
        t.smeMediumRisk = t.smeMediumRisk && new BigNumber(t.smeMediumRisk).toFixed(decimalPlaces, 1)
        t.smeLowRisk = t.smeLowRisk && new BigNumber(t.smeLowRisk).toFixed(decimalPlaces, 1)
        t.corporateHighRisk = t.corporateHighRisk && new BigNumber(t.corporateHighRisk).toFixed(decimalPlaces, 1)
        t.corporateMediumRisk = t.corporateMediumRisk && new BigNumber(t.corporateMediumRisk).toFixed(decimalPlaces, 1)
        t.corporateLowRisk = t.corporateLowRisk && new BigNumber(t.corporateLowRisk).toFixed(decimalPlaces, 1)
        t.agencyHighRisk = t.agencyHighRisk && new BigNumber(t.agencyHighRisk).toFixed(decimalPlaces, 1)
        t.agencyMediumRisk = t.agencyMediumRisk && new BigNumber(t.agencyMediumRisk).toFixed(decimalPlaces, 1)
        t.agencyLowRisk = t.agencyLowRisk && new BigNumber(t.agencyLowRisk).toFixed(decimalPlaces, 1)
        t.merchantHighRisk = t.merchantHighRisk && new BigNumber(t.merchantHighRisk).toFixed(decimalPlaces, 1)
        t.merchantMediumRisk = t.merchantMediumRisk && new BigNumber(t.merchantMediumRisk).toFixed(decimalPlaces, 1)
        t.merchantLowRisk = t.merchantLowRisk && new BigNumber(t.merchantLowRisk).toFixed(decimalPlaces, 1)
        return mapValues(t, (value, key) =>
            key.includes('Risk') && this.isBooleanType(t.type as AlertTriggerType)
                ? value && !new BigNumber(value as string).eq(0)
                : value
        ) as EditableAlertTrigger
    }
}
