import { HttpClient } from '@angular/common/http'
import { AfterContentInit, ChangeDetectorRef, Component, Input, OnInit, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { cloneDeep } from 'lodash'
import { readFileAsDataURL } from 'src/app/common/common.mixin'
import { ListComponent } from 'src/app/common/list.component'
import { MailFormComponent, Template } from 'src/app/common/mails/mail-form/mail-form.component'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { IUser, Mail, UserType } from '../../../common/api-interfaces'

@Component({
    selector: 'close-accounts-modal',
    templateUrl: 'close-accounts-modal.component.html',
})
export class CloseAccountsModalComponent extends ListComponent<IUser> implements OnInit, AfterContentInit {
    public templates = [
        {
            subject: 'Account Status Update',
            group: 'closing',
            name: 'Exit forced by us',
            message:
                '<p>We are a dynamic company, which considers and reviews its business activities on a periodic basis. As part of this review, we have made a commercial decision to close your account with us.</p>' +
                '<p>We hereby would like you to close your account during the next 30 days and return any remaining funds to an account with another institution in your name.</p>',
        },
        {
            subject: 'Account Status Update',
            group: 'closing',
            name: 'Exit requested by the client',
            message:
                '<p>Thank you for your recent request to our Customer Support team.</p>' +
                '<p>We are sorry that you wish to close your account but we wanted to thank you for doing business with us and remind you to return any remaining funds to an account with another institution in your name.</p>',
        },
        {
            subject: 'Account Status Update',
            group: 'closed',
            name: 'Account closed',
            message:
                '<p>Please note that the status of your account has now been marked as closed.</p>' +
                '<p>Thank you for doing business with us.</p>',
        },
        {
            subject: 'Account Status Update',
            group: 'rejected',
            name: 'Account rejected',
            message:
                '<p>Thank you for applying for an account.</p>' +
                '<p>We would like to inform you that we have been unable to process the information you have provided to us with your application to the standard required by our internal processes.</p>' +
                '<p>We advise you regrettably that your application has been declined.</p>',
        },
    ]

    public showMailForm = false

    public activeTab: 'overview' | 'accounts' = 'overview'
    public userTypes: UserType[] = ['personal', 'business']
    public item = {
        period: '180',
        type: 'personal',
        verified: false,
        fundingStatus: 'no-balance',
    }
    @ViewChild('form')
    public form: NgForm

    @Input()
    public selectedTemplate: Template
    @ViewChild('mailForm')
    public mailForm: MailFormComponent

    public mail: Partial<Mail> = {
        confidential: false,
    }
    public files: File[] = []
    public isLoading = false
    public canClose = true

    protected apiUrl = '/admin/users/account-closure'

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public ngOnInit(): void {
        this.selectedTemplate = this.templates.find(template => template.name === 'Account closed')!

        super.ngOnInit()
    }

    public ngAfterContentInit(): void {
        this.updateParams()
    }

    public async submit(): Promise<void> {
        if (!this.form.valid) {
            this.toastr.error(this.translate.instant('common.form-invalid'))
            return
        }

        const body = cloneDeep(this.item)

        if (this.showMailForm) {
            Object.assign(body, {
                message: {
                    subject: this.mail.subject,
                    content: this.mail.content,
                    confidential: this.mail.confidential,
                    attachments: await Promise.all(
                        this.files.map(async file => ({
                            name: encodeURIComponent(file.name),
                            base64: await readFileAsDataURL(file),
                        }))
                    ),
                },
            })
        }

        this.http.post<void>('/admin/users/account-closure', body).subscribe(() => {
            this.toastr.success(this.translate.instant('close-accounts-modal.account-closure-task-enqueued'))
            this.activeModal.close()
        })
    }

    public updateParams(): void {
        this.apiUrl = `/admin/users/account-closure?period=${this.item.period}&type=${this.item.type}&verified=${this.item.verified}&fundingStatus=${this.item.fundingStatus}`
    }

    public preventClose(): void {
        this.canClose = false
        setTimeout(() => {
            this.canClose = true
        }, 1000)
    }
}
