import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { faCalendar } from '@fortawesome/pro-regular-svg-icons'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import formatDate from 'date-fns/format'
import parseISO from 'date-fns/parseISO'
import { cloneDeep, pull } from 'lodash'
import { forkJoin, Observable, of, Subject, Subscription, throwError } from 'rxjs'
import { catchError, filter, finalize, flatMap, map, switchMap, tap } from 'rxjs/operators'
import slug from 'slugify'
import {
    File as IFile,
    ICurrency,
    Paginated,
    Product,
    ProductFile,
    ProductFilePurpose,
    ProductType,
} from '../../common/api-interfaces'
import { toStartCase } from '../../common/common.mixin'
import { FileView } from '../../common/file'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { ToastrService } from '../../common/services/toastr.service'

@Component({
    selector: 'product-form',
    templateUrl: 'product-form.component.html',
})
export class ProductFormComponent implements OnInit, OnDestroy {
    public faCalendar = faCalendar
    @Input()
    public item: Product
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()

    public copy: Partial<Product> = {}
    @ViewChild('productForm')
    public productForm: NgForm

    public currencies: ICurrency[]
    public productTypes: ProductType[] = ['entitlement', 'option', 'property', 'share', 'unit', 'bond', 'loan']

    public isSaving = false
    public isDeleting = false

    public currentTab: 'content' | 'terms' = 'content'

    public image?: File
    public filePurposes: ProductFilePurpose[] = [
        'product-overview',
        'security-document',
        'financial-service-guide',
        'product-disclosure-statement-1',
        'product-disclosure-statement-2',
        'frequently-asked-questions',
    ]
    public toStartCase = toStartCase
    public files: IFile[] = []
    public uploads: { [K in ProductFilePurpose]?: File } = {}
    public filesToDelete: any[] = []

    public openingDate: string | null = ''
    public closingDate: string | null = ''

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() =>
                        forkJoin([
                            this.item && this.item.id
                                ? this.http.get<Product>(`/products/${this.item.id}`)
                                : of(undefined),
                            this.item && this.item.id
                                ? this.http.get<ProductFile>(`/products/${this.item.id}/files`)
                                : of(undefined),
                            this.fetchCurrencies(),
                        ])
                    )
                )
                .subscribe(([product, productFiles]) => {
                    if (product) {
                        Object.assign(this.item, product)
                    }
                    Object.assign(this.files, productFiles)
                    if (!this.item.fundingCurrency || !this.item.fundingCurrency.code) {
                        this.item.fundingCurrency = this.currencies[0]
                    }
                    if (this.item.openingDate) {
                        this.openingDate = formatDate(parseISO(this.item.openingDate), 'ddMMyyyy')
                    }
                    if (this.item.closingDate) {
                        this.closingDate = formatDate(parseISO(this.item.closingDate), 'ddMMyyyy')
                    }
                    this.copy = cloneDeep(this.item)
                    if (this.copy.minimumSubscription) {
                        this.copy.minimumSubscription = new BigNumber(this.copy.minimumSubscription).toFixed(
                            this.copy.fundingCurrency!.decimalPlaces,
                            1
                        )
                    }
                    if (this.copy.maximumSubscription) {
                        this.copy.maximumSubscription = new BigNumber(this.copy.maximumSubscription).toFixed(
                            this.copy.fundingCurrency!.decimalPlaces,
                            1
                        )
                    }
                    if (this.copy.minimumInvestment) {
                        this.copy.minimumInvestment = new BigNumber(this.copy.minimumInvestment).toFixed(
                            this.copy.fundingCurrency!.decimalPlaces,
                            1
                        )
                    }
                    if (this.copy.currency && this.copy.currency.exchangeRate) {
                        this.copy.currency.exchangeRate = new BigNumber(this.copy.currency.exchangeRate).toFixed(
                            this.copy.fundingCurrency!.decimalPlaces,
                            1
                        )
                    }
                })
        )

        this.copy = cloneDeep(this.item)
        this.fetchEvent.next()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public fetchCurrencies(): Observable<void> {
        return this.http
            .get<Paginated<ICurrency>>('/currencies', {
                params: {
                    limit: 100 + '',
                    'filter.type': 'fiat',
                },
            })
            .pipe(
                tap(response => {
                    this.currencies = response.data
                }),
                map(() => undefined)
            )
    }

    public delete(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant('common.are-you-sure-want-to-delete')}<br>${this.translate.instant(
                        'common.you-cant-undo-action'
                    )}`,
                    confirmText: this.translate.instant('common.delete'),
                    confirmClass: 'danger',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => {
                        this.isDeleting = true
                        return this.http.delete<void>(`/products/${this.item.id}`)
                    }),
                    finalize(() => {
                        this.isDeleting = false
                    })
                )
                .subscribe(() => {
                    this.onDelete.emit()
                    this.toastr.success(this.translate.instant('product-form.product-deleted'))
                })
        )
    }

    public submit(): void {
        if (this.productForm.invalid) {
            return
        }
        this.isSaving = true
        this.subscriptions.add(
            (this.item.id
                ? this.http.put<Product>(`/products/${this.item.id}`, this.copy)
                : this.http.post<Product>('/products', this.copy)
            )
                .pipe(
                    flatMap(product =>
                        forkJoin<any>([
                            ...(Object.keys(this.uploads) as ProductFilePurpose[])
                                .filter(purpose => this.uploads[purpose])
                                .map((purpose: ProductFilePurpose) =>
                                    this.http
                                        .post<IFile>(`/products/${product.id}/files`, this.uploads[purpose], {
                                            headers: new HttpHeaders({
                                                'X-File-Purpose': purpose,
                                                'X-File-Name': this.uploads[purpose]!.name,
                                                'Content-Type': this.uploads[purpose]!.type,
                                            }),
                                        })
                                        .pipe(
                                            catchError(err => {
                                                this.toastr.error(`${this.uploads[purpose]!.name} - ${err.message}`)
                                                return throwError(err)
                                            }),
                                            tap(file => {
                                                this.files.push(file)
                                                this.uploads[purpose] = undefined
                                            })
                                        )
                                ),
                            ...this.filesToDelete.map(file =>
                                this.http.delete<void>(`/products/${product.id}/files/${file.id}`).pipe(
                                    catchError(err => {
                                        this.toastr.error(`${file.name} - ${err.message}`)
                                        return throwError(err)
                                    }),
                                    tap(() => pull(this.filesToDelete, file))
                                )
                            ),
                            this.image
                                ? this.http
                                      .post<IFile>(`/products/${product.id}/image`, this.image, {
                                          headers: new HttpHeaders({
                                              'X-File-Name': this.image.name,
                                              'Content-Type': this.image.type,
                                          }),
                                      })
                                      .pipe(
                                          catchError(err => {
                                              this.toastr.error(`${this.image!.name} - ${err.message}`)
                                              return throwError(err)
                                          })
                                      )
                                : of(undefined),
                        ])
                    ),
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(() => {
                    this.fetchEvent.next()
                    this.toastr.success(
                        `${this.translate.instant('common.product')} ${
                            this.item.id
                                ? this.translate.instant('common.updated')
                                : this.translate.instant('common.created')
                        }`
                    )
                    this.onSave.emit()
                })
        )
    }

    public slugify(name: string | undefined): string {
        if (!name) {
            return ''
        }
        return slug(name, { lower: true })
    }

    public isUpload(file: any): file is File {
        return file instanceof File
    }

    public getUploadByPurpose(purpose: ProductFilePurpose): File | undefined {
        return this.uploads[purpose]
    }

    public openFile(file: any): Observable<void> {
        const view = new FileView()
        return (
            this.isUpload(file)
                ? of(file)
                : this.http.get(`/products/${this.item.id}/files/${file.id}`, {
                      responseType: 'blob',
                  })
        ).pipe(
            catchError(err => {
                view.close()
                return throwError(err)
            }),
            tap(blob => view.show(blob, file.name)),
            map(() => undefined)
        )
    }

    public selectFile(file: File, purpose: ProductFilePurpose): void {
        const fileToDelete = this.getFileByPurpose(purpose)
        if (fileToDelete) {
            this.removeFile(fileToDelete)
        }
        this.uploads[purpose] = file
    }

    public removeFile(file: File | any, purpose?: ProductFilePurpose): void {
        if (file instanceof File && purpose) {
            this.uploads[purpose] = undefined
        } else {
            pull(this.files, file)
            this.filesToDelete.push(file)
        }
    }

    public getFileByPurpose(purpose: ProductFilePurpose): IFile | undefined {
        return this.files.find((file: any) => file.productFile && file.productFile.purpose === purpose)
    }

    public convertToISODate(date: string): string | undefined {
        return date && `${date.slice(4)}-${date.slice(2, 4)}-${date.slice(0, 2)}`
    }
}
