import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faCircle, faSyncAlt } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { Observable, Subject } from 'rxjs'
import { filter, flatMap, map, switchMap } from 'rxjs/operators'
import { ListV2Component } from 'src/app/common/list-v2.component'
import { ICurrency } from '../../common/api-interfaces'
import { Currency } from '../../common/models/accounting/currency.model'
import { CurrencyService } from '../../common/services/accounting/currency.service'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { SessionService } from '../../common/services/session.service'
import { ToastrService } from '../../common/services/toastr.service'
import { CurrencyFormComponent } from './currency-form.component'

@Component({
    selector: 'currencies',
    templateUrl: 'currencies.component.html',
})
export class CurrenciesComponent extends ListV2Component<ICurrency & any> implements OnInit {
    public exchangeRatesRefreshedEvent = new Subject<void>()
    public faCircle = faCircle
    public faSyncAlt = faSyncAlt
    public apiUrl = '/currencies'
    public defaultOrder = [['code', 'ASC']]
    public formComponent = CurrencyFormComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        private currencyService: CurrencyService,
        private confirmation: ConfirmationService,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session)
    }

    public ngOnInit(): void {
        this.subscriptions.add(
            this.exchangeRatesRefreshedEvent
                .pipe(
                    switchMap(() => this.refreshExchangeRates()),
                    map(() => this.updateEvent.asObservable())
                )
                .subscribe(() => this.toastr.info(this.translate.instant('currencies.exchange-rates-refreshed')))
        )
        super.ngOnInit()
    }

    public toggleValue(currency: Currency, property: string, value: string): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'info',
                    text: `${this.translate.instant('common.are-you-sure-want-to-make')} ${currency.code} ${
                        (!value ? '' : 'non-') + property
                    }?`,
                    confirmText: this.translate.instant('common.yes'),
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() =>
                        this.currencyService.update(currency.code, { ...currency, ...{ [property]: !value } })
                    )
                )
                .subscribe(response => {
                    Object.assign(currency, response)
                    this.toastr.success(this.translate.instant('common.currency-updated'))
                })
        )
    }

    public convertToUsd(currency: Currency): string {
        const value = new BigNumber(1).div(currency.exchangeRate).toNumber()
        return -Math.floor(Math.log10(value) + 1) > 2
            ? new BigNumber(value).toFixed(4, 1)
            : new BigNumber(value).toFixed(2, 1)
    }

    public refreshExchangeRates(): Observable<void> {
        return this.http.post<void>('/admin/currencies/refresh-exchange-rates', {}).pipe(map(() => undefined))
    }

    protected mapItem(item: any): Currency {
        return new Currency(item)
    }
}
