import { HttpClient } from '@angular/common/http'
import { AfterViewInit, Component, NgZone, OnDestroy, OnInit } from '@angular/core'
import {
    faArrowAltFromBottom,
    faArrowAltToBottom,
    faBell,
    faBuilding,
    faCoins,
    faDollarSign,
    faGripHorizontal,
    faPaperPlane,
    faUsers,
} from '@fortawesome/pro-duotone-svg-icons'
import { faCircle, faMinus, faPlus } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import BigNumber from 'bignumber.js'
import * as Color from 'color'
import * as getSymbolFromCurrency from 'currency-symbol-map'
import { orderBy, uniqWith } from 'lodash'
import { forkJoin, Subject, Subscription } from 'rxjs'
import { debounceTime, map, switchMap, tap } from 'rxjs/operators'
import { environment } from 'src/environments/environment'
import { ICardProgram, ICurrency, Paginated, Transaction as ITransaction, Wallet } from '../../common/api-interfaces'
import { countryMap } from '../../common/countries'
import { Currency } from '../../common/models/accounting/currency.model'
import { Transaction } from '../../common/models/accounting/transaction.model'
import { SessionService } from '../../common/services/session.service'
import { TransactionFormComponent } from '../transactions/transaction-form.component'
import am4geodata_worldLow from './world-low'

interface Supply {
    sum: string
    currency: ICurrency
}

interface VerificationStatistic {
    total: number
    verified: number
    approvalRate: string
}

interface NiumPrefundBalance {
    accountType: 'CLIENT_POOL' | 'WALLET_POOL'
    balance: number
    currency: string
    isDefault: 'true' | 'false'
    createdAt: string
    updatedAt: string
}

const importAmcharts = () =>
    Promise.all([
        import(/* webpackChunkName: "amcharts" */ '@amcharts/amcharts4/core'),
        import(/* webpackChunkName: "amcharts" */ '@amcharts/amcharts4/maps'),
    ])

@Component({
    selector: 'admin-dashboard',
    templateUrl: 'admin-dashboard.component.html',
})
export class AdminDashboardComponent implements OnInit, OnDestroy, AfterViewInit {
    public getSymbolFromCurrency = getSymbolFromCurrency
    public countryMap = countryMap
    public faUsers = faUsers
    public faBuilding = faBuilding
    public faCoins = faCoins
    public faArrowAltToBottom = faArrowAltToBottom
    public faArrowAltFromBottom = faArrowAltFromBottom
    public faPaperPlane = faPaperPlane

    public countryStats: any[]

    public usersStatistics: VerificationStatistic
    public companiesStatistics: VerificationStatistic

    public transactionsStatistics: any
    public profit: string

    public faGripHorizontal = faGripHorizontal
    public faDollarSign = faDollarSign
    public faBell = faBell

    public stats: Supply[]
    public rootWallets: Wallet[]
    public profitStats: any[]
    public faCircle = faCircle
    public faPlus = faPlus
    public faMinus = faMinus

    public cardPrograms: ICardProgram &
        {
            clientPoolBalances: NiumPrefundBalance[]
            walletPoolBalances: NiumPrefundBalance[]
        }[]
    public isFetchingCardProgram = true

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    private chart: any

    constructor(
        private http: HttpClient,
        private zone: NgZone,
        private ngbModal: NgbModal,
        public session: SessionService
    ) {}

    public ngOnInit(): void {
        if (!['admin', 'licensee'].includes(this.session.user.role)) {
            return
        }
        this.subscriptions.add(
            forkJoin([
                this.http.get<VerificationStatistic>('/statistics/users'),
                this.http.get<VerificationStatistic>('/statistics/companies'),
                this.http.get<VerificationStatistic>('/statistics/transactions'),
            ]).subscribe(([usersStatistics, companyStatistics, transactionsStatistics]) => {
                this.usersStatistics = usersStatistics
                this.companiesStatistics = companyStatistics
                this.transactionsStatistics = transactionsStatistics
            })
        )
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    debounceTime(600),
                    switchMap(() =>
                        forkJoin(
                            this.http.get<Supply[]>(`/statistics/supply`),
                            this.http.get<Wallet[]>(`/admin/wallets`, { params: { limit: 100 + '' } }),
                            this.http.get<any[]>('/statistics/profit'),
                            this.http
                                .get<Paginated<ICurrency>>('/currencies', { params: { limit: 100 + '' } })
                                .pipe(map(response => response.data))
                        )
                    )
                )
                .subscribe(([stats, rootWallets, profitStats, currencies]) => {
                    this.stats = stats.map(s => {
                        s.currency = new Currency(s.currency)
                        return s
                    })
                    this.rootWallets = rootWallets
                    this.profitStats = profitStats
                    this.profit = profitStats.reduce(
                        (prev, curr) =>
                            new BigNumber(
                                Currency.convert(
                                    curr.sum,
                                    currencies.find(currency => currency.code === curr.currency.code)!,
                                    currencies.find(
                                        currency => currency.code === this.session.user.preferredCurrency.code
                                    )!
                                )
                            )
                                .plus(prev)
                                .toString(),
                        0
                    )
                })
        )
        this.subscriptions.add(
            this.http
                .get<Paginated<ICardProgram>>('/admin/card-programs')
                .pipe(
                    tap(cardPrograms => {
                        cardPrograms.data.map((cardProgram: any) => {
                            this.http
                                .get<NiumPrefundBalance[]>(`/admin/card-programs/${cardProgram.id}/get-prefund-balance`)
                                .subscribe(balances => {
                                    cardProgram.clientPoolBalances = orderBy(
                                        balances.filter(balance => balance.accountType === 'CLIENT_POOL'),
                                        ['currency'],
                                        ['asc']
                                    )
                                    cardProgram.walletPoolBalances = orderBy(
                                        balances.filter(balance => balance.accountType === 'WALLET_POOL'),
                                        ['currency'],
                                        ['asc']
                                    )
                                })
                        })
                    })
                )
                .subscribe(cardPrograms => {
                    this.cardPrograms = cardPrograms.data as any
                    this.isFetchingCardProgram = false
                })
        )
        this.fetchEvent.next()
    }

    public ngAfterViewInit(): void {
        this.subscriptions.add(
            this.http.get<any[]>('/statistics/demography').subscribe(response => {
                this.zone.runOutsideAngular(async () => {
                    const [am4core, am4maps] = await importAmcharts()

                    const style = getComputedStyle(document.body)
                    const color = Color(style.getPropertyValue('--primary').trim())
                    const chart = am4core.create('chartdiv', am4maps.MapChart)

                    // Set map definition
                    chart.geodata = am4geodata_worldLow

                    // Set projection
                    chart.projection = new am4maps.projections.Miller()

                    // Create map polygon series
                    const polygonSeries = chart.series.push(new am4maps.MapPolygonSeries())

                    // Make map load polygon (like country names) data from GeoJSON
                    polygonSeries.useGeodata = true

                    // Configure series
                    const polygonTemplate = polygonSeries.mapPolygons.template
                    polygonTemplate.tooltipHTML =
                        '<b>{name}</b><br><small>Individuals: {users}<br>Businesses: {companies}</small>'
                    polygonTemplate.fill = am4core.color(color.lightness(83).desaturate(0.2).hex())

                    // Create hover state and set alternative fill color
                    const hs = polygonTemplate.states.create('hover')
                    hs.properties.fill = am4core.color(color.darken(0.2).hex())

                    // Remove Antarctica
                    polygonSeries.exclude = ['AQ']

                    // Add heat rule
                    polygonSeries.heatRules.push({
                        property: 'fill',
                        target: polygonSeries.mapPolygons.template,
                        min: am4core.color(
                            color
                                .lighten(environment.appName === 'sgpmx' ? 1 : 0.15)
                                .desaturate(environment.appName === 'sgpmx' ? 0.45 : 0.1)
                                .hex()
                        ),
                        max: am4core.color(color.darken(0.1).hex()),
                    })

                    // Add data
                    this.countryStats = uniqWith(response, (a, b) => a.country === b.country)
                        .map(item => {
                            const countryItems = response.filter(s => s.country === item.country)
                            const users = countryItems
                                .filter(c => c.type === 'personal')
                                .reduce((prev, curr) => prev + ~~curr.count, 0)
                            const companies = countryItems
                                .filter(c => c.type === 'business')
                                .reduce((prev, curr) => prev + ~~curr.count, 0)
                            return {
                                id: item.country,
                                value: users + companies,
                                users,
                                companies,
                            }
                        })
                        .sort((a, b) => (a.value > b.value ? -1 : 1))
                    const countriesWithDefaultStat = am4geodata_worldLow.features
                        .filter(country => !this.countryStats.find(c => c.id === country.id))
                        .map(country =>
                            // Add default values for other countries
                            ({
                                id: country.id,
                                value: 0,
                                users: 0,
                                companies: 0,
                            })
                        )
                    polygonSeries.data = this.countryStats.concat(countriesWithDefaultStat)

                    chart.chartContainer.wheelable = false
                    chart.zoomControl = new am4maps.ZoomControl()
                    chart.zoomControl.valign = 'top'

                    this.chart = chart
                })
            })
        )
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
        this.zone.runOutsideAngular(() => {
            if (this.chart) {
                this.chart.dispose()
            }
        })
    }

    public invertExchangeRate(exchangeRate: string): string {
        return new BigNumber(1).div(exchangeRate).toFixed(8, 1)
    }

    public formatTooltip = (item: any) => item.data.name

    public generateFunds(currency: ICurrency): void {
        this.showTransactionForm({
            type: 'mint',
            method: 'internal',
            baseWallet: null,
            counterWallet: { user: null } as Wallet,
            currency,
        })
    }

    public destroyFunds(currency: ICurrency): void {
        this.showTransactionForm({
            type: 'mint',
            method: 'internal',
            baseWallet: { user: null } as Wallet,
            counterWallet: null,
            currency,
        })
    }

    public getBalanceByCurrency(currency: ICurrency): string | null {
        const rootWallet = this.rootWallets.find(wallet => wallet.currency.code === currency.code)
        if (!rootWallet) {
            return new BigNumber(0).toFixed(1, currency.decimalPlaces as any)
        }
        return rootWallet.balance
    }

    private showTransactionForm(transaction: Partial<ITransaction>): void {
        const modal = this.ngbModal.open(TransactionFormComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
        })
        modal.componentInstance.item = new Transaction(transaction as ITransaction)
        modal.componentInstance.onSave.subscribe(() => {
            modal.close()
            this.fetchEvent.next()
        })
    }
}
