import { Component } from '@angular/core'
import { timer } from 'rxjs'
import { flatMap } from 'rxjs/operators'
import { StatisticsService } from '../../common/services/core/statistics.service'
import { SessionService } from '../../common/services/session.service'

@Component({
    selector: 'admin-realtime',
    templateUrl: 'admin-realtime.component.html',
})
export class AdminRealtimeComponent {
    public onlineUsers$ = timer(0, 1000 * 60).pipe(flatMap(() => this.statistics.getOnlineUsers()))

    constructor(private statistics: StatisticsService, public session: SessionService) {}
}
