import { HttpClient, HttpParams } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faAnalytics, faCircle } from '@fortawesome/pro-duotone-svg-icons'
import { faLongArrowRight } from '@fortawesome/pro-light-svg-icons'
import { faCalendarAlt } from '@fortawesome/pro-regular-svg-icons'
import { faExclamationTriangle } from '@fortawesome/pro-solid-svg-icons'
import { NgbCalendar, NgbDateStruct, NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { isEqual } from 'lodash'
import { merge, of } from 'rxjs'
import { debounceTime, filter, flatMap, switchMap } from 'rxjs/operators'
import { SecurityCheckService } from 'src/app/common/security-check/security-check.service'
import { environment } from 'src/environments/environment'
import { ICurrency, Paginated, TransactionMethod, TransactionTag, TransactionType } from '../../common/api-interfaces'
import { toStartCase } from '../../common/common.mixin'
import { ListComponent } from '../../common/list.component'
import { methodMap, Transaction } from '../../common/models/accounting/transaction.model'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { SessionService } from '../../common/services/session.service'
import { ToastrService } from '../../common/services/toastr.service'
import { TransactionService } from '../../common/services/transaction.service'
import {
    calculateCreditableAmount,
    calculateDeductableAmount,
    calculateFee,
    isTransactionProcessable,
} from '../../common/transactions.mixins'
import { TransactionFormComponent } from './transaction-form.component'
import { TransactionProcessComponent } from './transaction-process.component'

export type TransactionStats = (Pick<ICurrency, 'code' | 'name' | 'decimalPlaces'> & {
    volume?: string
    credit?: string
    debit?: string
    fees: string
    totalVolumeInUsd?: string
    totalCreditInUsd?: string
    totalDebitInUsd?: string
    totalFeesInUsd: string
})[]

@Component({
    selector: 'transactions',
    templateUrl: 'transactions.component.html',
})
export class TransactionsComponent extends ListComponent<Transaction> implements OnInit {
    public statuses = ['pending', 'processing', 'completed', 'cancelled', 'failed']
    public methods: TransactionMethod[] = ['card', 'crypto', 'internal', 'local', 'swift']
    public types: TransactionType[] = [
        'account-fee',
        'card-order',
        'conversion',
        'deposit',
        'investment',
        'mint',
        'payment',
        'redeem',
        'reverse',
        'trade',
        'transfer',
        'withdrawal',
        'invoice',
        'invoice-p2p',
    ]

    public typeTabs: TransactionType[] = ['deposit', 'withdrawal', 'transfer']
    public otherTypes: Exclude<TransactionType, 'deposit' | 'withdrawal' | 'transfer'>[] = [
        'card-order',
        'conversion',
        'investment',
        'payment',
        'redeem',
        'reverse',
        'account-fee',
        'trade',
    ]

    public faCalendarAlt = faCalendarAlt
    public faExclamationTriangle = faExclamationTriangle
    public faAnalytics = faAnalytics
    public faCircle = faCircle
    public faLongArrowRight = faLongArrowRight
    public toStartCase = toStartCase
    public calculateFee = calculateFee
    public calculateCreditableAmount = calculateCreditableAmount
    public calculateDeductableAmount = calculateDeductableAmount
    public methodMap = methodMap
    public isProcessable = isTransactionProcessable
    public currencies: ICurrency[] = []

    public appName = environment.appName

    @Input()
    public userId?: string
    @Input()
    public displayStatementButton = false
    public fromDate: NgbDateStruct
    public toDate: NgbDateStruct

    @Input()
    public apiUrl = '/transactions'
    public apiQuery = ['amount', 'type', 'method', 'currencyCode', 'status', 'tags', 'settledAt', 'createdAt']
    @Input()
    public defaultQuery: { [param: string]: { [operator: string]: string | string[] } } = {}
    public doCollapseFilterOptionsIfParamsSet = false
    public transactionsStats: TransactionStats

    // SGPMX
    public hasReceipt: Record<string, boolean> = {}

    public tags: TransactionTag[] = ['medici', 'nium', 'mni', 'public-bank', 'muamlat', 'b2c2', 'icb']

    protected formComponent = TransactionFormComponent
    protected model = Transaction

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private transactionService: TransactionService,
        private toastr: ToastrService,
        private securityCheckService: SecurityCheckService,
        public calendar: NgbCalendar,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
        this.fromDate = { year: new Date().getFullYear(), month: 1, day: 1 }
        this.toDate = this.calendar.getToday()
        this.http.get<Paginated<ICurrency>>('/currencies', { params: { limit: 100 + '' } }).subscribe(response => {
            this.currencies = response.data
        })
    }

    public ngOnInit(): void {
        super.ngOnInit()
        if (environment.appName === 'sgpmx') {
            this.subscriptions.add(
                this.itemsStream
                    .pipe(
                        flatMap(items =>
                            this.http.post<Record<string, boolean>>(
                                `/sgpmx/transactions/receipt-check`,
                                items.map(item => item.id)
                            )
                        )
                    )
                    .subscribe(receiptCheck => {
                        this.hasReceipt = receiptCheck
                    })
            )
        }
        this.subscriptions.add(
            merge(of(undefined), this.searchEvent.pipe(debounceTime(600)), this.updateEvent)
                .pipe(
                    switchMap(() => {
                        let params = this.generateListParams()
                        if (!params.get('status')) {
                            params = params.set('status', 'completed')
                        }
                        if (params.get('type') === 'ne:mint') {
                            params = params.set('type', 'or:deposit,transfer,withdrawal')
                        }
                        const userId = this.route.snapshot.parent?.paramMap.get('userId')
                        return this.http.get<TransactionStats>(
                            userId ? `/users/${userId}/transactions/stats` : `/transactions/stats`,
                            {
                                params,
                            }
                        )
                    })
                )
                .subscribe(stats => {
                    this.transactionsStats = stats
                })
        )
    }

    public generateListParams(override: { limit?: number } = {}): HttpParams {
        const params = super.generateListParams(override)
        if (!params.get('type')) {
            return params.set('type', 'ne:mint')
        }
        return params
    }

    public start(item: Transaction): void {
        this.subscriptions.add(
            this.transactionService.changeStatus(item.id, 'processing').subscribe(() => {
                this.updateEvent.next()
            })
        )
    }

    public process(item: Transaction): void {
        const modal = this.ngbModal.open(TransactionProcessComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
        })
        const component = modal.componentInstance as TransactionProcessComponent
        component.transaction = item
        component.onSave.subscribe(() => {
            this.updateEvent.next()
            modal.close()
        })
    }

    public cancel(item: Transaction): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    title: this.translate.instant('common.cancel-transaction'),
                    text: `${this.translate.instant(
                        'common.are-you-sure-want-to-cancel-transaction'
                    )} <div class="text-gray font-sm">${item.id}</div>`,
                    confirmText: this.translate.instant('common.confirm'),
                    cancelText: this.translate.instant('common.cancel'),
                    confirmClass: 'danger',
                    cancelClass: 'default',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.transactionService.changeStatus(item.id, 'cancelled'))
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('common.transaction-cancelled'))
                    this.updateEvent.next()
                })
        )
    }

    public revert(item: Transaction): void {
        this.confirmation
            .show({
                type: 'danger',
                title: this.translate.instant('common.revert-transaction'),
                text: `${this.translate.instant(
                    'common.are-you-sure-want-to-revert-transaction'
                )} <div class="text-gray font-sm">${item.id}</div>`,
                confirmText: this.translate.instant('common.confirm'),
                confirmClass: 'danger',
                cancelClass: 'default',
            })
            .pipe(
                filter(result => result === ConfirmationResult.CONFIRMED),
                flatMap(() => this.securityCheckService.getScopePermission('banking')),
                flatMap(() => this.transactionService.revert(item.id))
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('transactions.transaction-reverted'))
                this.updateEvent.next()
            })
    }

    public restore(item: Transaction): void {
        this.confirmation
            .show({
                type: 'info',
                title: this.translate.instant('common.restore-transaction'),
                text: `${this.translate.instant(
                    'common.are-you-sure-want-to-restore-transaction'
                )} <div class="text-gray font-sm">${item.id}</div>`,
                confirmText: this.translate.instant('common.confirm'),
                confirmClass: 'info',
                cancelClass: 'default',
            })
            .pipe(
                filter(result => result === ConfirmationResult.CONFIRMED),
                flatMap(() => this.securityCheckService.getScopePermission('banking')),
                flatMap(() => this.transactionService.restore(item.id))
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('transactions.transaction-restored'))
                this.updateEvent.next()
            })
    }

    public downloadReport(): void {
        this.subscriptions.add(
            this.session
                .createSignedUrl(`/users/${this.route.snapshot.parent!.paramMap.get('userId')}/transactions/statement`)
                .subscribe(signedUrl => {
                    window.open(
                        `${signedUrl}&from=${this.parseNgbdate(this.fromDate)}&to=${this.parseNgbdate(this.toDate)}`,
                        '_blank',
                        'noopener'
                    )
                })
        )
    }

    public requestExportForSgpmx(): void {
        let url = '/transactions/sgpmx-report'
        if (!!this.route.snapshot.parent!.paramMap.get('userId')) {
            url = `/users/${this.route.snapshot.parent!.paramMap.get('userId')}` + url
        }
        this.subscriptions.add(
            this.session.createSignedUrl(url).subscribe(signedUrl => {
                if (this.router.url.includes('?')) {
                    signedUrl = signedUrl + `&${this.router.url.split('?')[1]}`
                }
                if (this.router.url === '/admin/banking/deposits') {
                    signedUrl = signedUrl + '&type=eq:deposit'
                }
                if (this.router.url === '/admin/banking/withdrawals') {
                    signedUrl = signedUrl + '&type=eq:withdrawal'
                }
                window.open(`${signedUrl}`, '_blank', 'noopener')
            })
        )
    }

    public openSgpmxInvoice(transaction: Transaction): void {
        this.subscriptions.add(
            this.session.createSignedUrl(`/sgpmx-invoice/${transaction.id}`).subscribe(signedUrl => {
                window.open(signedUrl, '_blank', 'noopener')
            })
        )
    }

    public hasInvoice(transaction: Transaction): boolean {
        return this.transactionService.hasInvoice(transaction)
    }

    public isTabActive(type: TransactionType | 'all' | 'others'): boolean {
        if (type === 'all') {
            return !this.queryParams.type || !this.queryParams.type.or || !this.queryParams.type.or.length
        }
        if (type === 'others') {
            return (
                this.queryParams.type?.or?.length > 1 ||
                (!!this.queryParams.type?.or && this.otherTypes.includes(this.queryParams.type?.or[0] as any))
            )
        }
        return isEqual(this.queryParams.type, { or: [type] })
    }

    public isBalanceAdjustment(transaction: Transaction): boolean {
        return (
            transaction.type === 'transfer' &&
            (transaction.baseWallet?.user === null || transaction.counterWallet?.user === null)
        )
    }

    public isSender(transaction: Transaction): boolean {
        return !this.userId || this.userId === transaction.baseWallet?.user?.id
    }

    public isReceiver(transaction: Transaction): boolean {
        return !this.userId || this.userId === transaction.counterWallet?.user?.id
    }

    private parseNgbdate(date: NgbDateStruct | null): string {
        return `${date?.year}-${date?.month}-${date?.day}`
    }
}
