import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { forkJoin, of } from 'rxjs'
import { flatMap } from 'rxjs/operators'
import { SessionService } from 'src/app/common/services/session.service'
import { getTradeGrandTotal, getTradeValue } from 'src/app/common/trades/trade.mixins'
import { environment } from 'src/environments/environment'
import { Trade } from '../../common/api-interfaces'
import { ListV2Component } from '../../common/list-v2.component'
import { AdminTradeModalComponent } from './admin-trade-modal.component'

@Component({
    selector: 'admin-trades',
    templateUrl: 'admin-trades.component.html',
})
export class AdminTradesComponent extends ListV2Component<Trade> implements OnInit {
    @Input()
    public apiUrl = '/admin/trades'
    @Input()
    public formComponent = AdminTradeModalComponent
    public symbols = ['AU/USD', 'AG/USD']
    public sides = ['BUY', 'SELL']
    public apiQuery = ['filter.symbol', 'filter.side', 'filter.createdAt']
    public hasReceipt: Record<string, boolean> = {}

    public getTradeGrandTotal = getTradeGrandTotal
    public getTradeValue = getTradeValue

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session)
    }

    public ngOnInit(): void {
        super.ngOnInit()
        if (environment.appName === 'sgpmx') {
            this.subscriptions.add(
                this.itemsStream
                    .pipe(
                        flatMap(items =>
                            forkJoin([
                                of(items),
                                this.http.post<Record<string, boolean>>(
                                    `/sgpmx/transactions/receipt-check`,
                                    items.map(trade =>
                                        trade.side === 'BUY' ? trade.counterTransaction?.id : trade.baseTransaction?.id
                                    )
                                ),
                            ])
                        )
                    )
                    .subscribe(([items, receiptCheck]) => {
                        for (const [key, value] of Object.entries(receiptCheck)) {
                            const trade = items.find(item =>
                                [item.baseTransaction?.id, item.counterTransaction?.id].includes(key)
                            )
                            this.hasReceipt[trade!.id] = value
                        }
                    })
            )
        }
    }

    public showForm(): void {
        const modal = this.ngbModal.open(AdminTradeModalComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
            size: 'lg',
        })
        modal.result.then(() => this.updateEvent.next())
    }

    public downloadTrades(): void {
        this.subscriptions.add(
            this.session
                .createSignedUrl(`/admin/trades/report`, this.generateListParams().toString())
                .subscribe(signedUrl => {
                    window.open(`${signedUrl}`, '_blank', 'noopener')
                })
        )
    }

    public openSgpmxInvoice(trade: Trade): void {
        const transaction = trade.side === 'BUY' ? trade.counterTransaction : trade.baseTransaction
        this.subscriptions.add(
            this.session.createSignedUrl(`/sgpmx-invoice/${transaction!.id}`).subscribe(signedUrl => {
                window.open(signedUrl, '_blank', 'noopener')
            })
        )
    }
}
