import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { IUser } from '../../../common/api-interfaces'

@Component({
    selector: 'user-fee-schedule-list',
    template: ` <fee-schedule [user]="user"></fee-schedule> `,
})
export class UserFeeScheduleListComponent implements OnInit {
    public user: IUser
    constructor(private route: ActivatedRoute) {}
    public ngOnInit(): void {
        this.user = { id: this.route.snapshot.parent!.paramMap.get('userId') } as IUser
        this.route.parent!.paramMap.subscribe(params => {
            this.user = { id: params.get('userId') } as IUser
        })
    }
}
