import { CardOrderStatus, CardType, ICardOrder, Transaction } from '../../api-interfaces'
import { User } from '../core/user.model'
import { Card } from './card.model'
import { Currency } from './currency.model'

export class CardOrder implements ICardOrder {
    public id: string
    public type: CardType
    public status: CardOrderStatus
    public currency: Currency
    public transaction: Transaction | null
    public shippingAddress: string
    public card: Card | null
    public user: User
    public trackingCode: string | null
    public isExpressDelivery: boolean
    public createdAt: string
    public updatedAt: string
    public isCreating: boolean

    constructor(data: ICardOrder) {
        Object.assign(this, data)
        this.currency = new Currency(data.currency)
        if (data.card) {
            this.card = new Card(data.card)
        }
        if (data.user) {
            this.user = new User(data.user)
        }
    }
}
