import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faCogs } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { values } from 'lodash'
import { filter, finalize, flatMap, shareReplay, tap } from 'rxjs/operators'
import { ConfirmationResult, ConfirmationService } from 'src/app/common/services/confirmation.service'
import { Alert, AlertStatus, AlertTriggerType, IUser } from '../../common/api-interfaces'
import { ListComponent } from '../../common/list.component'
import { SessionService } from '../../common/services/session.service'
import { ToastrService } from '../../common/services/toastr.service'
import { AlertViewComponent } from './alert-view.component'

enum Mode {
    ALL = 'all',
    PENDING = 'pending',
    PRE_RESOLVED = 'pre-resolved',
    ASSIGNED_TO_ME = 'assigned-to-me',
}

@Component({
    selector: 'alerts',
    templateUrl: 'alerts.component.html',
})
export class AlertsComponent extends ListComponent<Alert> implements OnInit {
    @Input()
    public user: IUser
    public mode: Mode = Mode.ALL
    public modes: Mode[] = values(Mode) as Mode[]
    public hoveredAlert: Alert
    public alertChangingStatus = new Set<string>()
    public alertAssigneeSelectOpen = new Set<string>()
    public statuses: AlertStatus[] = ['pre-resolved', 'pending', 'resolved']
    public types: AlertTriggerType[] = [
        'account-inter-account-transactions',
        'account-new-large-credits',
        'account-dormant',
        'account-short-lifespan',
        'account-country-hopping',
        'account-delayed-opening',
        'account-same-ip-address',
        'account-sanction-matches',
        'account-monitored',
        'transaction-volume',
        'transaction-reporting-avoidance',
        'transaction-large',
        'transaction-high-velocity',
        'transaction-high-risk-jurisdiction',
        'transaction-fintrac-report',
    ]
    public cddStatus: string[] = ['true', 'false']
    public adminUsersStream = this.http
        .get<IUser[]>('/users', {
            params: new HttpParams({
                fromObject: {
                    limit: '25',
                    role: 'admin',
                },
            }),
        })
        .pipe(shareReplay(1))
    public faCogs = faCogs

    protected apiUrl = '/alerts'
    protected apiQuery = ['status', '$trigger.type', 'cdd', 'createdAt']
    protected formComponent = AlertViewComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public ngOnInit(): void {
        this.apiUrl = this.user ? `/users/${this.user.id}/alerts` : '/alerts'
        super.ngOnInit()
    }

    public setAssignees(alert: Alert): void {
        this.http
            .patch<Alert>(
                `/alerts/${alert.id}`,
                [
                    {
                        op: 'replace',
                        path: '/assignees',
                        value: alert.assignees,
                    },
                ],
                {
                    headers: new HttpHeaders({ 'Content-Type': 'application/json-patch+json' }),
                }
            )
            .subscribe(response => {
                Object.assign(alert, response)
                this.updateEvent.next()
            })
    }

    public changeStatus(alert: Alert, status: AlertStatus): void {
        this.alertChangingStatus.add(alert.id)

        this.http
            .patch<Alert>(
                `/alerts/${alert.id}`,
                [
                    {
                        op: 'replace',
                        path: '/status',
                        value: status,
                    },
                ],
                {
                    headers: new HttpHeaders({ 'Content-Type': 'application/json-patch+json' }),
                }
            )
            .pipe(
                finalize(() => {
                    this.alertChangingStatus.delete(alert.id)
                })
            )
            .subscribe(response => {
                Object.assign(alert, response)
                this.toastr.success(`Alert ${status}`)
                this.updateEvent.next()
            })
    }

    public changeCddLabel(value: boolean, alert: Alert): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'warning',
                    title: this.translate.instant('common.change-cdd-flag'),
                    text: this.translate.instant('common.change-cdd-flag-desc'),
                    confirmText: this.translate.instant('common.confirm'),
                    confirmClass: 'primary',
                    showCancel: true,
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    tap(() => {
                        this.alertChangingStatus.add(alert.id)
                    }),
                    flatMap(() =>
                        this.http.patch<Alert>(
                            `/alerts/${alert.id}`,
                            [
                                {
                                    op: 'replace',
                                    path: '/cdd',
                                    value,
                                },
                            ],
                            {
                                headers: new HttpHeaders({ 'Content-Type': 'application/json-patch+json' }),
                            }
                        )
                    ),
                    finalize(() => {
                        this.alertChangingStatus.delete(alert.id)
                    })
                )
                .subscribe(response => {
                    Object.assign(alert, response)
                    this.toastr.success(`${this.translate.instant('alerts.alert-cdd-set-to')} ${value}`)
                })
        )
    }

    public onModeChanged(): void {
        this.select(1)
    }

    public compareSelectOption = (a: IUser, b: IUser) => a.id === b.id

    public formatType(type: string): string {
        return (type.includes('transaction-') ? type.replace('transaction-', '') : type.replace('account-', ''))
            .toUpperCase()
            .replace(/-/g, ' ')
    }
}
