import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { IUser, Referrer } from '../../common/api-interfaces'

@Component({
    selector: 'user-referrers-modal',
    templateUrl: 'referrers-modal.component.html',
})
export class ReferrersModalComponent implements OnInit {
    public referrers: IUser[]
    @Input()
    public item: Referrer
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()
    public apiUrl: string

    constructor(public activeModal: NgbActiveModal) {}

    public ngOnInit(): void {
        this.apiUrl = `/admin/referrers/${this.item.id}/referrals`
    }

    public goBack(): void {
        this.activeModal.dismiss()
        this.onSave.emit()
    }
}
