import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { AdminRedemptionModalComponent } from '../../admin-redemptions/admin-redemption-modal.component'

@Component({
    selector: 'user-redemptions',
    template: `<admin-redemptions [apiUrl]="apiUrl" [formComponent]="formComponent"></admin-redemptions>`,
})
export class UserRedemptionsComponent implements OnInit {
    public apiUrl: string
    public formComponent = AdminRedemptionModalComponent
    constructor(private route: ActivatedRoute) {}

    public ngOnInit(): void {
        this.apiUrl = `/admin/users/${this.route.snapshot.parent!.paramMap.get('userId')}/redemptions`
        this.route.parent!.paramMap.subscribe(params => {
            if (params.get('userId')) {
                this.apiUrl = `/admin/users/${params.get('userId')}/redemptions`
            }
        })
    }
}
