import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http'
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { faBullhorn, faExclamationTriangle, faEye, faIdBadge, faPaperPlane } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { NgSelectComponent } from '@ng-select/ng-select'
import { TranslateService } from '@ngx-translate/core'
import { Observable, of, Subscription } from 'rxjs'
import { flatMap, map, takeWhile, tap } from 'rxjs/operators'
import { Alert, IUser, Risk, RPCResult, UserStatus, Verification } from '../../../common/api-interfaces'
import { toStartCase } from '../../../common/common.mixin'
import { ConfirmationResult, ConfirmationService } from '../../../common/services/confirmation.service'
import { UserService } from '../../../common/services/core/user.service'
import { MessageService } from '../../../common/services/message.service'
import { SessionService } from '../../../common/services/session.service'
import { ToastrService } from '../../../common/services/toastr.service'
import { UserMessageModalComponent } from './user-message-modal/user-message-modal.component'
import { UserModelService } from './user-model.service'

@Component({
    selector: 'user',
    templateUrl: 'user.component.html',
    providers: [UserModelService],
})
export class UserComponent implements OnInit, OnDestroy {
    public user: IUser
    public alerts: Observable<Alert[]>
    public statuses: UserStatus[] = ['inactive', 'pending', 'active', 'closing', 'suspended', 'closed']
    public riskClasses: Risk[] = ['low', 'medium', 'high', 'unacceptable']
    public isApproved: boolean
    public faExclamationTriangle = faExclamationTriangle
    public faPaperPlane = faPaperPlane
    public faIdBadge = faIdBadge
    public faBullhorn = faBullhorn
    public faEye = faEye
    public toStartCase = toStartCase
    @ViewChild('statusSelect')
    public statusSelect: NgSelectComponent

    public verification: Verification | null = null
    private subscriptions = new Subscription()

    constructor(
        private model: UserModelService,
        private modal: NgbModal,
        private http: HttpClient,
        private toastr: ToastrService,
        private message: MessageService,
        private userService: UserService,
        private route: ActivatedRoute,
        private confirmation: ConfirmationService,
        public session: SessionService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.route.paramMap.subscribe(params => {
            const userId = params.get('userId')!
            this.model.fetchUser(userId)
            this.subscriptions.add(
                this.model.user.dataStream
                    .pipe(
                        tap(user => {
                            if (user) {
                                this.user = user
                                this.isApproved = !!user.approvedAt
                                this.getVerification(user)
                            }
                        })
                    )
                    .subscribe(() => undefined)
            )
            this.alerts = this.http.get<Alert[]>('/alerts', {
                params: new HttpParams().set('userId', userId),
            })
        })
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public getVerification(user: IUser): void {
        this.http.get<Verification[]>(`/users/${user.id}/verifications`).subscribe(verifications => {
            if (verifications && verifications.length) {
                this.verification = verifications[0]
            }
        })
    }

    public goToBasisID(): void {
        window.open(`https://crm.basisid.com/clients/edit/${this.verification?.hash}`, '_blank')
    }

    public updateStatus(status: UserStatus): void {
        const template: { [key: string]: string } = {
            closing: 'Exit requested by the client',
            closed: 'Account closed',
            suspended: 'Exit forced by us',
        }
        ;(['closing', 'closed', 'suspended'].includes(status)
            ? this.message.prompt(this.user, template[status]).pipe(
                  takeWhile(result => {
                      if (result === ConfirmationResult.DISMISSED) {
                          this.statusSelect.itemsList.select(
                              this.statusSelect.itemsList.items.find(item => item.value === this.user.status)!
                          )
                          return false
                      }
                      return true
                  }),
                  map(() => undefined)
              )
            : of(undefined)
        )
            .pipe(flatMap(() => this.userService.changeStatus(this.user.id, status)))
            .subscribe(user => {
                this.model.user.set(user)
                this.toastr.success(this.translate.instant('common.status-updated'))
            })
    }

    public updateRiskRating(): void {
        this.subscriptions.add(
            this.userService.changeRiskRating(this.user.id, this.user.riskRating).subscribe(user => {
                this.model.user.set(user)
                this.toastr.success(this.translate.instant('user.risk-rating-updated'))
            })
        )
    }

    public openMessageModal(): void {
        const modal = this.modal.open(UserMessageModalComponent, {
            windowClass: 'modal-primary',
            size: 'lg',
            backdrop: 'static',
        })
        const messageModal = modal.componentInstance as UserMessageModalComponent
        messageModal.user = this.user
    }

    public updateApprovedAt(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'info',
                    text: this.translate.instant('common.are-you-sure-want-to-update-kyc'),
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'info',
                })
                .pipe(
                    takeWhile(result => {
                        if (result !== ConfirmationResult.CONFIRMED) {
                            throw Error(this.translate.instant('common.confirmation-dismissed'))
                        }
                        return true
                    }),
                    flatMap(() =>
                        this.http.post<RPCResult<IUser>>(
                            `/users/${this.user.id}`,
                            {
                                jsonrpc: '2.0',
                                id: Math.floor(Math.random() * 10000),
                                method: 'approve',
                                params: {
                                    value: this.isApproved,
                                },
                            },
                            {
                                headers: new HttpHeaders({
                                    'Content-Type': 'application/json-rpc',
                                }),
                            }
                        )
                    )
                )
                .subscribe(
                    response => {
                        this.model.user.set(response.result)
                        this.toastr.success(this.translate.instant('user.verification-status-updated'))
                    },
                    () => {
                        // Revert to old value if cancelled
                        this.isApproved = !this.isApproved
                    }
                )
        )
    }

    public switchVerification(value: boolean): void {
        this.verification!.completed = !value
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'info',
                    text: this.translate.instant('common.are-you-sure-want-to-change-wizard'),
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'info',
                })
                .pipe(
                    takeWhile(result => {
                        if (result !== ConfirmationResult.CONFIRMED) {
                            this.verification!.completed = value
                            return false
                        }
                        return true
                    }),
                    flatMap(() =>
                        this.http.put<Verification>(`/users/${this.user.id}/set-verification`, {
                            completed: !value,
                        })
                    )
                )
                .subscribe(verification => {
                    this.verification = verification
                    this.toastr.success(this.translate.instant('user.verification-status-updated'))
                })
        )
    }
}
