import { HttpClient } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { faQuestionCircle } from '@fortawesome/pro-light-svg-icons'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { Subscription } from 'rxjs'
import { filter, finalize, flatMap } from 'rxjs/operators'
import { IFee } from '../../../common/api-interfaces'
import { Fee } from '../../../common/models/accounting/fee.model'
import { ConfirmationResult, ConfirmationService } from '../../../common/services/confirmation.service'
import { ToastrService } from '../../../common/services/toastr.service'

@Component({
    selector: 'fee-schedule-form',
    templateUrl: 'fee-schedule-form.component.html',
})
export class FeeScheduleFormComponent implements OnInit, OnDestroy {
    @Input()
    public fee: Fee
    @Output()
    public readonly onSubmit = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()
    @ViewChild('form')
    public form: NgForm

    public relativeFee: string | null
    public fixedFee: string | null
    public maxFee: string | null
    public minFee: string | null
    public minValue: string | null
    public isSaving = false
    public isDeleting = false
    public faQuestionCircle = faQuestionCircle

    private subscriptions = new Subscription()

    constructor(
        public activeModal: NgbActiveModal,
        private http: HttpClient,
        private toastr: ToastrService,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        // Format relative fee for easier input, transforms 0.1 to 1
        this.relativeFee = this.fee.relative ? new BigNumber(this.fee.relative).times(100).toString() : null
        this.fixedFee = this.fee.fixed ? new BigNumber(this.fee.fixed).toString() : null
        this.maxFee = this.fee.max ? new BigNumber(this.fee.max).toString() : null
        this.minFee = this.fee.min ? new BigNumber(this.fee.min).toString() : null
        this.minValue = this.fee.minValue ? new BigNumber(this.fee.minValue).toString() : null
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (this.form.invalid) {
            return
        }
        if (!this.fee.fixed && !this.relativeFee) {
            this.toastr.warning(this.translate.instant('fee-schedule-form.invalid-fee'))
            return
        }
        this.isSaving = true
        const body = {
            ...this.fee,
            relative: this.relativeFee ? new BigNumber(this.relativeFee).div(100).toString() : null,
            max: !!this.relativeFee && !!this.fee.max && new BigNumber(this.fee.max).gt(0) ? this.fee.max : null,
            min: !!this.relativeFee && !!this.fee.min && new BigNumber(this.fee.min).gt(0) ? this.fee.min : null,
            fixed: this.fixedFee ? new BigNumber(this.fixedFee).toString() : null,
            minValue: this.minValue ? new BigNumber(this.minValue).toString() : null,
        }
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'warning',
                    text: this.translate.instant('common.this-will-require-all'),
                    confirmText: this.translate.instant('common.understood'),
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() =>
                        this.fee && this.fee.id
                            ? this.http.put<IFee>(`/admin/fees/${this.fee && this.fee.id}`, body)
                            : this.http.post<IFee>(`/admin/fees`, body)
                    ),
                    finalize(() => (this.isSaving = false))
                )
                .subscribe(() => {
                    this.toastr.success(
                        this.fee && this.fee.id
                            ? `${this.translate.instant('fee-schedule-form.fee-updated')}`
                            : `${this.translate.instant('fee-schedule-form.fee-created')}`
                    )
                    this.onSubmit.emit()
                })
        )
    }

    public delete(): void {
        if (!this.fee) {
            return
        }
        this.isDeleting = true
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'warning',
                    text: this.translate.instant('common.this-will-require-all'),
                    confirmText: this.translate.instant('common.understood'),
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.http.delete<IFee>(`/admin/fees/${this.fee && this.fee.id}`)),
                    finalize(() => {
                        this.isDeleting = false
                    })
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('fee-schedule-form.fee-deleted'))
                    this.onDelete.emit()
                })
        )
    }
}
