import { HttpClient } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { faCalendar } from '@fortawesome/pro-solid-svg-icons'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import addHours from 'date-fns/addHours'
import formatDistanceToNow from 'date-fns/formatDistanceToNow'
import { cloneDeep } from 'lodash'
import { Observable, of, Subject, Subscription } from 'rxjs'
import { finalize, flatMap, switchMap, tap } from 'rxjs/operators'
import { Paginated } from 'src/app/common/api-interfaces'
import { Conversion } from 'src/app/common/models/accounting/conversion.model'
import { Currency } from 'src/app/common/models/accounting/currency.model'
import { Transaction } from 'src/app/common/models/accounting/transaction.model'
import { SecurityCheckService } from 'src/app/common/security-check/security-check.service'
import { ConversionService } from 'src/app/common/services/accounting/conversion.service'
import { ToastrService } from 'src/app/common/services/toastr.service'

@Component({
    selector: 'conversion-offer-form',
    templateUrl: 'conversion-offer-form.component.html',
})
export class ConversionOfferFormComponent implements OnInit, OnDestroy {
    @Input()
    public item: Conversion
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()

    @ViewChild('conversionOfferForm')
    public conversionOfferForm: NgForm
    public copy: Partial<Conversion> = {}

    public currencies: Currency[]

    public isSaving = false

    public faCalendar = faCalendar

    public expiryHours: string
    public midMarketRate: string
    public directConversion = false
    public isUsingBaseCurrencyRate = true

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        private securityCheckService: SecurityCheckService,
        private conversionService: ConversionService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() => this.fetchCurrencies()),
                    flatMap(() =>
                        this.item && this.item.id ? this.conversionService.fetchById(this.item.id) : of(undefined)
                    )
                )
                .subscribe(response => {
                    if (response) {
                        Object.assign(this.item, response)
                    }
                    this.copy = cloneDeep(this.item)
                    this.copy.baseTransaction!.amount = new BigNumber(this.copy.baseTransaction!.amount).toFixed(
                        this.copy.baseTransaction!.currency!.decimalPlaces,
                        1
                    )
                    if (!this.copy.counterTransaction) {
                        this.copy.counterTransaction = {} as Transaction
                    }
                    this.calculateCurrentExchangeRate(this.currencies, this.item)
                    this.copy.exchangeRate = this.midMarketRate
                    this.exchangeRateUpdate(this.copy.exchangeRate)
                })
        )

        this.fetchEvent.next()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public fetchCurrencies(): Observable<Paginated<Currency>> {
        return this.http.get<Paginated<Currency>>('/currencies').pipe(
            tap(currencies => {
                this.currencies = currencies.data
            })
        )
    }

    public submit(): void {
        if (this.conversionOfferForm.invalid) {
            return
        }
        const exchangeRate = this.isUsingBaseCurrencyRate
            ? new BigNumber(this.copy.counterTransaction!.amount).div(this.copy.baseTransaction!.amount).toFixed(8, 1)
            : this.copy.exchangeRate!
        this.subscriptions.add(
            this.securityCheckService
                .getScopePermission('banking')
                .pipe(
                    flatMap(() => {
                        this.isSaving = true
                        return this.directConversion
                            ? this.conversionService.directlyConvert(this.item.id, {
                                  exchangeRate,
                              })
                            : this.conversionService.makeOffer(this.item.id, {
                                  exchangeRate,
                                  expiresAt: this.copy.expiresAt!,
                              })
                    }),
                    tap(conversion => {
                        this.copy = conversion
                    }),
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(() => {
                    this.fetchEvent.next()
                    this.toastr.success(
                        this.directConversion
                            ? `${this.translate.instant('conversion-offer-form.conversion-offer-successful')}`
                            : `${this.translate.instant('conversion-offer-form.conversion-offer-created')}`
                    )
                    this.reset()
                    this.onSave.emit()
                })
        )
    }

    public expiresInChange(): void {
        this.copy.expiresAt = addHours(new Date(), Number(this.expiryHours)).toUTCString()
    }

    public convertToISODate(date?: string): string | undefined {
        return (date && `${date.slice(4)}-${date.slice(2, 4)}-${date.slice(0, 2)}`) || undefined
    }

    public convertToWords(date?: string): string {
        if (date && date.length === 10) {
            return formatDistanceToNow(new Date(date), { addSuffix: true })
        }
        return ''
    }

    public exchangeRateUpdate(value: string) {
        if (this.isUsingBaseCurrencyRate) {
            this.copy!.counterTransaction!.amount = new BigNumber(this.item.baseTransaction.amount)
                .div(value)
                .toFixed(this.copy.currency!.decimalPlaces, 1)
        } else {
            this.copy!.counterTransaction!.amount = new BigNumber(this.item.baseTransaction.amount)
                .times(value)
                .toFixed(this.copy.baseTransaction!.currency!.decimalPlaces, 1)
        }
        this.calculateCurrentExchangeRate(this.currencies, this.item)
    }

    public switchCurrency(): void {
        this.calculateCurrentExchangeRate(this.currencies, this.item)
        this.copy.exchangeRate = this.midMarketRate
        this.exchangeRateUpdate(this.copy.exchangeRate)
    }

    private reset(): void {
        this.conversionOfferForm.reset()
        this.conversionOfferForm.resetForm()
    }

    private calculateCurrentExchangeRate(currencies: Currency[], conversion: Conversion): void {
        const baseCurrency = currencies.find(currency => currency.code === conversion.baseTransaction.currency.code)
        const counterCurrency = currencies.find(currency => currency.code === conversion.currency.code)
        if (baseCurrency && counterCurrency) {
            if (this.isUsingBaseCurrencyRate) {
                this.midMarketRate = Currency.calculateExchangeRate(counterCurrency, baseCurrency).toFixed(6, 1)
            } else {
                this.midMarketRate = Currency.calculateExchangeRate(baseCurrency, counterCurrency).toFixed(6, 1)
            }
        } else {
            this.midMarketRate = ''
        }
    }
}
