import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'

@Component({
    selector: 'user-account-bills',
    template: ` <account-bills [apiUrl]="apiUrl"></account-bills> `,
})
export class UserAccountBillsComponent implements OnInit {
    public apiUrl: string
    constructor(private route: ActivatedRoute) {}
    public ngOnInit(): void {
        this.apiUrl = `/users/${this.route.snapshot.parent!.paramMap.get('userId')}/account-bills`
        this.route.parent!.paramMap.subscribe(params => {
            this.apiUrl = `/users/${params.get('userId')}/account-bills`
        })
    }
}
