import { NgModule } from '@angular/core'
import { RouterModule, Routes } from '@angular/router'
import { ActivitiesComponent } from '../common/activities/activities.component'
import { AllocationsComponent } from '../common/allocations/allocations.component'
import { InvoicesComponent } from '../common/invoices/invoices.component'
import { LoggedInAuthGuard } from '../common/logged-in.auth-guard'
import { MailComponent } from '../common/mails/mail/mail.component'
import { AdminApiTokensComponent } from './admin-api-tokens/admin-api-tokens.component'
import { AdminConversionsComponent } from './admin-conversions/admin-conversions.component'
import { AdminDashboardComponent } from './admin-dashboard/admin-dashboard.component'
import { AdminOverviewComponent } from './admin-dashboard/admin-overview/admin-overview.component'
import { AdminRealtimeComponent } from './admin-realtime/admin-realtime.component'
import { AdminRedemptionsComponent } from './admin-redemptions/admin-redemptions.component'
import { AdminSettingsComponent } from './admin-settings/admin-settings.component'
import { AdminSpotTradingComponent } from './admin-spot-trading/admin-spot-trading.component'
import { AdminTermsComponent } from './admin-terms/admin-terms.component'
import { AdminTradesComponent } from './admin-trades/admin-trades.component'
import { AdminAuthGuard } from './admin.auth-guard'
import { AdminComponent } from './admin.component'
import { AlertTriggersComponent } from './alert-triggers/alert-triggers.component'
import { AlertsComponent } from './alerts/alerts.component'
import { CardOrdersComponent } from './card-orders/card-orders.component'
import { CardProgramsComponent } from './card-programs/card-programs.component'
import { CardsComponent } from './cards/cards.component'
import { CurrenciesComponent } from './currencies/currencies.component'
import { AdminDepositInstructionsComponent } from './deposit-instructions/admin-deposit-instructions.component'
import { FeeScheduleComponent } from './fee-schedule/fee-schedule.component'
import { FundsUnderManagementComponent } from './funds-under-management/funds-under-management.component'
import { ProductsComponent } from './products/products.component'
import { ReferrersComponent } from './referrers/referrers.component'
import { RoleAuthGuard } from './role.auth-guard'
import { TasksComponent } from './tasks/tasks.component'
import { TransactionsComponent } from './transactions/transactions.component'
import { UserAccountComponent } from './users/user/account/user-account.component'
import { CustomersComponent } from './users/user/customers/customers.component'
import { UserOverviewComponent } from './users/user/overview/user-overview.component'
import { UserAccountBillsComponent } from './users/user/user-account-bills.component'
import { UserActivitiesComponent } from './users/user/user-activities.component'
import { UserAlertsComponent } from './users/user/user-alerts.component'
import { AdminUserCardOrdersComponent } from './users/user/user-card-orders.component'
import { UserCardsComponent } from './users/user/user-cards.component'
import { UserDepositInstructionsComponent } from './users/user/user-deposit-instructions.component'
import { UserFeeScheduleListComponent } from './users/user/user-fees.component'
import { UserMailsComponent } from './users/user/user-mails.component'
import { UserRedemptionsComponent } from './users/user/user-redemptions.component'
import { UserReferrersComponent } from './users/user/user-referrers.component'
import { UserSpotTradingComponent } from './users/user/user-spot-trading.component'
import { UserTradesComponent } from './users/user/user-trades.component'
import { UserTransactionsComponent } from './users/user/user-transactions.component'
import { UserComponent } from './users/user/user.component'
import { UsersComponent } from './users/users.component'

const routes: Routes = [
    {
        path: '',
        component: AdminComponent,
        canActivate: [LoggedInAuthGuard, AdminAuthGuard],
        children: [
            {
                path: '',
                redirectTo: 'dashboard',
            },
            {
                path: 'dashboard',
                component: AdminDashboardComponent,
                children: [
                    {
                        path: '',
                        component: AdminOverviewComponent,
                        children: [
                            {
                                path: '',
                                component: ActivitiesComponent,
                            },
                        ],
                    },
                ],
            },
            {
                path: 'realtime',
                component: AdminRealtimeComponent,
            },
            {
                path: 'alerts',
                canActivate: [RoleAuthGuard],
                data: { roles: ['admin'] },
                component: AlertsComponent,
            },
            {
                path: 'accounts',
                children: [
                    { path: '', redirectTo: 'users', pathMatch: 'full' },
                    {
                        path: 'users',
                        component: UsersComponent,
                    },
                    {
                        path: 'users/:userId',
                        component: UserComponent,
                        children: [
                            { path: '', redirectTo: 'overview', pathMatch: 'full' },
                            {
                                path: 'overview',
                                component: UserOverviewComponent,
                            },
                            {
                                path: 'settings',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin', 'licensee'] },
                                component: UserAccountComponent,
                            },
                            {
                                path: 'fees',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin', 'licensee'] },
                                component: UserFeeScheduleListComponent,
                            },
                            {
                                path: 'transactions',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin', 'licensee'] },
                                component: UserTransactionsComponent,
                            },
                            {
                                path: 'activities',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin', 'licensee'] },
                                component: UserActivitiesComponent,
                            },
                            {
                                path: 'alerts',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin'] },
                                component: UserAlertsComponent,
                            },
                            {
                                path: 'referrers',
                                component: UserReferrersComponent,
                            },
                            {
                                path: 'redemptions',
                                component: UserRedemptionsComponent,
                            },
                            {
                                path: 'trades',
                                component: UserTradesComponent,
                            },
                            {
                                path: 'spot-trades',
                                component: UserSpotTradingComponent,
                            },
                            {
                                path: 'cards',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin', 'licensee'] },
                                component: UserCardsComponent,
                            },
                            {
                                path: 'card-orders',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin', 'licensee'] },
                                component: AdminUserCardOrdersComponent,
                            },
                            {
                                path: 'mails',
                                children: [
                                    {
                                        path: '',
                                        component: UserMailsComponent,
                                    },
                                    {
                                        path: ':mailId',
                                        component: MailComponent,
                                    },
                                ],
                            },
                            {
                                path: 'account-bills',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin', 'licensee'] },
                                component: UserAccountBillsComponent,
                            },
                            {
                                path: 'deposit-instructions',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin', 'licensee'] },
                                component: UserDepositInstructionsComponent,
                            },
                            {
                                path: 'customers',
                                canActivate: [RoleAuthGuard],
                                data: { roles: ['admin'] },
                                component: CustomersComponent,
                            },
                        ],
                    },
                    {
                        path: 'funds-under-management',
                        canActivate: [RoleAuthGuard],
                        data: { roles: ['admin', 'licensee'] },
                        component: FundsUnderManagementComponent,
                    },
                ],
            },
            {
                path: 'transactions',
                canActivate: [RoleAuthGuard],
                data: { roles: ['admin', 'licensee'] },
                component: TransactionsComponent,
            },
            {
                path: 'cards',
                canActivate: [RoleAuthGuard],
                data: { roles: ['admin', 'licensee'] },
                children: [
                    { path: '', redirectTo: 'orders', pathMatch: 'full' },
                    {
                        path: 'orders',
                        component: CardOrdersComponent,
                    },
                    {
                        path: 'cards',
                        component: CardsComponent,
                    },
                    {
                        path: 'programs',
                        component: CardProgramsComponent,
                    },
                ],
            },
            {
                path: 'investments',
                canActivate: [RoleAuthGuard],
                data: { roles: ['admin', 'licensee'] },
                children: [
                    { path: '', redirectTo: 'products', pathMatch: 'full' },
                    {
                        path: 'products',
                        component: ProductsComponent,
                    },
                    {
                        path: 'allocations',
                        component: AllocationsComponent,
                    },
                ],
            },
            {
                path: 'services',
                canActivate: [RoleAuthGuard],
                data: { roles: ['admin', 'licensee'] },
                children: [
                    {
                        path: 'invoices',
                        component: InvoicesComponent,
                    },
                    {
                        path: 'conversions',
                        component: AdminConversionsComponent,
                    },
                    {
                        path: 'trades',
                        component: AdminTradesComponent,
                    },
                    {
                        path: 'spot-trading',
                        component: AdminSpotTradingComponent,
                    },
                    {
                        path: 'redemptions',
                        component: AdminRedemptionsComponent,
                    },
                ],
            },
            {
                path: 'platform',
                canActivate: [RoleAuthGuard],
                data: { roles: ['admin', 'licensee'] },
                children: [
                    { path: '', redirectTo: 'currencies', pathMatch: 'full' },
                    {
                        path: 'currencies',
                        component: CurrenciesComponent,
                    },
                    {
                        path: 'deposit-instructions',
                        component: AdminDepositInstructionsComponent,
                    },
                    {
                        path: 'fee-schedule',
                        component: FeeScheduleComponent,
                    },
                    {
                        path: 'referrers',
                        component: ReferrersComponent,
                    },
                    {
                        path: 'triggers',
                        canActivate: [RoleAuthGuard],
                        data: { roles: ['admin'] },
                        component: AlertTriggersComponent,
                    },
                ],
            },
            {
                path: 'settings',
                canActivate: [RoleAuthGuard],
                data: { roles: ['admin'] },
                children: [
                    { path: '', redirectTo: 'api-tokens', pathMatch: 'full' },
                    {
                        path: 'api-tokens',
                        component: AdminApiTokensComponent,
                    },
                    {
                        path: 'other',
                        component: AdminSettingsComponent,
                    },
                    {
                        path: 'terms',
                        component: AdminTermsComponent,
                    },
                ],
            },
            {
                path: 'monitoring',
                canActivate: [RoleAuthGuard],
                data: { roles: ['admin'] },
                children: [
                    { path: '', redirectTo: 'tasks', pathMatch: 'full' },
                    {
                        path: 'tasks',
                        component: TasksComponent,
                    },
                ],
            },
        ],
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
    providers: [AdminAuthGuard, RoleAuthGuard],
})
export class AdminRoutingModule {}
