import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faEllipsisV } from '@fortawesome/pro-regular-svg-icons'
import { faCheck, faTimes } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { Subject } from 'rxjs'
import { filter, flatMap, switchMap } from 'rxjs/operators'
import { IUser } from 'src/app/common/api-interfaces'
import { ListComponent } from 'src/app/common/list.component'
import { ConfirmationResult, ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { ToastrService } from 'src/app/common/services/toastr.service'

@Component({
    selector: 'customers',
    templateUrl: 'customers.component.html',
})
export class CustomersComponent extends ListComponent<IUser> implements OnInit {
    public userId: string
    public statistics = {
        customerCount: 0,
        kycVerifiedCustomers: 0,
        withCardOrder: 0,
    }
    public hasCardOrder: Record<string, boolean> = {}

    public faEllipsisV = faEllipsisV
    public faCheck = faCheck
    public faTimes = faTimes

    private fetchStatisticsEvent = new Subject<void>()

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchStatisticsEvent
                .pipe(
                    switchMap(() =>
                        this.http.get<{ customerCount: number; kycVerifiedCustomers: number; withCardOrder: number }>(
                            `/admin/associations/${this.userId}/statistic`
                        )
                    )
                )
                .subscribe(statistics => {
                    this.statistics = statistics
                })
        )
        this.route.parent!.paramMap.subscribe(params => {
            this.userId = params.get('userId')!
            this.apiUrl = `/users/${this.userId}/associates`
            super.ngOnInit()
        })
        this.subscriptions.add(
            this.itemsStream
                .pipe(
                    flatMap(items =>
                        this.http.post<Record<string, boolean>>(
                            `/admin/card-orders/status-check`,
                            items.map(i => i.id)
                        )
                    )
                )
                .subscribe(hasCardOrder => {
                    this.hasCardOrder = hasCardOrder
                })
        )
    }

    public async orderCards(): Promise<void> {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'warning',
                    title: this.translate.instant('customers.order-cards'),
                    text: `${this.translate.instant('common.this-will-create-card-orders')} <b>${this.translate.instant(
                        'common.this-cannot-be-reverted'
                    )}.</b>`,
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'warning',
                    cancelText: this.translate.instant('common.cancel'),
                    cancelClass: 'primary',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.http.post(`/admin/users/${this.userId}/associates/card-orders`, {}))
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('common.card-ordered-successfully'))
                })
        )
    }
}
