import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { cloneDeep, orderBy, pick } from 'lodash'
import { forkJoin, of, Subject, Subscription } from 'rxjs'
import { filter, finalize, flatMap, switchMap } from 'rxjs/operators'
import { CardType, ICardProgram } from 'src/app/common/api-interfaces'
import { buildFileUrl } from 'src/app/common/file'
import { ConfirmationResult, ConfirmationService } from 'src/app/common/services/confirmation.service'
import { ToastrService } from 'src/app/common/services/toastr.service'

interface NiumPrefundBalance {
    accountType: 'CLIENT_POOL' | 'WALLET_POOL'
    balance: number
    currency: string
    isDefault: 'true' | 'false'
    createdAt: string
    updatedAt: string
}

@Component({
    selector: 'card-program-form',
    templateUrl: 'card-program-form.component.html',
})
export class CardProgramFormComponent implements OnInit, OnDestroy {
    @Input()
    public item: ICardProgram
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()

    public copy: Partial<ICardProgram> = {}

    public clientPoolBalances: NiumPrefundBalance[] = []
    public walletPoolBalances: NiumPrefundBalance[] = []
    public digitalCardArtWork: File | null
    public digitalCardArtWorkUrl: string | null
    public errorMessage: string | null

    public type: CardType[] = ['virtual', 'physical']

    public isSaving = false
    public isDeleting = false
    public isMakingDefault = false
    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        public activeModal: NgbActiveModal,
        private http: HttpClient,
        private toastr: ToastrService,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() =>
                        this.item && this.item.id
                            ? this.http.get<ICardProgram>(`/admin/card-programs/${this.item.id}`)
                            : of(undefined)
                    )
                )
                .subscribe(response => {
                    if (response) {
                        Object.assign(this.item, response)
                        this.item.pricePhysical = this.item.pricePhysical
                            ? parseFloat(this.item.pricePhysical) + ''
                            : null
                        this.item.priceVirtual = this.item.priceVirtual ? parseFloat(this.item.priceVirtual) + '' : null
                        this.item.maxCardBalance = this.item.maxCardBalance
                            ? parseFloat(this.item.maxCardBalance) + ''
                            : null
                        this.item.expressDeliveryPrice = this.item.expressDeliveryPrice
                            ? parseFloat(this.item.expressDeliveryPrice) + ''
                            : null

                        this.http
                            .get<NiumPrefundBalance[]>(`/admin/card-programs/${this.item.id}/get-prefund-balance`)
                            .subscribe(balances => {
                                this.clientPoolBalances = orderBy(
                                    balances.filter(balance => balance.accountType === 'CLIENT_POOL'),
                                    ['currency'],
                                    ['asc']
                                )
                                this.walletPoolBalances = orderBy(
                                    balances.filter(balance => balance.accountType === 'WALLET_POOL'),
                                    ['currency'],
                                    ['asc']
                                )
                            })

                        if (this.item.digitalCardArtwork) {
                            this.digitalCardArtWorkUrl = buildFileUrl(this.item.digitalCardArtwork)
                        }
                    }
                    this.copy = cloneDeep(this.item)
                })
        )
        this.fetchEvent.next()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        this.isSaving = true
        const body = pick(this.copy, [
            'name',
            'niumClientId',
            'apiKey',
            'pricePhysical',
            'priceVirtual',
            'physicalPlasticId',
            'virtualPlasticId',
            'maxCardBalance',
            'hasVirtualEnabled',
            'hasPhysicalEnabled',
            'allowMultiplePhysical',
            'allowMultipleVirtual',
            'expressDeliveryPrice',
            'freeViaApi',
        ])
        body.priceVirtual = body.priceVirtual ? body.priceVirtual + '' : null
        body.pricePhysical = body.pricePhysical ? body.pricePhysical + '' : null
        ;(!this.item.id
            ? this.http.post<ICardProgram>('/admin/card-programs', body)
            : this.http.put<ICardProgram>(`/admin/card-programs/${this.item.id}`, body)
        )
            .pipe(
                switchMap(cardProgram =>
                    forkJoin([
                        this.digitalCardArtWork
                            ? this.http
                                  .post(
                                      `/admin/card-programs/${cardProgram.id}/digital-card-artwork`,
                                      this.digitalCardArtWork,
                                      {
                                          headers: new HttpHeaders({
                                              'X-File-Name': encodeURIComponent(this.digitalCardArtWork.name),
                                              'Content-Type': this.digitalCardArtWork.type,
                                          }),
                                      }
                                  )
                                  .pipe(
                                      finalize(() => {
                                          this.isSaving = false
                                      })
                                  )
                            : of(undefined),
                    ])
                ),
                finalize(() => {
                    this.isSaving = false
                })
            )
            .subscribe(() => {
                this.toastr.success(
                    `${this.translate.instant('common.card-program')} ${
                        this.item.id
                            ? `${this.translate.instant('common.updated')}`
                            : `${this.translate.instant('common.created')}`
                    }`
                )
                this.onSave.emit()
            })
    }

    public uploadCardArtwork(artwork: File): void {
        const reader = new FileReader()
        reader.readAsDataURL(artwork)
        reader.onload = data => {
            const image = new Image()
            image.src = data.target?.result as string
            image.onload = () => {
                this.digitalCardArtWork = artwork
                this.digitalCardArtWorkUrl = data.target?.result as string
                this.errorMessage = null
            }
        }
    }

    public delete(): void {
        this.isDeleting = true
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    title: 'Delete card program',
                    text: `${this.translate.instant(
                        'common.are-you-sure-want-to-delete-this-card-program'
                    )} <b>${this.translate.instant('common.it-cannot-be-undone')}<b>`,
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'danger',
                    showCancel: false,
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.http.delete(`/admin/card-programs/${this.item.id}`)),
                    finalize(() => {
                        this.isDeleting = false
                    })
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('card-program-form.card-program-deleted'))
                    this.onDelete.emit()
                })
        )
    }

    public makeDefault(): void {
        this.isMakingDefault = true
        this.http
            .post<ICardProgram>(`/admin/card-programs/${this.item.id}/make-default`, {})
            .pipe(
                finalize(() => {
                    this.isMakingDefault = false
                })
            )
            .subscribe(cardProgram => {
                this.toastr.success(`${cardProgram.name} ${this.translate.instant('card-program-form.set-as-default')}`)
                this.onSave.emit()
            })
    }

    public toggleVirtualType(checked?: boolean) {
        this.copy.hasVirtualEnabled = checked
        if (!checked) {
            this.copy.virtualPlasticId = null
            this.copy.priceVirtual = null
        }
    }

    public togglePhysicalType(checked?: boolean) {
        this.copy.hasPhysicalEnabled = checked
        if (!checked) {
            this.copy.physicalPlasticId = null
            this.copy.pricePhysical = null
        }
    }
}
