import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { values } from 'lodash'
import { IBeneficiary } from '../../common/api-interfaces'
import { BeneficiaryFormComponent } from '../../common/beneficiary-form/beneficiary-form.component'
import { ListComponent, Scope as DefaultScope } from '../../common/list.component'
import { ConfirmationService } from '../services/confirmation.service'
import { SessionService } from '../services/session.service'

type Scope = DefaultScope | 'deposit-instruction'
const Scope = {
    ...DefaultScope,
    DEPOSIT_INSTRUCTION: 'deposit-instruction',
}
@Component({
    selector: 'beneficiaries',
    templateUrl: 'beneficiaries.component.html',
})
export class BeneficiariesComponent extends ListComponent<IBeneficiary> {
    @Input()
    public scope: Scope = Scope.DEFAULT
    public scopes: Scope[] = values(Scope) as Scope[]
    @Input()
    public apiUrl = '/beneficiaries'
    public formComponent = BeneficiaryFormComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }
}
