import { Injectable } from '@angular/core'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { from, Observable, of } from 'rxjs'
import { catchError, flatMap, map } from 'rxjs/operators'
import { UserMessageModalComponent } from '../../admin/users/user/user-message-modal/user-message-modal.component'
import { IUser } from '../api-interfaces'
import { ConfirmationResult, ConfirmationService } from './confirmation.service'

@Injectable({
    providedIn: 'root',
})
export class MessageService {
    constructor(
        private modal: NgbModal,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public prompt(user: IUser, templateName: string): Observable<ConfirmationResult> {
        return this.confirmation
            .show({
                type: 'info',
                title: this.translate.instant('common.send-message') + '?',
                text: `Click 'Compose' if you want to send a message before updating account status.`,
                confirmText: 'Compose',
                confirmClass: 'info',
                cancelText: 'Just update',
                cancelClass: 'success',
            })
            .pipe(
                flatMap(result => {
                    if (result === ConfirmationResult.CONFIRMED) {
                        const modal = this.modal.open(UserMessageModalComponent, {
                            windowClass: 'modal-primary',
                            size: 'lg',
                            backdrop: 'static',
                        })
                        const messageModal = modal.componentInstance as UserMessageModalComponent
                        messageModal.user = user
                        messageModal.selectedTemplate = messageModal.templates.find(t => t.name === templateName)!
                        return from(modal.result).pipe(
                            map(() => ConfirmationResult.CONFIRMED),
                            catchError(() => of(ConfirmationResult.DISMISSED))
                        )
                    }
                    return of(result)
                })
            )
    }
}
