import { HttpClient, HttpHeaders } from '@angular/common/http'
import { ChangeDetectorRef, Component, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faMinus } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { forkJoin, Subject } from 'rxjs'
import { debounceTime, switchMap } from 'rxjs/operators'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { ICurrency, Product, Transaction as ITransaction, Wallet } from '../../common/api-interfaces'
import { toStartCase } from '../../common/common.mixin'
import { ListComponent } from '../../common/list.component'
import { Transaction } from '../../common/models/accounting/transaction.model'
import { SessionService } from '../../common/services/session.service'
import { ToastrService } from '../../common/services/toastr.service'
import { TransactionFormComponent } from '../transactions/transaction-form.component'
import { ProductFormComponent } from './product-form.component'

@Component({
    selector: 'products',
    templateUrl: 'products.component.html',
})
export class ProductsComponent extends ListComponent<Product> implements OnInit {
    public faMinus = faMinus
    public toStartCase = toStartCase
    public supplyStats: { sum: string; currency: ICurrency }[]
    public rootWallets: Wallet[]
    protected apiUrl = '/admin/products'
    protected formComponent = ProductFormComponent
    private fetchEvent = new Subject<void>()

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public ngOnInit(): void {
        super.ngOnInit()
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    debounceTime(600),
                    switchMap(() =>
                        forkJoin(
                            this.http.get<{ sum: string; currency: ICurrency }[]>(`/statistics/supply`),
                            this.http.get<Wallet[]>(`/admin/wallets`)
                        )
                    )
                )
                .subscribe(([supplyStats, rootWallets]) => {
                    this.supplyStats = supplyStats
                    this.rootWallets = rootWallets
                })
        )
        this.fetchEvent.next()
    }

    public getAvailable(currency: ICurrency): string {
        if (!this.rootWallets) {
            return '0.00'
        }
        const wallet = this.rootWallets.find(w => w.currency.code === currency.code)
        if (!wallet) {
            return new BigNumber(0).toFixed(currency.decimalPlaces, 1)
        }
        return wallet.balance
    }

    public getIssued(currency: ICurrency): string {
        if (!this.supplyStats) {
            return '0.00'
        }
        const issued = this.supplyStats.find(s => s.currency.code === currency.code)
        if (!issued) {
            return new BigNumber(0).toFixed(currency.decimalPlaces, 1)
        }
        return issued.sum
    }

    public getTotal(currency: ICurrency): string {
        if (!this.supplyStats || !this.rootWallets) {
            return '0.00'
        }
        return new BigNumber(this.getIssued(currency))
            .plus(this.getAvailable(currency))
            .toFixed(currency.decimalPlaces, 1)
    }

    public getIssuedPercentage(currency: ICurrency): string {
        if (!this.supplyStats || !this.supplyStats.length || !this.rootWallets || !this.rootWallets.length) {
            return '0'
        }
        return new BigNumber(this.getIssued(currency)).div(this.getTotal(currency)).times(100).toFixed(0)
    }

    public getAvailablePercentage(currency: ICurrency): string {
        if (!this.supplyStats || !this.supplyStats.length || !this.rootWallets || !this.rootWallets.length) {
            return '0'
        }
        return new BigNumber(this.getAvailable(currency)).div(this.getTotal(currency)).times(100).toFixed(0)
    }

    public generateFunds(currency: ICurrency): void {
        this.showTransactionForm({
            type: 'mint',
            method: 'internal',
            baseWallet: null,
            counterWallet: { user: null } as Wallet,
            currency,
        })
    }

    public destroyFunds(currency: ICurrency): void {
        this.showTransactionForm({
            type: 'mint',
            method: 'internal',
            baseWallet: { user: null } as Wallet,
            counterWallet: null,
            currency,
        })
    }

    public togglePublishment(product: Product): void {
        this.http
            .patch<Product>(
                `/products/${product.id}`,
                [
                    {
                        op: 'replace',
                        path: '/published',
                        value: !product.published,
                    },
                ],
                {
                    headers: new HttpHeaders({ 'Content-Type': 'application/json-patch+json' }),
                }
            )
            .subscribe(response => {
                Object.assign(product, response)
                this.toastr.success(this.translate.instant('products.product-updated'))
            })
    }

    public toggleTransferability(product: Product): void {
        this.http
            .patch(
                `/products/${product.id}`,
                [
                    {
                        op: 'replace',
                        path: '/currency/transferable',
                        value: !product.currency!.transferable,
                    },
                ],
                {
                    headers: new HttpHeaders({ 'Content-Type': 'application/json-patch+json' }),
                }
            )
            .subscribe(response => {
                Object.assign(product, response)
                this.toastr.success(this.translate.instant('products.product-updated'))
            })
    }

    private showTransactionForm(transaction: Partial<ITransaction>): void {
        const modal = this.ngbModal.open(TransactionFormComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
        })
        modal.componentInstance.item = new Transaction(transaction as ITransaction)
        modal.componentInstance.onSave.subscribe(() => {
            modal.close()
            this.fetchEvent.next()
        })
    }
}
