import { Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core'
import { faExclamationTriangle, faGlobe, faUser, faUsers } from '@fortawesome/pro-light-svg-icons'
import { faCaretDown } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { clone } from 'lodash'
import { Subject, Subscription } from 'rxjs'
import { switchMap } from 'rxjs/operators'
import { SessionService } from 'src/app/common/services/session.service'
import { BeneficiaryFormComponent } from '../../../common/beneficiary-form/beneficiary-form.component'
import { Beneficiary } from '../../../common/models/accounting/beneficiary.model'
import { BeneficiaryService } from '../../../common/services/accounting/beneficiary.service'

@Component({
    selector: '[deposit-instruction]',
    templateUrl: 'deposit-instruction.component.html',
})
export class DepositInstructionComponent implements OnInit, OnDestroy, OnChanges {
    @Input()
    public beneficiary: Beneficiary
    public foundBeneficiary: Beneficiary | null

    public faExclamationTriangle = faExclamationTriangle
    public faGlobe = faGlobe
    public faUsers = faUsers
    public faUser = faUser
    public faCaretDown = faCaretDown

    private fetchEvent = new Subject<void>()
    private subscription = new Subscription()

    public constructor(
        private ngbModal: NgbModal,
        private beneficiaryService: BeneficiaryService,
        public session: SessionService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscription.add(
            this.fetchEvent
                .pipe(
                    switchMap(() =>
                        this.beneficiaryService.fetchDepositInstruction(
                            this.beneficiary.method,
                            this.beneficiary.currency?.code,
                            this.beneficiary.user?.id ?? undefined
                        )
                    )
                )
                .subscribe(depositInstruction => {
                    this.foundBeneficiary = depositInstruction
                })
        )

        this.fetchEvent.next()
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.beneficiary) {
            // Only re fetch when currency is changed
            if (
                changes.beneficiary.previousValue &&
                changes.beneficiary.previousValue.currency?.code !== changes.beneficiary.currentValue?.currency?.code
            ) {
                this.fetchEvent.next()
            }
        }
    }

    public ngOnDestroy(): void {
        this.subscription.unsubscribe()
    }

    public showForm(): void {
        const modal = this.ngbModal.open(BeneficiaryFormComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
        })
        const component = modal.componentInstance as BeneficiaryFormComponent
        component.item = clone(
            this.foundBeneficiary &&
                (this.isSpecific() || (this.isGlobal() && !this.isFallback() && !this.beneficiary.user))
                ? this.foundBeneficiary
                : this.beneficiary
        )
        component.fixateType = true
        component.hideFields = ['owner', 'type']
        const refresh = () => {
            this.fetchEvent.next()
            modal.close()
        }
        component.onSave.subscribe(refresh)
        component.onDelete.subscribe(refresh)
    }

    public get status(): 'global' | 'fallback' | 'specific' | 'association' | 'none' {
        if (this.foundBeneficiary) {
            if (this.isFallback()) {
                return 'fallback'
            } else if (this.isGlobal()) {
                return 'global'
            } else if (this.isSpecific()) {
                return 'specific'
            } else if (this.isAssociation()) {
                return 'association'
            }
        }
        return 'none'
    }

    private isSpecific(): boolean {
        return !!(
            this.beneficiary.currency.code === this.foundBeneficiary?.currency.code &&
            this.beneficiary.user &&
            this.foundBeneficiary?.user?.id === this.beneficiary.user.id
        )
    }

    private isAssociation(): boolean {
        return !!(
            this.beneficiary.currency.code === this.foundBeneficiary?.currency.code &&
            this.beneficiary.user &&
            this.foundBeneficiary?.user?.id !== this.beneficiary.user.id
        )
    }

    private isFallback(): boolean {
        return this.foundBeneficiary?.currency.code !== this.beneficiary.currency.code
    }

    private isGlobal(): boolean {
        return !this.foundBeneficiary?.user
    }
}
