import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faCheckCircle, faTimesCircle, faTrash } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { filter, finalize, flatMap } from 'rxjs/operators'
import { ListV2Component } from 'src/app/common/list-v2.component'
import { ConfirmationResult, ConfirmationService } from 'src/app/common/services/confirmation.service'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { Task, TaskStatus } from '../../common/api-interfaces'
import { SessionService } from '../../common/services/session.service'

@Component({
    selector: 'tasks',
    templateUrl: 'tasks.component.html',
})
export class TasksComponent extends ListV2Component<Task> implements OnInit {
    public faCheckCircle = faCheckCircle
    public faTimesCircle = faTimesCircle
    public faTrash = faTrash

    public statuses: TaskStatus[] = ['queued', 'errored', 'completed', 'processing']

    public isDeleting = false

    public apiUrl = '/admin/tasks'
    public defaultOrder = [['id', 'DESC']]
    public apiQuery = ['filter.status']

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        private confirmation: ConfirmationService,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session)
    }

    public ngOnInit(): void {
        super.ngOnInit()
    }

    public formatType(type: string): string {
        return type.replace(/([A-Z])/g, ' $1').replace(/^./, str => str.toUpperCase())
    }

    public deleteErroredTasks(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: this.translate.instant('common.are-you-sure-want-to-delete-tasks'),
                    confirmText: this.translate.instant('common.confirm'),
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => {
                        this.isDeleting = true
                        return this.http.delete<void>('/admin/tasks')
                    }),
                    finalize(() => {
                        this.isDeleting = false
                    })
                )
                .subscribe(() => {
                    this.updateEvent.next()
                    this.toastr.success(this.translate.instant('tasks.errored-tasks-deleted'))
                })
        )
    }
}
