import { Component, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges } from '@angular/core'
import {
    faArrowFromBottom,
    faExclamationTriangle,
    faGlobe,
    faLandmark,
    faUniversity,
    faUsers,
} from '@fortawesome/pro-light-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { cloneDeep } from 'lodash'
import { forkJoin, merge, of, Subject, Subscription } from 'rxjs'
import { switchMap } from 'rxjs/operators'
import { SessionService } from 'src/app/common/services/session.service'
import { BeneficiaryMethod } from '../../../common/api-interfaces'
import { Currency } from '../../../common/models/accounting/currency.model'
import { Fee, feeMethodMap, feeTypeMap } from '../../../common/models/accounting/fee.model'
import { BeneficiaryService } from '../../../common/services/accounting/beneficiary.service'
import { FeeService } from '../../../common/services/accounting/fee.service'
import { FeeScheduleFormComponent } from '../fee-schedule-form/fee-schedule-form.component'

@Component({
    selector: '[fee]',
    templateUrl: 'fee.component.html',
})
export class FeeComponent implements OnInit, OnChanges, OnDestroy {
    @Input()
    public fee: Fee
    @Input()
    public displayMethod = false
    @Output()
    public readonly onFeeChange = new EventEmitter<void>()
    // This is the fee being used for a particular type, method, currency combination
    public foundFee: Fee | null
    public isMissingBeneficiary = false
    public feeTypes = feeTypeMap

    public faExclamationTriangle = faExclamationTriangle
    public faGlobe = faGlobe
    public faLandmark = faLandmark
    public faUsers = faUsers
    public faUniversity = faUniversity
    public faArrowFromBottom = faArrowFromBottom

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        private feeService: FeeService,
        private beneficiaryService: BeneficiaryService,
        private ngbModal: NgbModal,
        public session: SessionService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() =>
                        forkJoin([
                            this.feeService.fetch(this.fee),
                            !['internal', 'card', 'redeem'].includes(this.fee.method)
                                ? this.beneficiaryService.count({
                                      type: 'deposit-instruction',
                                      method: this.fee.method as BeneficiaryMethod,
                                      currencyCode: this.fee.currency.code,
                                  })
                                : of(undefined),
                        ])
                    )
                )
                .subscribe(([fee, beneficiaryCount]) => {
                    this.foundFee = fee
                    this.isMissingBeneficiary = !!(
                        this.foundFee &&
                        this.fee.type === 'deposit' &&
                        beneficiaryCount !== undefined &&
                        beneficiaryCount === 0
                    )
                })
        )
        this.fetchEvent.next()
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.fee) {
            // Only re fetch when currency is changed
            if (
                changes.fee.previousValue &&
                changes.fee.previousValue.currency.code !== changes.fee.currentValue.currency.code
            ) {
                this.fetchEvent.next()
            }
        }
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public getFixedFee(): string {
        return new BigNumber(
            this.foundFee ? Currency.convert(this.foundFee.fixed || 0, this.foundFee.currency, this.fee.currency) : 0
        ).toFixed(this.fee.currency.decimalPlaces, 1)
    }

    public getMinValue(): string {
        return new BigNumber(
            this.foundFee ? Currency.convert(this.foundFee.minValue || 0, this.foundFee.currency, this.fee.currency) : 0
        ).toFixed(this.fee.currency.decimalPlaces, 1)
    }

    public getMaxFee(): string | null {
        return this.foundFee && this.foundFee.max
            ? new BigNumber(Currency.convert(this.foundFee.max, this.foundFee.currency, this.fee.currency)).toFixed(
                  this.fee.currency.decimalPlaces,
                  1
              )
            : null
    }

    public getMinFee(): string | null {
        return this.foundFee && this.foundFee.min
            ? new BigNumber(Currency.convert(this.foundFee.min, this.foundFee.currency, this.fee.currency)).toFixed(
                  this.fee.currency.decimalPlaces,
                  1
              )
            : null
    }

    public isFallbackFee(): boolean {
        return !!(this.foundFee && this.foundFee.isFallback(this.fee.currency))
    }

    public isAssociationFee(): boolean {
        return !!(this.foundFee && this.foundFee.isAssociationFee(this.fee.user))
    }

    public showAddButton(): boolean {
        if (!this.fee.user) {
            // Global setup
            return !this.foundFee || (this.foundFee && this.isFallbackFee())
        } else {
            // User setup
            return (
                !this.foundFee ||
                (this.foundFee && (this.foundFee.isGlobal() || this.isFallbackFee() || this.isAssociationFee()))
            )
        }
    }

    public showForm(): void {
        const modal = this.ngbModal.open(FeeScheduleFormComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
        })
        const form = modal.componentInstance as FeeScheduleFormComponent
        let fee = new Fee(this.fee)
        if (
            // Global fee schedule
            (!fee.user && this.foundFee && !this.isFallbackFee()) ||
            // User fee schedule
            (fee.user &&
                this.foundFee &&
                !this.foundFee.isGlobal() &&
                !this.isFallbackFee() &&
                !this.isAssociationFee())
        ) {
            fee = this.foundFee
        }
        form.fee = cloneDeep(fee)
        this.subscriptions.add(
            merge(form.onSubmit, form.onDelete).subscribe(() => {
                this.fetchEvent.next()
                this.onFeeChange.next()
                modal.close()
            })
        )
    }

    public getMethod(): string {
        if (this.fee.method === 'card') {
            return this.fee.type === 'withdrawal' ? 'Topup Card' : 'Unload Card'
        }
        return feeMethodMap[this.fee.method]
    }
}
