import { HttpClient } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { Subject, Subscription } from 'rxjs'
import { filter, finalize, flatMap, switchMap } from 'rxjs/operators'
import { CurrencyType, ICurrency } from '../../common/api-interfaces'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { ToastrService } from '../../common/services/toastr.service'

@Component({
    selector: 'currency-form',
    templateUrl: 'currency-form.component.html',
})
export class CurrencyFormComponent implements OnInit, OnDestroy {
    @Input()
    public item: ICurrency
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()

    public copy: Partial<ICurrency> = {}
    @ViewChild('currencyForm')
    public currencyForm: NgForm
    public currencyTypes: CurrencyType[] = ['fiat', 'crypto', 'metal']

    public isSaving = false
    public isDeleting = false

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(switchMap(() => this.http.get<ICurrency>(`/currencies/${this.item.code}`)))
                .subscribe(currency => {
                    Object.assign(this.item, currency)
                    this.copy = Object.assign({}, this.item)
                })
        )

        if (this.item && this.item.code) {
            this.fetchEvent.next()
        } else {
            this.copy = Object.assign({}, this.item)
        }
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public delete(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant('common.delete')}<br>${this.translate.instant(
                        'common.you-cant-undo-action'
                    )}`,
                    confirmText: this.translate.instant('common.delete'),
                    confirmClass: 'danger',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => {
                        this.isDeleting = true
                        return this.http.delete<void>(`/admin/currencies/${this.item.code}`)
                    }),
                    finalize(() => {
                        this.isDeleting = false
                    })
                )
                .subscribe(() => {
                    this.onDelete.emit()
                    this.toastr.success(this.translate.instant('currency-form.currency-deleted'))
                })
        )
    }

    public submit(): void {
        if (this.currencyForm.invalid) {
            throw new Error(this.translate.instant('common.form-invalid'))
        }
        this.isSaving = true
        this.subscriptions.add(
            (this.item.code
                ? this.http.put<ICurrency>(`/admin/currencies/${this.item.code}`, this.copy)
                : this.http.post<ICurrency>('/admin/currencies', this.copy)
            )
                .pipe(
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(currency => {
                    this.item = currency
                    this.toastr.success(
                        `Currency ${
                            this.item.code
                                ? this.translate.instant('common.updated')
                                : this.translate.instant('common.created')
                        }`
                    )
                    this.onSave.emit()
                })
        )
    }
}
