import { HttpClient } from '@angular/common/http'
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { NgModel } from '@angular/forms'
import { faExclamationTriangle, faSave } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { Subscription } from 'rxjs'
import { filter, finalize, flatMap } from 'rxjs/operators'
import { IAddress, Permission, RPCResult, Setting } from '../../common/api-interfaces'
import { permissionMap } from '../../common/common.mixin'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { ToastrService } from '../../common/services/toastr.service'
import { GlobalMessageModalComponent } from '../global-message-modal/global-message-modal.component'

@Component({
    selector: 'admin-settings',
    templateUrl: 'admin-settings.component.html',
})
export class AdminSettingsComponent implements OnInit, OnDestroy {
    public dailyReportReceiver: string[] = []
    public commission?: string
    public statusMessage?: string
    public registrationDisabled?: boolean
    public ipAddress: string | null
    public invoicerAddress: IAddress = {} as IAddress
    public defaultPermissions: Permission[] = []
    public cancelDeposit: string | null

    public isSavingDailyReportReceiver = false
    public isSavingCommission = false
    public isSavingStatusMessage = false
    public isSavingRegistrationDisabled = false
    public isResettingLoginBlocker = false
    public isSavingInvoicerAddress = false
    public isSavingDefaultPermissions = false
    public isSavingCancelDeposit = false

    public faExclamationTriangle = faExclamationTriangle
    public faSave = faSave

    @ViewChild('ipAddressInput')
    public ipAddressInput: NgModel

    private subscriptions = new Subscription()

    constructor(
        public translate: TranslateService,
        private http: HttpClient,
        private toastr: ToastrService,
        private confirmation: ConfirmationService,
        private ngbModal: NgbModal
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.http.get<Setting[]>('/settings').subscribe(settings => {
                const dailyReportReceiverSetting = settings.find(setting => setting.key === 'daily-report-receiver')
                const commissionSetting = settings.find(setting => setting.key === 'commission')
                const statusMessageSetting = settings.find(setting => setting.key === 'status-message')
                const registrationDisabledSetting = settings.find(setting => setting.key === 'registration-disabled')
                const invoicerAddressSetting = settings.find(setting => setting.key === 'invoicer-address')
                const defaultPermissionsSetting = settings.find(setting => setting.key === 'default-permissions')
                const cancelDepositSetting = settings.find(setting => setting.key === 'cancel-deposit')
                if (commissionSetting) {
                    this.commission = commissionSetting.value as string
                }
                if (dailyReportReceiverSetting) {
                    this.dailyReportReceiver = (dailyReportReceiverSetting.value as string[] | undefined) || []
                }
                if (statusMessageSetting) {
                    this.statusMessage = statusMessageSetting.value as string | undefined
                }
                if (registrationDisabledSetting) {
                    this.registrationDisabled = registrationDisabledSetting.value as boolean
                }
                if (invoicerAddressSetting) {
                    this.invoicerAddress = invoicerAddressSetting.value as IAddress
                }
                if (defaultPermissionsSetting) {
                    this.defaultPermissions = defaultPermissionsSetting.value as Permission[]
                }
                if (cancelDepositSetting) {
                    this.cancelDeposit = cancelDepositSetting.value as string
                }
            })
        )
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public saveCommission(): void {
        this.isSavingCommission = true
        this.http
            .put<Setting>('/settings/commission', { value: this.commission || null })
            .pipe(
                finalize(() => {
                    this.isSavingCommission = false
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('admin-settings.saved-commission'))
            })
    }

    public saveStatusMessage(): void {
        this.isSavingStatusMessage = true
        this.http
            .put<Setting>('/settings/status-message', { value: this.statusMessage || null })
            .pipe(
                finalize(() => {
                    this.isSavingStatusMessage = false
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('admin-settings.saved-status-message'))
            })
    }

    public saveRegistrationDisabled(): void {
        this.isSavingRegistrationDisabled = true
        this.http
            .put<Setting>('/settings/registration-disabled', { value: this.registrationDisabled })
            .pipe(
                finalize(() => {
                    this.isSavingRegistrationDisabled = false
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('admin-settings.saved-registration-disabled'))
            })
    }

    public resetLoginAttempts(): void {
        if (this.ipAddressInput.invalid) {
            return
        }
        this.isResettingLoginBlocker = true
        this.http
            .post<RPCResult<void>>(
                `/activities`,
                {
                    id: Math.round(Math.random() * 10000),
                    jsonrpc: '2.0',
                    method: 'resetLoginBlocker',
                    params: {
                        ipAddress: this.ipAddress,
                    },
                },
                {
                    headers: {
                        'Content-Type': 'application/json-rpc',
                    },
                }
            )
            .pipe(
                finalize(() => {
                    this.isResettingLoginBlocker = false
                })
            )
            .subscribe(() => {
                this.toastr.success(
                    `${this.translate.instant('admin-settings.login-blocker-reset-for')} ${
                        this.ipAddress
                    } ${this.translate.instant('common.sucessful')}`
                )
                this.ipAddress = null
                this.ipAddressInput.reset()
            })
    }

    public saveInvoicerAddress(): void {
        this.isSavingInvoicerAddress = true

        this.http
            .put<Setting>('/settings/invoicer-address', { value: this.invoicerAddress })
            .pipe(
                finalize(() => {
                    this.isSavingInvoicerAddress = false
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('admin-settings.saved-invoicer-address'))
            })
    }

    public saveDefaultPermissions(): void {
        this.isSavingDefaultPermissions = true
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant('common.you-are-about-update-default-permissions')}`,
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'danger',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() =>
                        this.http.put<Setting>('/settings/default-permissions', { value: this.defaultPermissions })
                    ),
                    finalize(() => {
                        this.isSavingDefaultPermissions = false
                    })
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('admin-settings.saved-default-permissions'))
                })
        )
    }

    public saveCancelDeposit(): void {
        this.isSavingCancelDeposit = true
        this.http
            .put<Setting>('/settings/cancel-deposit', { value: this.cancelDeposit })
            .pipe(
                finalize(() => {
                    this.isSavingCancelDeposit = false
                })
            )
            .subscribe(setting => {
                this.cancelDeposit = setting.value as string
                this.toastr.success(
                    `${this.translate.instant('admin-settings.pending-deposits-will-now-be-cancelled-after')} ${
                        this.cancelDeposit ?? 5
                    } ${this.translate.instant('common.days')}`
                )
            })
    }

    public getPermissionLabel(permission: Permission): string {
        return permissionMap[permission]
    }

    public toggleDefaultPermissions(permission: Permission): void {
        if (!this.defaultPermissions.includes(permission)) {
            this.defaultPermissions.push(permission)
        } else {
            this.defaultPermissions.splice(this.defaultPermissions.indexOf(permission), 1)
        }
    }

    public openGlobalMessageModal(): void {
        this.ngbModal.open(GlobalMessageModalComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
            size: 'lg',
        })
    }
}
