import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { AdminTradeModalComponent } from '../../admin-trades/admin-trade-modal.component'

@Component({
    selector: 'user-trades',
    template: `<admin-trades [apiUrl]="apiUrl" [formComponent]="formComponent"></admin-trades>`,
})
export class UserTradesComponent implements OnInit {
    public apiUrl: string
    public formComponent = AdminTradeModalComponent
    constructor(private route: ActivatedRoute) {}

    public ngOnInit(): void {
        this.apiUrl = `/admin/users/${this.route.snapshot.parent!.paramMap.get('userId')}/trades`
        this.route.parent!.paramMap.subscribe(params => {
            if (params.get('userId')) {
                this.apiUrl = `/admin/users/${params.get('userId')}/trades`
            }
        })
    }
}
