import { HttpClient, HttpParams } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { cloneDeep } from 'lodash'
import { of, Subject, Subscription } from 'rxjs'
import { debounceTime, distinctUntilChanged, filter, finalize, flatMap, switchMap } from 'rxjs/operators'
import { toStartCase } from 'src/app/common/common.mixin'
import { ApiToken, ApiTokenPermission, IUser } from '../../common/api-interfaces'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { ToastrService } from '../../common/services/toastr.service'

@Component({
    selector: 'api-token-form',
    templateUrl: 'api-token-form.component.html',
})
export class ApiTokenFormComponent implements OnInit, OnDestroy {
    @Input()
    public item: ApiToken
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()

    public copy: Partial<ApiToken> = {}
    @ViewChild('apiTokenForm', { static: false })
    public apiTokenForm: NgForm

    public isSaving = false
    public isDeleting = false

    public user?: IUser = undefined
    public userAutocomplete = new EventEmitter<string>()
    public legalEntitiesSteam = this.userAutocomplete.pipe(
        distinctUntilChanged(),
        debounceTime(200),
        switchMap(search =>
            this.http.get<IUser[]>('/users', {
                params: new HttpParams().set('limit', '25').set('search', search),
            })
        )
    )

    public toStartCase = toStartCase

    public permissions: ApiTokenPermission[] = ['card', 'customer']
    public ipAddress = ''

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() =>
                        this.item && this.item.publicKey
                            ? this.http.get<ApiToken>(`/admin/api-tokens/${this.item.publicKey}`)
                            : of(undefined)
                    )
                )
                .subscribe(response => {
                    if (response) {
                        Object.assign(this.item, response)
                    }
                    this.copy = cloneDeep(this.item)
                    this.user = this.item.user
                })
        )
        this.fetchEvent.next()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (this.apiTokenForm.invalid) {
            throw new Error(this.translate.instant('common.form-invalid'))
        }
        this.isSaving = true
        this.subscriptions.add(
            (this.item.publicKey
                ? this.http.put<ApiToken>(`/api-tokens/${this.item.publicKey}`, {
                      ipnCallbackUrl: this.copy.ipnCallbackUrl,
                      permissions: this.copy.permissions,
                      whitelist: this.copy.whitelist,
                  })
                : this.http.post<ApiToken>('/api-tokens', {
                      userId: this.user!.id,
                      ipnCallbackUrl: this.copy.ipnCallbackUrl,
                      permissions: this.copy.permissions,
                      whitelist: this.copy.whitelist,
                  })
            )
                .pipe(
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(response => {
                    // Show private key on create
                    if (!this.item.publicKey) {
                        this.confirmation
                            .show({
                                type: 'warning',
                                title: this.translate.instant('common.important'),
                                text: `
                        ${this.translate.instant('common.please-keep-this-private-key')}
                        <br>
                        <b>${response.privateKey}</b>
                        <br>${this.translate.instant('common.this-cannot-be-retrieved-once-lost')}
                    `,
                                confirmText: this.translate.instant('common.understood'),
                                showCancel: false,
                            })
                            .subscribe()
                    }
                    this.onSave.emit()
                    this.toastr.success(
                        `Api token ${
                            this.item.publicKey
                                ? this.translate.instant('common.updated')
                                : this.translate.instant('common.created')
                        }`
                    )
                    this.item = Object.assign(this.item, response)
                    this.copy = Object.assign(this.copy, this.item)
                    this.fetchEvent.next()
                })
        )
    }

    public revoke(): void {
        this.isDeleting = true
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    title: this.translate.instant('common.revoke-api-token'),
                    text: `${this.translate.instant(
                        'common.are-you-sure-want-to-revoke-this-token'
                    )} <b>${this.translate.instant('common.it-cannot-be-undone')}<b>`,
                    confirmText: this.translate.instant('common.revoke'),
                    confirmClass: 'danger',
                    showCancel: false,
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.http.delete(`/api-tokens/${this.item.publicKey}`)),
                    finalize(() => {
                        this.isDeleting = false
                    })
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('common.api-token-revoked'))
                    this.onDelete.emit()
                })
        )
    }

    public addIp(): void {
        if (!this.copy.whitelist) {
            this.copy.whitelist = []
        }

        this.copy.whitelist.push(this.ipAddress)
        this.ipAddress = ''
    }

    public removeIp(index: number): void {
        this.copy.whitelist!.splice(index, 1)
    }
}
