import { HttpClient } from '@angular/common/http'
import { Component, Input, ViewChild } from '@angular/core'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { finalize } from 'rxjs/operators'
import { IUser, Mail } from '../../../../common/api-interfaces'
import { readFileAsDataURL } from '../../../../common/common.mixin'
import { MailFormComponent, Template } from '../../../../common/mails/mail-form/mail-form.component'
import { ToastrService } from '../../../../common/services/toastr.service'

@Component({
    selector: 'user-message-modal',
    templateUrl: 'user-message-modal.component.html',
})
export class UserMessageModalComponent {
    public templates = [
        {
            subject: 'Account Status Update',
            group: 'closing',
            name: 'Exit forced by us',
            message:
                '<p>We are a dynamic company, which considers and reviews its business activities on a periodic basis. As part of this review, we have made a commercial decision to close your account with us.</p>' +
                '<p>We hereby would like you to close your account during the next 30 days and return any remaining funds to an account with another institution in your name.</p>',
        },
        {
            subject: 'Account Status Update',
            group: 'closing',
            name: 'Exit requested by the client',
            message:
                '<p>Thank you for your recent request to our Customer Support team.</p>' +
                '<p>We are sorry that you wish to close your account but we wanted to thank you for doing business with us and remind you to return any remaining funds to an account with another institution in your name.</p>',
        },
        {
            subject: 'Account Status Update',
            group: 'closed',
            name: 'Account closed',
            message:
                '<p>Please note that the status of your account has now been marked as closed.</p>' +
                '<p>Thank you for doing business with us.</p>',
        },
        {
            subject: 'Account Status Update',
            group: 'rejected',
            name: 'Account rejected',
            message:
                '<p>Thank you for applying for an account.</p>' +
                '<p>We would like to inform you that we have been unable to process the information you have provided to us with your application to the standard required by our internal processes.</p>' +
                '<p>We advise you regrettably that your application has been declined.</p>',
        },
    ]

    @Input()
    public user: IUser
    @Input()
    public selectedTemplate: Template
    @ViewChild('mailForm')
    public mailForm: MailFormComponent

    public mail: Partial<Mail> = {
        confidential: false,
    }
    public files: File[] = []
    public isLoading = false

    constructor(
        public activeModal: NgbActiveModal,
        private http: HttpClient,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {}

    public async submit(): Promise<void> {
        if (!this.mailForm.isValid()) {
            return
        }
        this.isLoading = true
        this.http
            .post<Mail>(`/admin/mails`, {
                subject: this.mail.subject,
                content: this.mail.content,
                confidential: this.mail.confidential,
                userId: this.user.id,
                attachments: await Promise.all(
                    this.files.map(async file => ({
                        name: encodeURIComponent(file.name),
                        base64: await readFileAsDataURL(file),
                    }))
                ),
            })
            .pipe(
                finalize(() => {
                    this.isLoading = false
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('common.message-sent'))
                this.activeModal.close()
            })
    }
}
