import { Component, OnDestroy } from '@angular/core'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { Subject, Subscription } from 'rxjs'
import { filter, finalize, flatMap } from 'rxjs/operators'
import { Term } from '../../../common/api-interfaces'
import { TermService } from '../../../common/services/compliance/term.service'
import { ConfirmationResult, ConfirmationService } from '../../../common/services/confirmation.service'
import { ToastrService } from '../../../common/services/toastr.service'

@Component({
    selector: 'terms-form',
    templateUrl: 'terms-form.component.html',
})
export class TermsFormComponent implements OnDestroy {
    public item: Term
    public currentTab: 'terms' | 'privacy' = 'terms'

    public editorModules = {
        toolbar: [
            ['bold', 'italic', 'underline'],
            [{ list: 'bullet' }],
            [{ indent: '-1' }, { indent: '+1' }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ align: [] as string[] }],
            [{ script: 'sub' }, { script: 'super' }],
        ],
    }
    public onSave = new Subject<void>()

    public isLoading = false

    private subscriptions = new Subscription()

    constructor(
        public activeModal: NgbActiveModal,
        private toastr: ToastrService,
        private termService: TermService,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (!this.item.terms) {
            this.toastr.error(this.translate.instant('terms-form.terms-is-required'))
            return
        }
        if (!this.item.privacy) {
            this.toastr.error(this.translate.instant('terms-form.privacy-is-required'))
            return
        }
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'warning',
                    text: this.translate.instant('terms-form.require-affected-users'),
                    confirmText: this.translate.instant('common.understood'),
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.termService.create(this.item)),
                    finalize(() => {
                        this.isLoading = false
                    })
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('terms-form.terms-created'))
                    this.onSave.next()
                })
        )
    }
}
