import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { faTimes } from '@fortawesome/pro-solid-svg-icons'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { cloneDeep } from 'lodash'
import { forkJoin, of, Subject, Subscription } from 'rxjs'
import { filter, finalize, flatMap, tap } from 'rxjs/operators'
import { Alert, IUser, Note } from '../../../common/api-interfaces'
import { deepOmitEmptyProperties } from '../../../common/common.mixin'
import { ConfirmationResult, ConfirmationService } from '../../../common/services/confirmation.service'
import { ToastrService } from '../../../common/services/toastr.service'

@Component({
    selector: 'notes-modal',
    templateUrl: 'notes-modal.component.html',
})
export class NotesModalComponent implements OnDestroy, OnInit {
    @Input()
    public note: Partial<Note>
    @Input()
    public user: IUser
    @Input()
    public alert: Alert
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @ViewChild('noteForm')
    public noteForm: NgForm

    public copy: Partial<Note>
    public files: File[] = []
    public isSaving = false
    public editorModules = {
        toolbar: [
            ['bold', 'italic', 'underline'],
            [{ list: 'bullet' }],
            [{ indent: '-1' }, { indent: '+1' }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ align: [] as string[] }],
            ['clean'],
            ['link'],
        ],
    }
    public faTimes = faTimes
    private fetchFilesEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        public activeModal: NgbActiveModal,
        private toastr: ToastrService,
        private http: HttpClient,
        private confirmation: ConfirmationService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.copy = cloneDeep(this.note)
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (this.noteForm.invalid) {
            this.toastr.error(this.translate.instant('common.form-invalid'))
            return
        }
        this.isSaving = true
        this.subscriptions.add(
            (this.note.id
                ? this.http.put<Note>(`/admin/notes/${this.note.id}`, this.copy)
                : this.http.post<Note>(
                      `/admin/notes`,
                      deepOmitEmptyProperties({
                          ...this.copy,
                          user: {
                              id: this.user && this.user.id,
                          },
                          alert: {
                              id: this.alert && this.alert.id,
                          },
                      })
                  )
            )
                .pipe(
                    tap(note => (this.note = note)),
                    flatMap(() =>
                        this.files.length > 0
                            ? forkJoin(
                                  this.files.map(file =>
                                      this.http.post<File>(`/admin/notes/${this.note.id}/files`, file, {
                                          headers: new HttpHeaders({
                                              'X-File-Name': encodeURIComponent(file.name),
                                              'Content-Type': file.type,
                                          }),
                                      })
                                  )
                              )
                            : of([])
                    ),
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(() => {
                    this.files = []
                    this.onSave.emit()
                    this.fetchFilesEvent.next()
                    this.toastr.success(this.translate.instant('notes-modal.note-saved'))
                })
        )
    }

    public deleteNote(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant(
                        'common.are-you-sure-want-to-delete-note'
                    )}<br>${this.translate.instant('common.you-cant-undo-action')}`,
                    confirmText: this.translate.instant('common.delete'),
                    confirmClass: 'danger',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.http.delete<void>(`/admin/notes/${this.note.id}`))
                )
                .subscribe(() => {
                    this.onSave.emit()
                    this.toastr.success(this.translate.instant('notes-modal.note-deleted'))
                    this.activeModal.dismiss()
                })
        )
    }
}
