import { HttpClient } from '@angular/common/http'
import { Component, Input, OnInit } from '@angular/core'
import { cloneDeep, find } from 'lodash'
import { lowerCase } from 'lodash'
import { Subject, Subscription } from 'rxjs'
import { switchMap } from 'rxjs/operators'
import { BeneficiaryMethod, ICurrency, IUser, Paginated } from '../../common/api-interfaces'
import { Beneficiary } from '../../common/models/accounting/beneficiary.model'
import { SessionService } from '../../common/services/session.service'

@Component({
    selector: 'admin-deposit-instructions',
    templateUrl: 'admin-deposit-instructions.component.html',
})
export class AdminDepositInstructionsComponent implements OnInit {
    @Input()
    public user?: IUser
    @Input()
    public currency: ICurrency
    public currencies: ICurrency[]
    public fetchEvent = new Subject<void>()
    public methods: BeneficiaryMethod[] = []

    private subscriptions = new Subscription()

    constructor(public session: SessionService, private http: HttpClient) {}

    public ngOnInit(): void {
        if (!(this.currency && this.currency.code)) {
            this.currency = cloneDeep(this.session.user.preferredCurrency)
        }
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() =>
                        this.http.get<Paginated<ICurrency>>('/currencies', {
                            params: {
                                limit: 100 + '',
                                'filter.type': 'fiat',
                                'filter.depositable': JSON.stringify(true),
                            },
                        })
                    )
                )
                .subscribe(response => {
                    this.currencies = response.data
                    this.currency = find(this.currencies, { code: this.currency.code }) ?? this.currencies[0]
                    this.methods = ['swift', 'local']
                })
        )
        this.fetchEvent.next()
    }

    public buildBeneficiary(method: BeneficiaryMethod): Beneficiary {
        return new Beneficiary({
            type: 'deposit-instruction',
            method,
            currency: this.currency,
            user: this.user ?? null,
        })
    }

    public onCurrencyChange(): void {
        this.methods =
            this.currency.type === 'crypto' ? [lowerCase(this.currency.code) as BeneficiaryMethod] : ['swift', 'local']
    }
}
