import { HttpClient, HttpParams } from '@angular/common/http'
import { Component, EventEmitter, OnDestroy, OnInit, ViewChild } from '@angular/core'
import {
    faBan,
    faBell,
    faCheckCircle,
    faCog,
    faEnvelope,
    faGhost,
    faInfoCircle,
    faKey,
    faLock,
    faMobile,
    faTrash,
    faUniversity,
    faUserAlt,
    faUserFriends,
    faWallet,
    faCreditCard,
    faIdCard,
    faGlobe,
} from '@fortawesome/pro-light-svg-icons'
import { TranslateService } from '@ngx-translate/core'
import { Subscription } from 'rxjs'
import { debounceTime, distinctUntilChanged, finalize, map, switchMap } from 'rxjs/operators'
import {
    Association,
    ICardProgram,
    ICurrency,
    IUser,
    Paginated,
    Referrer,
    UserDetails,
    UserRole,
    UserSegment,
} from '../../../../common/api-interfaces'
import { toStartCase } from '../../../../common/common.mixin'
import { UserService } from '../../../../common/services/core/user.service'
import { SessionService } from '../../../../common/services/session.service'
import { ToastrService } from '../../../../common/services/toastr.service'
import { UserDetailsFormComponent } from '../../../../common/user-details-form/user-details-form.component'
import { UserModelService } from '../user-model.service'

@Component({
    selector: 'user-account',
    templateUrl: 'user-account.component.html',
})
export class UserAccountComponent implements OnInit, OnDestroy {
    public user: IUser
    public details = {} as UserDetails
    @ViewChild('userForm')
    public userForm: UserDetailsFormComponent
    public newPassword: string
    public roles: UserRole[] = ['admin', 'licensee', 'support']
    public cardPrograms: ICardProgram[] = []
    public segments: UserSegment[] = []
    public currencies: ICurrency[] = []
    public referrer: Referrer
    public permissionStrategy: 'user' | 'association' | 'global'

    public referrerAutocomplete = new EventEmitter<string>()
    public referrersStream = this.referrerAutocomplete.pipe(
        distinctUntilChanged(),
        debounceTime(200),
        switchMap(search =>
            this.http
                .get<Paginated<Referrer>>('/admin/referrers', {
                    params: new HttpParams().set('limit', '25').set('search', search),
                })
                .pipe(map(response => response.data))
        )
    )
    public associationsAutoComplete = new EventEmitter<string>()
    public associationsStream = this.associationsAutoComplete.pipe(
        distinctUntilChanged(),
        debounceTime(200),
        switchMap(search =>
            this.http
                .get<Association>('/users', {
                    params: new HttpParams().set('limit', '5').set('search', search),
                })
                .pipe(map(response => response))
        )
    )

    public toStartCase = toStartCase

    public isUpdating = false
    public isFreezing = false
    public isEmailUpdating = false
    public isPermissionsUpdating = false
    public isPasswordUpdating = false
    public isTwoFactorAuthUpdating = false
    public sendVerificationEmail = false
    public isKYCReminderUpdating = false
    public isEmailNotificationsUpdating = false
    public isFatcaUpdating = false
    public isReferrerUpdating = false
    public isAssociationUpdating = false
    public isCardProgramUpdating = false
    public isExternalIdUpdating = false

    public faCheckCircle = faCheckCircle
    public faInfoCircle = faInfoCircle
    public faEnvelope = faEnvelope
    public faKey = faKey
    public faWallet = faWallet
    public faLock = faLock
    public faMobile = faMobile
    public faGhost = faGhost
    public faBan = faBan
    public faTrash = faTrash
    public faBell = faBell
    public faUniversity = faUniversity
    public faCog = faCog
    public faUserAlt = faUserAlt
    public faUserFriends = faUserFriends
    public faCreditCard = faCreditCard
    public faIdCard = faIdCard
    public faGlobe = faGlobe

    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private model: UserModelService,
        private toastr: ToastrService,
        private userService: UserService,
        public session: SessionService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.model.user.dataStream.subscribe(user => {
                if (user) {
                    this.user = user
                    this.segments =
                        this.user.type === 'business' ? ['agency', 'corporate', 'sme', 'merchant'] : ['retail']
                    this.http.get<UserDetails>(`/admin/users/${user.id}/details`).subscribe(details => {
                        this.details = details
                    })
                    if (user.referrer && user.referrer.id) {
                        this.subscriptions.add(
                            this.http
                                .get<Referrer>(`/referrers/${user.referrer.id}`)
                                .subscribe(referrer => (this.referrer = referrer))
                        )
                    }
                    this.subscriptions.add(
                        this.http
                            .get<{ permissionStrategy: 'user' | 'association' | 'global' }>(
                                `/admin/users/${user.id}/permission-strategy`
                            )
                            .subscribe(({ permissionStrategy }) => (this.permissionStrategy = permissionStrategy))
                    )
                }
            })
        )

        this.http.get<Paginated<ICurrency>>('/currencies', { params: { limit: 100 + '' } }).subscribe(response => {
            this.currencies = response.data
        })

        this.http
            .get<Paginated<ICardProgram>>('/admin/card-programs')
            .subscribe(response => (this.cardPrograms = response.data))
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        this.userForm.submit()
        if (!this.userForm.isValid()) {
            this.toastr.error(this.translate.instant('common.form-invalid'))
            return
        }
        this.isUpdating = true
        this.subscriptions.add(
            this.userService
                .updateDetails(this.user.id, this.details)
                .pipe(
                    finalize(() => {
                        this.isUpdating = false
                    })
                )
                .subscribe(details => {
                    Object.assign(this.details, details)
                    this.toastr.success(this.translate.instant('user-account.details-updated'))
                })
        )
    }

    public updateEmail(): void {
        this.isEmailUpdating = true
        this.subscriptions.add(
            this.userService
                .changeEmail(this.user.id, this.user.email)
                .pipe(
                    finalize(() => {
                        this.isEmailUpdating = false
                    })
                )
                .subscribe(user => {
                    this.model.user.set(Object.assign(this.user, user))
                    this.toastr.success(this.translate.instant('user-account.email-updated'))
                })
        )
    }

    public resendActivationEmail(): void {
        this.subscriptions.add(
            this.userService.resendActivationMail(this.user.id).subscribe(() => {
                this.toastr.success(this.translate.instant('user-account.activation-email-sent'))
            })
        )
    }

    public updatePermissions(options: { fallback: boolean } = { fallback: false }): void {
        this.isPermissionsUpdating = true
        this.subscriptions.add(
            this.userService
                .changePermission(this.user.id, options.fallback ? null : this.user.permissions)
                .pipe(
                    finalize(() => {
                        this.isPermissionsUpdating = false
                    })
                )
                .subscribe(user => {
                    this.model.user.set(Object.assign(this.user, user))
                    this.toastr.success(
                        options.fallback
                            ? this.translate.instant('common.user-will-now-fallback')
                            : this.translate.instant('user-account.permissions-updated')
                    )
                })
        )
    }

    public updateReferrer(): void {
        this.isReferrerUpdating = true
        this.subscriptions.add(
            this.userService
                .changeReferrer(this.user.id, this.referrer.id)
                .pipe(
                    finalize(() => {
                        this.isReferrerUpdating = false
                    })
                )
                .subscribe(user => {
                    this.model.user.set(Object.assign(this.user, user))
                    this.toastr.success(this.translate.instant('user-account.referrer-updated'))
                })
        )
    }

    public updateAssociation(): void {
        this.isAssociationUpdating = true
        this.subscriptions.add(
            this.userService
                .changeAssociation(this.user.id, this.user.association?.id || null)
                .pipe(
                    finalize(() => {
                        this.isAssociationUpdating = false
                    })
                )
                .subscribe(user => {
                    this.model.user.set(Object.assign(this.user, user))
                    this.toastr.success(this.translate.instant('user-account.association-updated'))
                })
        )
    }

    public updateExternalId(): void {
        this.isExternalIdUpdating = true
        this.subscriptions.add(
            this.userService
                .changeExternalId(this.user.id, this.user.externalId)
                .pipe(
                    finalize(() => {
                        this.isExternalIdUpdating = false
                    })
                )
                .subscribe(user => {
                    this.model.user.set(Object.assign(this.user, user))
                    this.toastr.success(this.translate.instant('user-account.external-id-updated'))
                })
        )
    }

    public updateRole(): void {
        this.subscriptions.add(
            this.userService.changeRole(this.user.id, this.user.role).subscribe(user => {
                this.model.user.set(Object.assign(this.user, user))
                this.toastr.success(this.translate.instant('user-account.role-updated'))
            })
        )
    }

    public updateSegment(): void {
        this.subscriptions.add(
            this.userService.changeSegment(this.user.id, this.user.segment).subscribe(user => {
                this.model.user.set(Object.assign(this.user, user))
                this.toastr.success(this.translate.instant('user-account.segment-updated'))
            })
        )
    }

    public updateStatus(): void {
        this.subscriptions.add(
            this.userService.changeStatus(this.user.id, this.user.status).subscribe(user => {
                this.model.user.set(Object.assign(this.user, user))
                this.toastr.success(this.translate.instant('common.status-updated'))
            })
        )
    }

    public updatePreferredCurrency(): void {
        this.subscriptions.add(
            this.userService.changePreferredCurrency(this.user.id, this.user.preferredCurrency).subscribe(user => {
                this.model.user.set(Object.assign(this.user, user))
                this.toastr.success(this.translate.instant('user-account.preferred-currency-updated'))
            })
        )
    }

    public changePassword(): void {
        this.isPasswordUpdating = true
        this.subscriptions.add(
            this.userService
                .changePassword(this.user.id, this.newPassword)
                .pipe(
                    finalize(() => {
                        this.isPasswordUpdating = false
                        this.newPassword = ''
                    })
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('user-account.password-updated'))
                })
        )
    }

    public disableTwoFactorAuth(): void {
        this.isTwoFactorAuthUpdating = true
        this.subscriptions.add(
            this.userService
                .changeTwoFactor(this.user.id, false)
                .pipe(
                    finalize(() => {
                        this.isTwoFactorAuthUpdating = false
                    })
                )
                .subscribe(user => {
                    this.model.user.set(Object.assign(this.user, user))
                    this.toastr.success(this.translate.instant('user-account.two-factor-authentication-disabled'))
                })
        )
    }

    public updateEmailNotifications(): void {
        this.isEmailNotificationsUpdating = true
        this.subscriptions.add(
            this.userService
                .changeSendEmailNotifications(this.user.id, this.user.sendEmailNotifications)
                .pipe(
                    finalize(() => {
                        this.isEmailNotificationsUpdating = false
                    })
                )
                .subscribe(() => {
                    this.model.fetchUser(this.user.id)
                    this.toastr.success(this.translate.instant('common.email-notifications-updated'))
                })
        )
    }

    public updateCardProgram(): void {
        this.isCardProgramUpdating = true
        this.subscriptions.add(
            this.userService
                .changeCardProgram(this.user.id, this.user.cardProgram?.id || null)
                .pipe(
                    finalize(() => {
                        this.isCardProgramUpdating = false
                    })
                )
                .subscribe(user => {
                    this.model.user.set(Object.assign(this.user, user))
                    this.toastr.success(this.translate.instant('common.card-program-updated'))
                })
        )
    }
}
