import { HttpClient, HttpParams } from '@angular/common/http'
import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { pick } from 'lodash'
import { Subject } from 'rxjs'
import { debounceTime, distinctUntilChanged, finalize, switchMap } from 'rxjs/operators'
import { IUser, Referrer, ReferrerType } from '../../common/api-interfaces'
import { ToastrService } from '../../common/services/toastr.service'

@Component({
    selector: 'referrers-form',
    templateUrl: 'referrers-form.component.html',
})
export class ReferrersFormComponent {
    public referrer?: Referrer
    public referrerAutocomplete = new EventEmitter<string>()
    public legalEntitiesSteam = this.referrerAutocomplete.pipe(
        distinctUntilChanged(),
        debounceTime(200),
        switchMap(search =>
            this.http.get<IUser[]>('/users', {
                params: new HttpParams().set('limit', '25').set('search', search),
            })
        )
    )
    @Input()
    public item: Referrer
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()
    public copy: Partial<Referrer> = {}
    @ViewChild('referrerForm')
    public referrerForm: NgForm
    public isSaving = false
    public isDeleting = false
    public referrerTypes: ReferrerType[] = ['platform', 'marketplace']
    private fetchEvent = new Subject<void>()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        public translate: TranslateService
    ) {}

    public submit(): void {
        if (this.referrerForm.invalid) {
            throw new Error(this.translate.instant('common.form-invalid'))
        }
        const body = {
            ...pick(this.copy, ['type', 'shareFeeSchedule', 'associate']),
            user: this.referrer ? { id: this.referrer.id } : null,
        }
        this.isSaving = true

        this.http
            .post<Referrer>('/admin/referrers', body)
            .pipe(
                finalize(() => {
                    this.isSaving = false
                })
            )
            .subscribe(() => {
                this.fetchEvent.next()
                this.toastr.success(this.translate.instant('common.referrer-created'))
                this.onSave.emit()
            })
    }
}
