import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { AlertTrigger } from '../../common/api-interfaces'
import { toStartCase } from '../../common/common.mixin'
import { ListComponent } from '../../common/list.component'
import { AlertTriggerFormComponent } from './alert-trigger-form.component'

@Component({
    selector: 'alert-triggers',
    templateUrl: 'alert-triggers.component.html',
})
export class AlertTriggersComponent extends ListComponent<AlertTrigger> {
    public toStartCase = toStartCase
    protected apiUrl = '/alert-triggers'
    protected defaultOrder = [['type', 'DESC']]
    protected formComponent = AlertTriggerFormComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public formatType(type: string): string {
        return (type.includes('transaction-') ? type.replace('transaction-', '') : type.replace('account-', ''))
            .toUpperCase()
            .replace(/-/g, '_')
    }
}
