import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { ConversionStatus } from 'src/app/common/api-interfaces'
import { ListComponent } from 'src/app/common/list.component'
import { Conversion } from 'src/app/common/models/accounting/conversion.model'
import { ConversionService } from 'src/app/common/services/accounting/conversion.service'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { AdminConversionFormComponent } from './admin-conversion-form.component'
import { ConversionOfferFormComponent } from './conversion-offer-form.component'

@Component({
    selector: 'admin-conversions',
    templateUrl: 'admin-conversions.component.html',
})
export class AdminConversionsComponent extends ListComponent<Conversion> {
    public statuses: ConversionStatus[] = ['accepted', 'cancelled', 'declined', 'offered', 'pending']

    protected apiUrl = '/conversions'
    protected apiQuery = ['status']
    protected formComponent = AdminConversionFormComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private toastr: ToastrService,
        private conversionService: ConversionService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public cancel(conversion: Conversion): void {
        this.subscriptions.add(
            this.conversionService
                .patch(conversion.id, [
                    {
                        op: 'replace',
                        path: '/status',
                        value: 'cancelled',
                    },
                ])
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('common.conversion-cancelled'))
                    this.updateEvent.next()
                })
        )
    }

    public makeAnOffer(conversion: Conversion): void {
        const modal = this.ngbModal.open(ConversionOfferFormComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
        })
        modal.componentInstance.item = conversion
        modal.componentInstance.onSave.subscribe(() => {
            modal.close()
            this.updateEvent.next()
        })
        modal.componentInstance.onDelete.subscribe(() => {
            modal.close()
            this.updateEvent.next()
        })
    }

    protected mapItem(item: any): Conversion {
        return new Conversion(item)
    }
}
