import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faCircle } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { Subject } from 'rxjs'
import { switchMap } from 'rxjs/operators'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { environment } from 'src/environments/environment'
import { ICardProgram, Paginated, SupplyStatistic, Wallet } from '../../common/api-interfaces'
import { ListComponent } from '../../common/list.component'

const CurrencyMap = {
    USD: 'USD',
}

@Component({
    selector: 'funds-under-management',
    templateUrl: 'funds-under-management.component.html',
})
export class FundsUnderManagementComponent extends ListComponent<Wallet> implements OnInit, OnDestroy {
    public appName = environment.appName
    public faCircle = faCircle
    public currencies: { currencyCode: string; decimalPlaces: number; sum: string }[] = []
    public currency: { currencyCode: string; decimalPlaces: number; sum: string } | null = null
    public programs: ICardProgram[] = []
    protected defaultOrder = [['balance', 'DESC']]
    protected apiQuery = ['$user.cardProgramId']
    private fetchEvent = new Subject<void>()

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
        this.http.get<Paginated<ICardProgram>>('/admin/card-programs').subscribe(response => {
            this.programs = response.data
        })
    }

    public ngOnInit(): void {
        this.apiUrl = this.getApiUrl(CurrencyMap.USD)
        super.ngOnInit()
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() =>
                        this.http.get<SupplyStatistic[]>('/statistics/supply', { params: { limit: 100 + '' } })
                    )
                )
                .subscribe(response => {
                    this.currencies = response.map(statistic => ({
                        currencyCode: statistic.currency.code,
                        decimalPlaces: statistic.currency.decimalPlaces,
                        sum: statistic.sum,
                    }))
                    this.currency = this.currencies.find(currency => currency.currencyCode === 'USD')!
                })
        )
        this.fetchEvent.next()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public onCurrencyChanged(option: { currencyCode: string; sum: string }): void {
        this.apiUrl = this.getApiUrl(option.currencyCode)
        this.select(1)
    }

    private getApiUrl(currencyCode: string): string {
        return `/admin/currencies/${currencyCode}/wallets?userId=ne:null`
    }
}
