import { HttpClient, HttpParams } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { Observable } from 'rxjs'
import { map } from 'rxjs/operators'
import { CardOrder } from '../../models/accounting/card-order.model'

@Injectable({
    providedIn: 'root',
})
export class CardOrderService {
    constructor(private http: HttpClient) {}

    public fetchByUserId(id: string): Observable<CardOrder[]> {
        return this.http
            .get<CardOrder[]>(`/users/${id}/card-orders`)
            .pipe(map(cardOrders => cardOrders.map(cardOrder => new CardOrder(cardOrder))))
    }

    public countByUserId(id: string, where?: any): Observable<number> {
        return this.count(`/users/${id}/card-orders`, where)
    }

    private count(url: string, where?: any): Observable<number> {
        const params = new HttpParams()
        if (where) {
            params.set('where', JSON.stringify(where))
        }
        return this.http
            .head(url, {
                observe: 'response',
                params,
            })
            .pipe(
                map(response =>
                    response.headers && response.headers.has('x-total-count')
                        ? ~~response.headers.get('x-total-count')!
                        : 0
                )
            )
    }
}
