import { cloneDeep } from 'lodash'
import { BehaviorSubject, Observable } from 'rxjs'
import { map } from 'rxjs/operators'

export class Model<T> {
    public dataStream: Observable<T>
    private data: BehaviorSubject<T>

    constructor(initialData: T, immutable = true, clone?: (data: T) => T) {
        this.data = new BehaviorSubject(initialData)
        this.dataStream = this.data
            .asObservable()
            .pipe(map(data => (immutable ? (clone ? clone(data) : cloneDeep(data)) : data)))
    }

    public get(): T {
        return this.data.getValue()
    }

    public set(data: T): void {
        this.data.next(data)
    }
}
