import { HttpClient } from '@angular/common/http'
import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { faCheck, faSpinner } from '@fortawesome/pro-solid-svg-icons'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { of, Subject, Subscription } from 'rxjs'
import { finalize, switchMap } from 'rxjs/operators'
import { Alert, Transaction } from '../../common/api-interfaces'
import { toStartCase } from '../../common/common.mixin'
import { ToastrService } from '../../common/services/toastr.service'

@Component({
    selector: 'alert-view',
    templateUrl: 'alert-view.component.html',
})
export class AlertViewComponent implements OnInit {
    @Input()
    public item: Alert
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()
    @ViewChild('form')
    public form: NgForm
    @ViewChild('walletAddressForm')
    public walletAddressForm: NgForm

    public transactions: Transaction[] = []

    public isCreating = false
    public submitted = false
    public note: string

    // Fintrac report
    public transactionsForReport: Transaction[] = []
    public walletAddresses: Record<string, string> = {}
    public isGeneratingFintracReport = false

    public toStartCase = toStartCase
    public faSpinner = faSpinner
    public faCheck = faCheck

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        public activeModal: NgbActiveModal,
        public http: HttpClient,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() =>
                        this.item.id
                            ? this.http.get<Transaction[]>(`/alerts/${this.item.id}/transactions`)
                            : of(undefined)
                    )
                )
                .subscribe(response => {
                    if (response) {
                        this.transactions = response

                        if (this.item.trigger?.type === 'transaction-fintrac-report') {
                            this.transactionsForReport = this.transactions.filter(transaction =>
                                ['pending', 'processing', 'completed'].includes(transaction.status)
                            )
                        }
                    }
                })
        )
        this.fetchEvent.next()
    }

    public create(): void {
        this.submitted = true
        if (this.form.invalid) {
            return
        }
        this.isCreating = true
        this.http
            .post<Alert>('/alerts', {
                user: this.item.user
                    ? {
                          id: this.item.user.id,
                      }
                    : null,
                note: this.note,
            })
            .pipe(
                finalize(() => {
                    this.isCreating = false
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('alert-view.alert-created'))
                this.onSave.next()
            })
    }

    public generateFintracReport(): void {
        if (this.walletAddressForm.invalid) {
            return
        }
        this.isGeneratingFintracReport = true
        this.http
            .post(
                `/alerts/${this.item.id}/fintrac-report`,
                {
                    sendingWalletAddresses: this.walletAddresses,
                },
                {
                    observe: 'response',
                    responseType: 'blob',
                }
            )
            .pipe(
                finalize(() => {
                    this.isGeneratingFintracReport = false
                })
            )
            .subscribe(response => {
                this.walletAddressForm.resetForm()
                if (window.navigator && window.navigator.msSaveOrOpenBlob) {
                    window.navigator.msSaveOrOpenBlob(response.body)
                } else {
                    const data = window.URL.createObjectURL(response.body)
                    const link = document.createElement('a')
                    link.href = data
                    const contentDisposition = response.headers.get('Content-Disposition')
                    link.download = contentDisposition
                        ? contentDisposition.substring(contentDisposition.indexOf('filename=') + 9)
                        : 'report.json'
                    link.click()
                    setTimeout(() => {
                        // For Firefox it is necessary to delay revoking the ObjectURL
                        window.URL.revokeObjectURL(data)
                    }, 100)
                }
            })
    }
}
