import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'

@Component({
    selector: 'user-transactions',
    template: ` <transactions [apiUrl]="apiUrl" [displayStatementButton]="true" [userId]="userId"></transactions> `,
})
export class UserTransactionsComponent implements OnInit {
    public apiUrl: string
    public userId: string | null
    constructor(private route: ActivatedRoute) {}
    public ngOnInit(): void {
        this.userId = this.route.snapshot.parent!.paramMap.get('userId')
        this.apiUrl = `/users/${this.userId}/transactions`
        this.route.parent!.paramMap.subscribe(params => {
            this.userId = params.get('userId')
            this.apiUrl = `/users/${params.get('userId')}/transactions`
        })
    }
}
