import { Component, OnDestroy, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { Subscription } from 'rxjs'

@Component({
    selector: 'user-spot-trading',
    template: `<admin-spot-trading [apiUrl]="apiUrl"></admin-spot-trading>`,
})
export class UserSpotTradingComponent implements OnInit, OnDestroy {
    public apiUrl: string

    private subscriptions = new Subscription()

    constructor(private route: ActivatedRoute) {}

    public ngOnInit(): void {
        this.apiUrl = `/users/${this.route.snapshot.parent!.paramMap.get('userId')}/spot-trades`
        this.subscriptions.add(
            this.route.parent!.paramMap.subscribe(params => {
                if (params.get('userId')) {
                    this.apiUrl = `/users/${params.get('userId')}/spot-trades`
                }
            })
        )
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }
}
