import { Component, OnDestroy, OnInit } from '@angular/core'
import {
    faCheckCircle,
    faChevronRight,
    faClock,
    faFileAlt,
    faInfoCircle,
    faUndo,
} from '@fortawesome/pro-solid-svg-icons'
import { TranslateService } from '@ngx-translate/core'
import { Subscription } from 'rxjs'
import { filter, finalize } from 'rxjs/operators'
import { IUser, UserFlag } from '../../../../common/api-interfaces'
import { toStartCase } from '../../../../common/common.mixin'
import { UserService } from '../../../../common/services/core/user.service'
import { SessionService } from '../../../../common/services/session.service'
import { ToastrService } from '../../../../common/services/toastr.service'
import { UserModelService } from '../user-model.service'

@Component({
    selector: 'user-overview',
    templateUrl: 'user-overview.component.html',
})
export class UserOverviewComponent implements OnInit, OnDestroy {
    public user: IUser
    public flags: UserFlag[] = ['fatca', 'pep', 'hnw', 'monitored']

    public faFileAlt = faFileAlt
    public faChevronRight = faChevronRight
    public faUndo = faUndo
    public faCheckCircle = faCheckCircle
    public faClock = faClock
    public faInfoCircle = faInfoCircle

    public isFlagsUpdating = false

    public toStartCase = toStartCase

    private subscriptions = new Subscription()

    constructor(
        private model: UserModelService,
        private toastr: ToastrService,
        private userService: UserService,
        public session: SessionService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.model.user.dataStream.pipe(filter((user): user is IUser => !!user)).subscribe(user => {
                this.user = user
            })
        )
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public updateFlags(): void {
        this.isFlagsUpdating = true
        this.subscriptions.add(
            this.userService
                .changeFlags(this.user.id, this.user.flags)
                .pipe(
                    finalize(() => {
                        this.isFlagsUpdating = false
                    })
                )
                .subscribe(user => {
                    this.model.user.set(Object.assign(this.user, user))
                    this.toastr.success(this.translate.instant('user-overview.flags-updated'))
                })
        )
    }
}
