import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { ModalController } from '@ionic/angular'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { CardStatus, CardType, ICard, ICardProgram, Paginated } from '../../common/api-interfaces'
import { CardViewPage } from '../../common/card-view/card-view.page'
import { ListComponent } from '../../common/list.component'
import { providerMap } from '../../common/models/accounting/card.model'
import { CardFormComponent } from './card-form.component'

@Component({
    selector: 'cards',
    templateUrl: 'cards.component.html',
})
export class CardsComponent extends ListComponent<ICard> {
    public providerMap = providerMap
    @Input()
    public apiUrl = '/admin/cards'
    public formComponent = CardFormComponent
    public statuses: CardStatus[] = ['active', 'inactive', 'blocked']
    public types: CardType[] = ['virtual', 'physical']
    public programs: ICardProgram[] = []
    protected apiQuery = ['status', 'type', '$cardholder.cardProgramId']

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private modalController: ModalController,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
        this.http.get<Paginated<ICardProgram>>('/admin/card-programs').subscribe(response => {
            this.programs = response.data
        })
    }

    public async viewCard(card: ICard): Promise<void> {
        const modal = await this.modalController.create({
            component: CardViewPage,
            componentProps: {
                item: card,
            },
        })
        return await modal.present()
    }
}
