import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faCheckSquare, faPaperPlane } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { filter, flatMap } from 'rxjs/operators'
import { CardOrderStatus, CardType, ICardProgram, Paginated } from 'src/app/common/api-interfaces'
import { ConfirmationResult, ConfirmationService } from 'src/app/common/services/confirmation.service'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { CardOrderConfirmationComponent } from 'src/app/user-cards/card-order-confirmation/card-order-confirmation.component'
import { ListComponent } from '../../common/list.component'
import { CardOrder } from '../../common/models/accounting/card-order.model'
import { SessionService } from '../../common/services/session.service'
import { CardOrderCreateFormComponent } from './card-order-create-form.component'
import { CardOrderFormComponent } from './card-order-form.component'

@Component({
    selector: 'card-orders',
    templateUrl: 'card-orders.component.html',
})
export class CardOrdersComponent extends ListComponent<CardOrder> {
    public faCheckSquare = faCheckSquare

    public statuses: CardOrderStatus[] = [
        'pending',
        'processing',
        'completed',
        'cancelled',
        'unconfirmed',
        'action-required',
    ]
    public types: CardType[] = ['physical', 'virtual']
    public programs: ICardProgram[] = []

    @Input()
    public apiUrl = '/card-orders'
    public faPaperPlane = faPaperPlane

    protected formComponent = CardOrderFormComponent
    protected apiQuery = ['status', 'type', '$user.cardProgramId', 'isExpressDelivery']

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
        this.http.get<Paginated<ICardProgram>>('/admin/card-programs').subscribe(response => {
            this.programs = response.data
        })
    }

    public openConfirmCardOrderModal(cardOrder: CardOrder): void {
        const cardOrderConfirmationModal = this.ngbModal.open(CardOrderConfirmationComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
            keyboard: false,
        })
        ;(cardOrderConfirmationModal.componentInstance as CardOrderConfirmationComponent).cardOrder = cardOrder
    }

    public approveCardOrders(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'info',
                    title: this.translate.instant('card-orders.run-auto-approval-task'),
                    text: 'This action will approve all orders from customers that passed the issuers compliance check.',
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'info',
                    cancelText: this.translate.instant('common.cancel'),
                    cancelClass: 'danger',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.http.post(`/admin/card-orders/auto-approve`, {}))
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('card-orders.card-order-approval-task-enqueued'))
                })
        )
    }

    public showCreateForm() {
        const modal = this.ngbModal.open(CardOrderCreateFormComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
        })
        this.subscriptions.add(
            modal.componentInstance.onSave.subscribe(() => {
                modal.close()
                this.updateEvent.next()
            })
        )
    }

    protected mapItem(item: any): CardOrder {
        return new CardOrder(item)
    }
}
