import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import {
    faCalendarAlt,
    faCircle,
    faDownload,
    faPlus,
    faSyncAlt,
    faCheckCircle,
    faTimesCircle,
} from '@fortawesome/pro-solid-svg-icons'
import { NgbCalendar, NgbDateStruct, NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { filter, flatMap } from 'rxjs/operators'
import { ListV2Component } from 'src/app/common/list-v2.component'
import { Referrer } from '../../common/api-interfaces'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { SessionService } from '../../common/services/session.service'
import { ToastrService } from '../../common/services/toastr.service'
import { ReferrersFormComponent } from './referrers-form.component'
import { ReferrersModalComponent } from './referrers-modal.component'

export enum Scope {
    DEFAULT = 'default',
}

@Component({
    selector: 'referrers',
    templateUrl: 'referrers.component.html',
})
export class ReferrersComponent extends ListV2Component<any> {
    public faCircle = faCircle
    public faSyncAlt = faSyncAlt
    public faDownload = faDownload
    public faPlus = faPlus
    public faCalendarAlt = faCalendarAlt
    public faCheckCircle = faCheckCircle
    public faTimesCircle = faTimesCircle
    public currentHost = window.location.hostname
    public fromDate: NgbDateStruct
    public toDate: NgbDateStruct
    @Input()
    public apiUrl = '/admin/referrers'
    @Input()
    public formComponent = ReferrersModalComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        private toastr: ToastrService,
        private confirmation: ConfirmationService,
        public calendar: NgbCalendar,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session)
        this.fromDate = { year: new Date().getFullYear(), month: 1, day: 1 }
        this.toDate = this.calendar.getToday()
    }

    public addNew(): void {
        const modal = this.ngbModal.open(ReferrersFormComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
        })
        modal.componentInstance.item = this.mapItem({})
        modal.componentInstance.onSave.subscribe(() => {
            modal.close()
            this.updateEvent.next()
        })
        modal.componentInstance.onDelete.subscribe(() => {
            modal.close()
            this.updateEvent.next()
        })
    }

    public delete(referrer: Referrer): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant(
                        'common.are-you-sure-want-to-delete-referrer'
                    )}<br>${this.translate.instant('common.you-cant-undo-action')}`,
                    confirmText: this.translate.instant('common.delete'),
                    confirmClass: 'danger',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.http.delete<void>(`/admin/referrers/${referrer.id}`))
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('common.referrer-deleted'))
                    this.updateEvent.next()
                })
        )
    }

    public downloadReport(): void {
        this.subscriptions.add(
            this.session.createSignedUrl('/admin/referrers/report').subscribe(signedUrl => {
                window.open(
                    `${signedUrl}&from=${this.parseNgbdate(this.fromDate)}&to=${this.parseNgbdate(this.toDate)}`,
                    '_blank',
                    'noopener'
                )
            })
        )
    }

    protected parseNgbdate(date: NgbDateStruct | null): string {
        return `${date?.year}-${date?.month}-${date?.day}`
    }
}
