import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input, OnDestroy } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { Subscription } from 'rxjs'
import { filter, flatMap } from 'rxjs/operators'
import { RedemptionConfirmModalComponent } from 'src/app/common/redemption/redemption-confirm-modal.component'
import { SecurityCheckService } from 'src/app/common/security-check/security-check.service'
import { ConfirmationResult, ConfirmationService } from 'src/app/common/services/confirmation.service'
import { RedemptionService } from 'src/app/common/services/redemption.service'
import { SessionService } from 'src/app/common/services/session.service'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { Redemption } from '../../common/api-interfaces'
import { ListV2Component } from '../../common/list-v2.component'
import { AdminRedemptionModalComponent } from './admin-redemption-modal.component'

@Component({
    selector: 'admin-redemptions',
    templateUrl: 'admin-redemptions.component.html',
})
export class AdminRedemptionsComponent extends ListV2Component<Redemption> implements OnDestroy {
    @Input()
    public apiUrl = '/admin/redemptions'
    @Input()
    public formComponent = AdminRedemptionModalComponent

    public subscriptions = new Subscription()

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        public translate: TranslateService,
        protected toastr: ToastrService,
        private confirmation: ConfirmationService,
        private redemptionService: RedemptionService,
        private securityCheckService: SecurityCheckService
    ) {
        super(http, ngbModal, changeDetector, route, router, session)
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public showRedemptionModal(isEditing: boolean, item?: Partial<Redemption>): void {
        const modal = this.ngbModal.open(AdminRedemptionModalComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
            size: 'lg',
        })
        if (isEditing) {
            modal.componentInstance.redemption = this.mapRedemption(item!)
            modal.componentInstance.address = item!.shippingAddress
            modal.componentInstance.user = item!.user
        } else {
            modal.componentInstance.redemption = {
                method: 'self-collect',
                type: 'gold',
                quantity: 0,
                denomination: {
                    oneGram: 0,
                    fiveGram: 0,
                    fiftyGram: 0,
                    hundredGram: 0,
                    thousandGram: 0,
                    twoOunce: 0,
                    fiveOunce: 0,
                    tenOunce: 0,
                    hundredOunce: 0,
                    oneKilogram: 0,
                },
            }
        }
        modal.componentInstance.isEditing = isEditing
        modal.result.then(() => this.updateEvent.next())
    }

    public mapRedemption(item: Partial<Redemption>): Partial<Redemption> {
        return {
            ...item,
            method: item.method,
            type: item.type,
            oneGram: item.oneGram,
            fiveGram: item.fiveGram,
            fiftyGram: item.fiftyGram,
            hundredGram: item.hundredGram,
            thousandGram: item.thousandGram,
            twoOunce: item.twoOunce,
            fiveOunce: item.fiveOunce,
            tenOunce: item.tenOunce,
            hundredOunce: item.hundredOunce,
            oneKilogram: item.oneKilogram,
            shippingAddress: item.shippingAddress,
            transaction: item.transaction,
        }
    }

    public showConfirmModal(redemption: Partial<Redemption>, isPreview: boolean): void {
        const modal = this.ngbModal.open(RedemptionConfirmModalComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
        })
        const component = modal.componentInstance as RedemptionConfirmModalComponent
        component.redemption = this.mapRedemption(redemption)
        component.address = redemption.shippingAddress ?? null
        component.confirmButtonText = this.translate.instant('admin-redemption.process-redemption')
        if (isPreview) {
            component.cancelButtonText = 'Close'
        }
        component.isPreview = isPreview
        component.onExecute.subscribe(() => {
            this.securityCheckService
                .getScopePermission('banking')
                .pipe(flatMap(() => this.redemptionService.updateRedemption(redemption, 'processing')))
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('admin-redemptions.redemption-status-updated'))
                    this.updateEvent.next()
                    modal.close()
                })
        })
    }

    public completeRedemption(redemption: Partial<Redemption>): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'warning',
                    title: this.translate.instant('common.complete-redemption'),
                    text: this.translate.instant('common.are-you-sure-want-to-change-redemptions-status'),
                    confirmText: this.translate.instant('common.confirm'),
                    cancelText: this.translate.instant('common.cancel'),
                    showCancel: true,
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.redemptionService.updateRedemption(redemption, 'completed'))
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('admin-redemptions.redemption-status-updated'))
                    this.updateEvent.next()
                })
        )
    }

    public getQuantity(redemption: Redemption): string {
        return new BigNumber(this.redemptionService.totalGrams(redemption)).toFixed(0)
    }

    public cancel(redemption: Redemption): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant('common.are-you-sure-want-to-cancel')} ${redemption.id}`,
                    confirmText: this.translate.instant('common.yes'),
                    confirmClass: 'danger',
                    cancelText: this.translate.instant('common.no'),
                    cancelClass: 'success',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.redemptionService.updateRedemption(redemption, 'cancelled'))
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('admin-redemptions.redemption-cancelled'))
                    this.updateEvent.next()
                })
        )
    }
}
