import { Component, Input, OnInit } from '@angular/core'
import { take } from 'rxjs/operators'
import { SessionService } from 'src/app/common/services/session.service'
import { FeeType, ICurrency, IUser, TransactionMethod } from '../../common/api-interfaces'
import { Fee } from '../../common/models/accounting/fee.model'
import { CurrenciesQuery } from '../../store/currencies/currencies.query'

@Component({
    selector: 'fee-schedule',
    templateUrl: 'fee-schedule.component.html',
})
export class FeeScheduleComponent implements OnInit {
    @Input()
    public user: IUser | null
    @Input()
    public currency: ICurrency
    public showCurrencyDropdown = false
    public methods: TransactionMethod[] = ['swift', 'local']

    constructor(private currenciesQuery: CurrenciesQuery, public session: SessionService) {}

    public ngOnInit(): void {
        if (!(this.currency && this.currency.code)) {
            this.showCurrencyDropdown = true
        }
        this.currenciesQuery
            .selectEntity('USD')
            .pipe(take(1))
            .subscribe(usd => {
                this.currency = usd!
                this.onCurrencyChange()
            })
    }

    public createFee(type: FeeType, method: TransactionMethod): Fee {
        return new Fee({
            type,
            method,
            currency: this.currency,
            user: this.user,
        })
    }

    public onCurrencyChange(): void {
        this.methods = this.currency.type === 'crypto' ? ['crypto'] : ['swift', 'local']
    }
}
