import { HttpClient } from '@angular/common/http'
import { Component, Input, OnDestroy, OnInit } from '@angular/core'
import { faPencil, faPlus } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { Subject, Subscription } from 'rxjs'
import { flatMap } from 'rxjs/operators'
import { Alert, IUser, Note } from '../../common/api-interfaces'
import { SessionService } from '../../common/services/session.service'
import { NotesModalComponent } from './notes-modal/notes-modal.component'

@Component({
    selector: 'notes',
    templateUrl: 'notes.component.html',
})
export class NotesComponent implements OnInit, OnDestroy {
    @Input()
    public user: IUser
    @Input()
    public alert: Alert
    @Input()
    public isLoading = false
    public notes: Note[] = []
    public faPlus = faPlus
    public faPencil = faPencil
    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(private http: HttpClient, private modal: NgbModal, public session: SessionService) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    flatMap(() =>
                        this.http.get<Note[]>(
                            `/admin/${this.user ? 'users' : 'alerts'}/${this.user ? this.user.id : this.alert.id}/notes`
                        )
                    )
                )
                .subscribe(notes => {
                    this.notes = notes
                })
        )
        this.fetchEvent.next()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public openModal(note: Partial<Note>): void {
        const modal = this.modal.open(NotesModalComponent, {
            windowClass: 'modal-primary',
            size: 'lg',
            backdrop: 'static',
        })
        ;(modal.componentInstance as NotesModalComponent).note = note
        ;(modal.componentInstance as NotesModalComponent).user = this.user
        ;(modal.componentInstance as NotesModalComponent).alert = this.alert
        ;(modal.componentInstance as NotesModalComponent).onSave.subscribe(() => {
            this.fetchEvent.next()
            modal.close()
        })
    }
}
