import { Component, Input, OnChanges, SimpleChanges } from '@angular/core'
import BigNumber from 'bignumber.js'
import { TransactionStats } from '../transactions.component'

@Component({
    selector: 'transaction-stats',
    templateUrl: 'transaction-stats.component.html',
})
export class TransactionStatsComponent implements OnChanges {
    @Input()
    public stats: TransactionStats

    public totalVolumeInUsd?: string
    public totalFeesInUsd?: string
    public totalCreditInUsd?: string
    public totalDebitInUsd?: string

    public ngOnChanges(changes: SimpleChanges) {
        if (changes.stats) {
            this.totalFeesInUsd = undefined
            this.totalVolumeInUsd = undefined
            this.totalCreditInUsd = undefined
            this.totalDebitInUsd = undefined

            for (const stat of this.stats) {
                this.totalFeesInUsd = new BigNumber(this.totalFeesInUsd || 0).plus(stat.totalFeesInUsd).toFixed()
                if (stat.totalVolumeInUsd) {
                    this.totalVolumeInUsd = new BigNumber(this.totalVolumeInUsd || 0)
                        .plus(stat.totalVolumeInUsd)
                        .toFixed()
                }
                if (stat.totalCreditInUsd) {
                    this.totalCreditInUsd = new BigNumber(this.totalCreditInUsd || 0)
                        .plus(stat.totalCreditInUsd)
                        .toFixed()
                }
                if (stat.totalDebitInUsd) {
                    this.totalDebitInUsd = new BigNumber(this.totalDebitInUsd || 0).plus(stat.totalDebitInUsd).toFixed()
                }
            }
        }
    }
}
