import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { IUser } from '../../../common/api-interfaces'

@Component({
    selector: 'user-deposit-instructions',
    template: `<admin-deposit-instructions [user]="user"></admin-deposit-instructions>`,
})
export class UserDepositInstructionsComponent implements OnInit {
    public user: IUser
    constructor(private route: ActivatedRoute) {}
    public ngOnInit(): void {
        this.user = { id: this.route.snapshot.parent!.paramMap.get('userId') } as IUser
        this.route.parent!.paramMap.subscribe(params => {
            this.user = { id: params.get('userId') } as IUser
        })
    }
}
