import { HttpClient, HttpParams } from '@angular/common/http'
import { Component, EventEmitter, OnDestroy, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { pick } from 'lodash'
import { from, of, Subscription } from 'rxjs'
import { debounceTime, distinctUntilChanged, finalize, flatMap, switchMap } from 'rxjs/operators'
import { formatAddressToUSFormat } from 'src/app/common/common.mixin'
import { SecurityCheckService } from 'src/app/common/security-check/security-check.service'
import { CardOrderTermsComponent } from 'src/app/user-cards/card-order/card-order-terms/card-order-terms.component'
import { CardType, ICardOrder, IUser } from '../../common/api-interfaces'
import { SessionService } from '../../common/services/session.service'
import { ToastrService } from '../../common/services/toastr.service'

interface AdminCreateCardOrderPayload {
    cardOrder: {
        type: CardType
        userId: string
        shippingAddress: string | null
    }
    acceptOnBehalfOfCustomer: boolean
    terms?: { accept: boolean; versionId: string; name: string }
}

@Component({
    selector: 'card-order-create-form',
    templateUrl: 'card-order-create-form.component.html',
})
export class CardOrderCreateFormComponent implements OnDestroy {
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @ViewChild('cardOrderCreateForm')
    public cardOrderCreateForm: NgForm

    public cardholder?: IUser
    public cardholderAutocomplete = new EventEmitter<string>()
    public usersStream = this.cardholderAutocomplete.pipe(
        distinctUntilChanged(),
        debounceTime(200),
        switchMap(search =>
            this.http.get<IUser[]>('/users', {
                params: new HttpParams().set('limit', '25').set('search', search),
            })
        )
    )

    public types: CardType[] = ['physical', 'virtual']
    public cardOrder: Partial<ICardOrder> = {}
    public acceptOnBehalfOfCustomer = false

    public isLoading = false

    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        public ngbModal: NgbModal,
        public session: SessionService,
        private securityCheckService: SecurityCheckService,
        public translate: TranslateService
    ) {}

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public selectCardholder(): void {
        this.subscriptions.add(
            this.http.get<IUser>(`/users/${this.cardholder?.id}`).subscribe(user => {
                this.cardOrder.shippingAddress = formatAddressToUSFormat(user.address)
                this.cardOrder.user = {
                    id: user.id,
                } as IUser
            })
        )
    }

    public createOrder(): void {
        if (this.cardOrderCreateForm.invalid) {
            return
        }
        this.isLoading = true

        this.subscriptions.add(
            this.securityCheckService
                .getScopePermission('banking')
                .pipe(
                    flatMap(() =>
                        this.acceptOnBehalfOfCustomer
                            ? from(
                                  this.ngbModal.open(CardOrderTermsComponent, {
                                      windowClass: 'modal-primary',
                                      backdrop: 'static',
                                  }).result
                              )
                            : of(null)
                    ),
                    flatMap(terms => {
                        const body: AdminCreateCardOrderPayload = {
                            cardOrder: {
                                type: this.cardOrder.type!,
                                userId: this.cardOrder.user?.id!,
                                shippingAddress: this.cardOrder.shippingAddress ?? null,
                            },
                            acceptOnBehalfOfCustomer: this.acceptOnBehalfOfCustomer,
                        }
                        if (terms) {
                            body.terms = pick(terms, ['accept', 'versionId', 'name'])
                        }
                        return this.http.post<ICardOrder>('/admin/card-orders', body)
                    }),
                    finalize(() => {
                        this.isLoading = false
                    })
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('card-order-create-form.card-order-created'))
                    this.onSave.emit()
                })
        )
    }
}
