import { HttpClient } from '@angular/common/http'
import { AfterViewInit, ChangeDetectorRef, Component } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faUserSlash } from '@fortawesome/pro-light-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import {
    Association,
    IUser,
    Risk,
    UserFlag,
    UserRole,
    UserSegment,
    UserStatus,
    UserType,
} from '../../common/api-interfaces'
import { countriesSortedByName } from '../../common/countries'
import { ListComponent } from '../../common/list.component'
import { CloseAccountsModalComponent } from './close-accounts-modal/close-accounts-modal.component'
import { UserFormModalComponent } from './user-form-modal/user-form-modal.component'

@Component({
    selector: 'users',
    templateUrl: 'users.component.html',
})
export class UsersComponent extends ListComponent<IUser> implements AfterViewInit {
    public risks: Risk[] = ['low', 'medium', 'high', 'unacceptable']
    public roles: UserRole[] = ['admin', 'licensee', 'support']
    public statuses: UserStatus[] = ['inactive', 'pending', 'active', 'closing', 'suspended', 'closed']
    public flags: UserFlag[] = ['fatca', 'pep', 'hnw', 'monitored']
    public segments: UserSegment[] = ['retail', 'corporate', 'agency', 'sme', 'merchant']
    public types: UserType[] = ['personal', 'business']
    public countries = countriesSortedByName
    public associations: Association[] = [{ name: 'NONE', id: 'null' }]
    public faUserSlash = faUserSlash

    protected apiUrl = '/users'
    protected apiQuery = [
        'flags',
        'status',
        'role',
        'riskRating',
        'approvedAt',
        'segment',
        'details.nationality',
        'details.documentExpiryDate',
        '$address.country',
        'type',
        'associationId',
        'createdAt',
    ]
    protected formComponent = UserFormModalComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public ngAfterViewInit(): void {
        this.http
            .get<Association[]>('/admin/associations')
            .subscribe(associations => (this.associations = [...this.associations, ...associations]))
    }

    public export(type: 'personal' | 'business'): void {
        this.queryParams.type = { eq: type }
        this.requestExport()
    }

    public exportExpiredPOI(): void {
        this.queryParams['details.documentExpiryDate'] = { lte: this.formatDate(new Date(), 'yyyy-MM-dd') }
        this.requestExport(true)
    }

    public openCloseAccountsModal(): void {
        const modal = this.ngbModal.open(CloseAccountsModalComponent, {
            windowClass: 'modal-primary',
            size: 'md',
            backdrop: 'static',
            beforeDismiss: () => modal.componentInstance.canClose,
        })
    }
}
