import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'

@Component({
    selector: 'admin-user-card-orders',
    template: ` <card-orders [apiUrl]="apiUrl"></card-orders> `,
})
export class AdminUserCardOrdersComponent implements OnInit {
    public apiUrl: string
    constructor(private route: ActivatedRoute) {}
    public ngOnInit(): void {
        this.apiUrl = `/users/${this.route.snapshot.parent!.paramMap.get('userId')}/card-orders`
        this.route.parent!.paramMap.subscribe(params => {
            this.apiUrl = `/users/${params.get('userId')}/card-orders`
        })
    }
}
