import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { AccountBill } from '../../common/api-interfaces'
import { determineAccountBillStatus } from '../../common/common.mixin'
import { ListComponent } from '../../common/list.component'

@Component({
    selector: 'account-bills',
    templateUrl: 'account-bills.component.html',
})
export class AccountBillsComponent extends ListComponent<AccountBill> {
    public determineStatus = determineAccountBillStatus
    @Input()
    public apiUrl = '/account-bills'

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public openInvoiceModal(accountBill: AccountBill): void {
        if (!accountBill.invoice) {
            return
        }
        window.open(accountBill.invoice.signedUrl, '_blank', 'noopener')
    }
}
