import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { ReferrersModalComponent } from './../../referrers/referrers-modal.component'

@Component({
    selector: 'user-referrers',
    template: `<referrers [apiUrl]="apiUrl" [formComponent]="formComponent"></referrers>`,
})
export class UserReferrersComponent implements OnInit {
    public apiUrl: string
    public formComponent = ReferrersModalComponent
    constructor(private route: ActivatedRoute) {}

    public ngOnInit(): void {
        this.apiUrl = `/admin/users/${this.route.snapshot.parent!.paramMap.get('userId')}/referrers`
        this.route.parent!.paramMap.subscribe(params => {
            if (params.get('userId')) {
                this.apiUrl = `/admin/users/${params.get('userId')}/referrers`
            }
        })
    }
}
