import { HttpClient, HttpParams } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { faArrowRight } from '@fortawesome/pro-light-svg-icons'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { cloneDeep } from 'lodash'
import { Subject, Subscription } from 'rxjs'
import { debounceTime, distinctUntilChanged, finalize, flatMap, map, switchMap } from 'rxjs/operators'
import { CurrencyType, IConversion, ICurrency, IUser, Paginated, Wallet } from 'src/app/common/api-interfaces'
import { SecurityCheckService } from 'src/app/common/security-check/security-check.service'
import { ConversionService } from 'src/app/common/services/accounting/conversion.service'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { environment } from 'src/environments/environment'

@Component({
    selector: 'admin-conversion-form',
    templateUrl: 'admin-conversion-form.component.html',
})
export class AdminConversionFormComponent implements OnInit, OnDestroy {
    @Input()
    public item: IConversion
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()
    @ViewChild('conversionForm')
    public conversionForm: NgForm

    public from: ICurrency
    public to: ICurrency
    public amount: string
    public user: IUser | undefined

    public userAutocomplete = new EventEmitter<string>()
    public legalEntitiesStream = this.userAutocomplete.pipe(
        distinctUntilChanged(),
        debounceTime(200),
        switchMap(search =>
            this.http.get<IUser[]>('/users', {
                params: new HttpParams().set('limit', '25').set('search', search),
            })
        )
    )

    public currencies: ICurrency[]
    public filteredCurrencies: ICurrency[]
    public wallets: { balance: string; code: string; decimalPlaces: number; name: string; type: CurrencyType }[] = []

    public isSaving = false
    public faArrowRight = faArrowRight
    public appName = environment.appName

    public fetchWalletEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        private securityCheckService: SecurityCheckService,
        private conversionService: ConversionService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.http
            .get<Paginated<ICurrency>>('/currencies', {
                params: {
                    limit: 100 + '',
                    'filter.holdable': JSON.stringify(true),
                },
            })
            .pipe(map(response => response.data))
            .subscribe(cur => {
                this.currencies = cur
                this.filteredCurrencies = cloneDeep(this.currencies)
            })
        this.subscriptions.add(
            this.fetchWalletEvent
                .pipe(
                    switchMap(() => this.http.get<Wallet[]>(`/users/${this.user?.id}/wallets`)),
                    map(wallets =>
                        wallets
                            .filter(wallet => new BigNumber(wallet.availableBalance).isGreaterThan(0))
                            .map(wallet => {
                                const currency = this.currencies.find(c => c.code === wallet.currency.code)!
                                return {
                                    balance: wallet.availableBalance,
                                    code: currency.code,
                                    name: currency.name,
                                    decimalPlaces: currency.decimalPlaces,
                                    type: currency.type,
                                }
                            })
                    )
                )
                .subscribe(wallets => (this.wallets = wallets))
        )
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (this.conversionForm.invalid) {
            return
        }
        this.subscriptions.add(
            this.securityCheckService
                .getScopePermission('banking')
                .pipe(
                    flatMap(() =>
                        this.conversionService.create({
                            currency: this.to,
                            baseTransaction: {
                                amount: this.amount,
                                currency: this.from,
                            },
                            user: this.user,
                        })
                    ),
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('common.conversion-created'))
                    this.reset()
                    this.onSave.emit()
                })
        )
    }

    public convertToISODate(date: string | null): string | null {
        return date && `${date.slice(4)}-${date.slice(2, 4)}-${date.slice(0, 2)}`
    }

    public accept(): void {
        this.subscriptions.add(
            this.securityCheckService
                .getScopePermission('banking')
                .pipe(flatMap(() => this.conversionService.accept(this.item.id)))
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('common.conversion-accepted'))
                    this.onSave.emit()
                    this.activeModal.dismiss()
                })
        )
    }

    public decline(): void {
        this.subscriptions.add(
            this.securityCheckService
                .getScopePermission('banking')
                .pipe(() =>
                    this.conversionService.patch(this.item.id, [
                        {
                            op: 'replace',
                            path: '/status',
                            value: 'declined',
                        },
                    ])
                )
                .subscribe(() => {
                    this.toastr.success(this.translate.instant('common.conversion-decline'))
                    this.onSave.emit()
                    this.activeModal.dismiss()
                })
        )
    }

    public resetCurrencies(): void {
        this.conversionForm.controls.fromCurrency.reset()
        this.conversionForm.controls.toCurrency.reset()
    }

    public filterCurrencies(): void {
        if (this.from && this.from.type === 'crypto') {
            this.filteredCurrencies = this.currencies.filter(currency => ['EUR', 'USD'].includes(currency.code))
        } else {
            this.filteredCurrencies = cloneDeep(this.currencies)
        }
    }

    private reset(): void {
        this.user = undefined
        this.conversionForm.reset()
    }
}
