import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { pick, values } from 'lodash'
import { forkJoin, Subject, Subscription } from 'rxjs'
import { filter, finalize, flatMap, map, switchMap, tap } from 'rxjs/operators'
import { countryMap } from 'src/app/common/countries'
import { ICard, ICardOrder, IUser, RPCResult, UserDetails } from '../../common/api-interfaces'
import { CardOrder } from '../../common/models/accounting/card-order.model'
import { ConfirmationResult, ConfirmationService } from '../../common/services/confirmation.service'
import { SessionService } from '../../common/services/session.service'
import { ToastrService } from '../../common/services/toastr.service'

@Component({
    selector: 'card-order-form',
    templateUrl: 'card-order-form.component.html',
})
export class CardOrderFormComponent implements OnInit, OnDestroy {
    public activeTab: 'overview' | 'details' | 'logs' = 'overview'
    @Input()
    public item: CardOrder
    @Output()
    public readonly onSave = new EventEmitter<void>()
    @Output()
    public readonly onDelete = new EventEmitter<void>()
    @Output()
    public readonly onDiscard = new EventEmitter<void>()

    @ViewChild('cardOrderForm')
    public cardOrderForm: NgForm

    public isSaving = false
    public isCancelling = false
    public isAssigning = false
    public isSyncing = false
    public isLoading = false
    public isRetryingKyc = false

    public niumCustomer: any = {}

    @ViewChild('cardForm')
    public cardForm: NgForm

    public card: Partial<ICard & { last4Digits: string }> = {}
    public user: IUser
    public userDetails: UserDetails
    public countryMap = countryMap

    public resync = {
        data: false,
        poi: false,
        poa: false,
        selfie: false,
    }

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        public ngbModal: NgbModal,
        private confirmation: ConfirmationService,
        public session: SessionService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        if (!this.item || !this.item.id) {
            throw new Error(this.translate.instant('common.existing-card-order-required'))
        }
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() => this.http.get<ICardOrder>(`/card-orders/${this.item.id}`)),
                    map(cardOrder => new CardOrder(cardOrder))
                )
                .subscribe(item => {
                    Object.assign(this.item, item)
                    this.item.shippingAddress = item.shippingAddress
                })
        )
        this.subscriptions.add(
            forkJoin([
                this.http.get<IUser>(`/users/${this.item.user.id}`),
                this.http.get<UserDetails>(`/users/${this.item.user.id}/details`),
            ]).subscribe(([user, details]) => {
                this.user = user
                this.userDetails = details
            })
        )

        this.fetchEvent.next()
        if (this.item.status !== 'unconfirmed') {
            this.getNiumCustomer()
        }
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public approve(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'info',
                    text: this.translate.instant('common.approve-this-card-order'),
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'info',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    tap(() => {
                        this.isSaving = true
                    }),
                    flatMap(() =>
                        this.http.patch<CardOrder>(
                            `/card-orders/${this.item.id}`,
                            [
                                {
                                    op: 'replace',
                                    path: '/status',
                                    value: 'processing',
                                },
                            ],
                            {
                                headers: new HttpHeaders({
                                    'Content-Type': 'application/json-patch+json',
                                }),
                            }
                        )
                    ),
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(cardOrder => {
                    Object.assign(this.item, cardOrder)
                    this.toastr.success(this.translate.instant('card-order-form.card-order-approved'))
                })
        )
    }

    public resolve(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'info',
                    text: this.translate.instant('common.change-card-order-status-to-pending'),
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'info',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    tap(() => {
                        this.isSaving = true
                    }),
                    flatMap(() =>
                        this.http.patch<CardOrder>(
                            `/card-orders/${this.item.id}`,
                            [
                                {
                                    op: 'replace',
                                    path: '/status',
                                    value: 'pending',
                                },
                            ],
                            {
                                headers: new HttpHeaders({
                                    'Content-Type': 'application/json-patch+json',
                                }),
                            }
                        )
                    ),
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(cardOrder => {
                    Object.assign(this.item, cardOrder)
                    this.toastr.success(this.translate.instant('card-order-form.card-order-status-changed'))
                })
        )
    }

    public complete(): void {
        if (this.cardOrderForm.invalid) {
            return
        }
        const completeOrder = (trackingCode?: string) =>
            this.http.post<RPCResult<CardOrder>>(
                '/card-orders/' + this.item.id,
                {
                    id: Math.round(Math.random() * 10000),
                    jsonrpc: '2.0',
                    method: 'complete',
                    params: !!trackingCode ? { trackingCode } : {},
                },
                {
                    headers: new HttpHeaders({
                        'Content-Type': 'application/json-rpc',
                    }),
                }
            )
        const handleResponse = (response: RPCResult<CardOrder>) => {
            Object.assign(this.item, response.result)
            this.toastr.success(this.translate.instant('card-order-form.card-order-completed'))
            this.onSave.emit()
        }
        if (this.item.type === 'physical') {
            this.confirmation
                .show({
                    type: 'info',
                    text: this.translate.instant('common.mark-the-card-as-shipped'),
                    confirmText: this.translate.instant('common.complete'),
                    confirmClass: 'info',
                    input: 'text',
                    inputLabel: this.translate.instant('common.please-enter-the-tracking-code'),
                    inputValidator(value: string): string | null {
                        return !value ? `'This is required. Otherwise enter UNKNOWN'` : null
                    },
                })
                .pipe(
                    filter(([result]) => result === ConfirmationResult.CONFIRMED),
                    tap(() => {
                        this.isSaving = true
                    }),
                    flatMap(([_, trackingCode]) => completeOrder(trackingCode)),
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(handleResponse)
        } else {
            this.isSaving = true
            completeOrder()
                .pipe(
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(handleResponse)
        }
    }

    public cancel(): void {
        const refund =
            this.item.status !== 'unconfirmed'
                ? ` ${this.translate.instant('common.and-return-the-customer-funds')}`
                : ''
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant(
                        'common.are-you-sure-you-want-to-cancel-this-card-order'
                    )} ${refund}?`,
                    confirmText: this.translate.instant('common.refund'),
                    confirmClass: 'danger',
                    input: 'text',
                    inputLabel: 'Reason',
                    inputValidator: (input: string) =>
                        !input ? this.translate.instant('common.this-is-required') : null,
                })
                .pipe(
                    filter(([result]) => result === ConfirmationResult.CONFIRMED),
                    tap(() => {
                        this.isCancelling = true
                    }),
                    flatMap(([, reason]) =>
                        this.http.post<RPCResult<CardOrder>>(
                            '/card-orders/' + this.item.id,
                            {
                                id: Math.round(Math.random() * 10000),
                                jsonrpc: '2.0',
                                method: 'cancel',
                                params: {
                                    reason,
                                },
                            },
                            {
                                headers: new HttpHeaders({
                                    'Content-Type': 'application/json-rpc',
                                }),
                            }
                        )
                    ),
                    finalize(() => {
                        this.isCancelling = false
                    })
                )
                .subscribe(response => {
                    Object.assign(this.item, response.result)
                    this.toastr.success(this.translate.instant('card-order-form.card-order-cancelled'))
                    this.onSave.emit()
                })
        )
    }

    public assignCard(): void {
        if (this.cardForm.invalid) {
            return
        }
        this.isAssigning = true

        this.http
            .post<ICard>('/cards', {
                ...this.card,
                type: this.item.type,
                provider: 'nium',
                cardholder: pick(this.item.user, 'id'),
            })
            .pipe(
                flatMap(card =>
                    this.http
                        .patch<CardOrder>(
                            `/card-orders/${this.item.id}`,
                            [
                                {
                                    op: 'replace',
                                    path: '/card',
                                    value: { id: card.id },
                                },
                            ],
                            {
                                headers: new HttpHeaders({
                                    'Content-Type': 'application/json-patch+json',
                                }),
                            }
                        )
                        .pipe(
                            tap(response => {
                                Object.assign(this.item, response)
                            })
                        )
                ),
                finalize(() => {
                    this.isAssigning = false
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('card-order-form.card-assigned'))
            })
    }

    public updateDeliveryAddress(): void {
        if (this.cardOrderForm.invalid) {
            return
        }

        this.isLoading = true

        this.http
            .put<CardOrder>(`/admin/card-orders/${this.item.id}/delivery-address`, {
                shippingAddress: this.item.shippingAddress,
            })
            .subscribe(() => {
                this.toastr.success(this.translate.instant('card-order-form.shipping-address-updated'))
                this.isLoading = false
            })
    }

    public getNiumCustomer(): void {
        this.isLoading = true
        this.http.get<any>(`/users/${this.item.user.id}/nium/get-customer`).subscribe(response => {
            this.niumCustomer = response
            this.isLoading = false
        })
    }

    public syncNiumCustomer(): void {
        this.isSyncing = true
        this.http
            .post<{ message: string }>(`/admin/users/${this.item.user.id}/nium/update-customer`, {})
            .pipe(
                finalize(() => {
                    this.isSyncing = false
                })
            )
            .subscribe(response => {
                this.toastr.success(response.message)
                this.getNiumCustomer()
            })
    }

    public resyncCustomer(): void {
        if (values(this.resync).every(value => !value)) {
            this.toastr.error(this.translate.instant('card-order-form.you-must-choose-one-option-to-re-sync'))
            return
        }
        this.subscriptions.add(
            this.http.post(`/admin/users/${this.item.user.id}/nium/resync`, this.resync).subscribe(() => {
                this.toastr.success(this.translate.instant('card-order-form.re-sync-triggered'))
            })
        )
    }

    public retryKyc(): void {
        this.isRetryingKyc = true
        this.http
            .post(`/admin/users/${this.item.user.id}/nium/retry-kyc`, this.resync)
            .pipe(
                finalize(() => {
                    this.isRetryingKyc = false
                })
            )
            .subscribe(() => {
                this.toastr.success(
                    `${this.translate.instant('card-order-form.kyc-reinitiated-for')} ${this.user.name}`
                )
                this.getNiumCustomer()
            })
    }

    public revert(): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'warning',
                    text: this.translate.instant('common.revert-this-card-order-to-processing'),
                    confirmText: this.translate.instant('common.proceed'),
                    confirmClass: 'warning',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    tap(() => {
                        this.isSaving = true
                    }),
                    flatMap(() =>
                        this.http.patch<CardOrder>(
                            `/card-orders/${this.item.id}`,
                            [
                                {
                                    op: 'replace',
                                    path: '/status',
                                    value: 'processing',
                                },
                            ],
                            {
                                headers: new HttpHeaders({
                                    'Content-Type': 'application/json-patch+json',
                                }),
                            }
                        )
                    ),
                    finalize(() => {
                        this.isSaving = false
                    })
                )
                .subscribe(cardOrder => {
                    Object.assign(this.item, cardOrder)
                    this.toastr.success(this.translate.instant('card-order-form.card-order-reverted-to-processing'))
                })
        )
    }
}
