import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input, OnInit, ViewChild } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faTimes } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { filter, flatMap } from 'rxjs/operators'
import { File as IFile, Note } from '../../../common/api-interfaces'
import { DropzoneComponent } from '../../../common/dropzone/dropzone.component'
import { ListComponent } from '../../../common/list.component'
import { ConfirmationResult, ConfirmationService } from '../../../common/services/confirmation.service'
import { SessionService } from '../../../common/services/session.service'
import { ToastrService } from '../../../common/services/toastr.service'

@Component({
    selector: 'note-files',
    templateUrl: 'note-files.component.html',
})
export class NoteFilesComponent extends ListComponent<IFile> implements OnInit {
    @Input()
    public note: Note
    @Input()
    public canCreate = true
    @Input()
    public canDelete = true
    @Input()
    public files: File[] = []
    public existingFiles: IFile[]
    public defaultLimit = 5

    public faTimes = faTimes

    @ViewChild('dropzone')
    public dropzone: DropzoneComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private toastr: ToastrService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public ngOnInit(): void {
        if (this.note.id) {
            this.apiUrl = `/admin/notes/${this.note.id}/files`
            super.ngOnInit()
        }
    }

    public deleteFile(file: IFile): void {
        this.subscriptions.add(
            this.confirmation
                .show({
                    type: 'danger',
                    text: `${this.translate.instant(
                        'common.are-you-sure-want-to-delete-file'
                    )}<br>${this.translate.instant('common.you-cant-undo-action')}`,
                    confirmText: this.translate.instant('common.delete'),
                    confirmClass: 'danger',
                })
                .pipe(
                    filter(result => result === ConfirmationResult.CONFIRMED),
                    flatMap(() => this.http.delete<void>(`${this.apiUrl}/${file.id}`))
                )
                .subscribe(() => {
                    this.updateEvent.next()
                    this.toastr.success(`${file.name} ${this.translate.instant('common.removed')}`)
                })
        )
    }

    public submit(): void {
        if (this.dropzone) {
            this.dropzone.submit()
        }
    }

    public isValid(): boolean {
        return this.dropzone.isValid()
    }

    protected async stateChange(): Promise<void> {
        // do nothing
    }
}
